<template>
  <div class="bottomButton">
    <div class="leftButton" @click="select('left')" :style="{'background':backgroundL,'color':leftColor}">
      {{buttonL}}
    </div>
    <div class="rightButton" @click="select('right')" :style="{'background':backgroundR,'color':rightColor}">
      {{buttonR}}
    </div>
  </div>
</template>
<script>
  export default{
    name:'bottomButton',
    props:{
      buttonL:{
        type:String,
        default(){
          return '查看分析报告';
        }
      },
      buttonR:{
        type:String,
        default(){
          return '立即购买';
        }
      },
      leftColor:{
        type:String,
        default(){
          return 'rgba(255,255,255,1)'
        }
      },
      rightColor:{
        type:String,
        default(){
          return 'rgba(255,255,255,1)'
        }
      },
      backgroundL:{
        type:String,
        default(){
          return 'rgba(255,164,0,1)';
        }
      },
      backgroundR:{
        type:String,
        default(){
          return 'rgba(57,173,239,1)';
        }
      },
    },
    methods:{
      select(value){
        this.$emit('selectedTab',value);//select事件触发后，自动触发showCityName事件
      }
    }
  }
</script>
<style lang="scss" scoped>
  .bottomButton{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 110px;
    font-size:32px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    .leftButton{
      box-sizing: border-box;
      width:50%;
      height:110px;
      line-height:110px;
      text-align:center;
      float:left;
    }
    .rightButton{
      float:left;
      width:50%;
      height:110px;
      line-height:110px;
      text-align:center;
    }
  }
</style>



