<template>
  <div>
    <!--录播课头部显示-->
    <div class='EndInfoCard'>
      <div class="imgShow">
        <img v-lazy="data.imageurl" width="100%" height="100%" alt="">
      </div>
      <div class="infoContent">
        <div class="title">
          {{data.coursename}}
        </div>
        <div class="subTitle">
          {{data.subhead}}
        </div>
        <div class="teacher">
          {{data.teachername}}
          <!--<div class="learnNum"  >-->
            <!--已学{{data.processs}}-->
          <!--</div>-->
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'EndInfoCard',
    props:{
      data:{
        type:Object,
        default(){
          return {};
        }
      },
    }
  }
</script>
<style lang="scss" scoped>
  .EndInfoCard{
    height:257px;
    box-sizing: border-box;
    background:rgba(255,255,255,1);
    padding:56px 38px 0px 24px;
    .imgShow{
      width:280px;
      height:160px;
      float:left;
      border-radius:8px;
      overflow: hidden;
      margin-right:34px;
    }
    .infoContent{
      height:160px;
      width:374px;
      float:left;
      .title{
        height:63px;
        font-size:30px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(51,51,51,1);
      }
      .subTitle{
        height:68px;
        font-size:30px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(102,102,102,1);
      }
      .teacher{
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        height:26px;
        color:rgba(128,128,128,1);
        .learnNum{
          float:right;
          font-size:26px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(235,31,21,1);
        }
      }
    }
  }
</style>



