<template>
  <!--账户余额-->
  <div class="MyBalance">
    <headerBack title="账户余额"></headerBack>
    <div class="topCard">
      <div class="balanceName">
        账户余额
      </div>
      <div class="price">
        ￥<span style="font-size:38px;display:inline-block;padding:0px 5px;">{{price}}</span>元
      </div>
      <router-link to="/AccountDetails">
        <div class="bottomInfo">
          账单明细
        </div>
      </router-link>
    </div>
    <div class="bottomButton"  @click="popupVisible=true;">
      账户充值
    </div>
    <!--充值-->
    <mt-popup  v-model="popupVisible"
               position="bottom" style="width:100%">
      <div class="contentInfo">
        <div class="topTitle">
          请选择充值方式
        </div>
        <!--<div class="payType" @click="toPay(3)">支付宝</div>-->
        <div class="payType" @click="toastStatus=true;popupVisible=false">微信</div>
        <div class="cancel" @click="popupVisible=false;">取消</div>
      </div>
    </mt-popup>
    <!--充钱-->
    <middleToast v-if="toastStatus==true" >
      <div class="infoContent">
        <div class="title">
          请输入充值金额
        </div>
        <div class="lineGray"></div>
        <div class="weChat">
          <div class="inputDiv">
            <div class="leftTitle">
              充值金额
            </div>
            <div class="rightTitle">
              <input type="text" placeholder="请输入充值金额"  v-model="amount">
            </div>
          </div>
          <div class="iconWechat">
            <div class='iconDiv'>
            </div>
            <div class="iconTitle">微信支付</div>
          </div>
        </div>
        <div class="lineGray"></div>
        <div class="bottomDiv">
          <div class="leftDiv">
            <div class="leftButton" @click="toPay(2)">
              确认
            </div>
          </div>
          <div class="leftDiv">
            <div class="rightButton"  @click="toastStatus=false">
              取消
            </div>
          </div>
        </div>
      </div>
    </middleToast>
  </div>
</template>
<script>
  import middleToast from '@/components/MiddleToast';     //自定义模态弹框
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import {Popup,Toast} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  let that = {};
  export default{
    name:'MyBalance',
    components:{
      headerBack,
      'mt-popup':Popup,
      middleToast
    },
    data(){
      return {
        price:'',   //账户的余额
        popupVisible:false, //充值默认不显示
        studentid:'',
        toastStatus:false,  //充钱多少
        amount:'',   //钱财
        weiInfo:{},     //返回的微信数据
      }
    },
    created(){
      that = this;
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 请求个人信息
      this.getStudentInfo();
    },
    methods:{
      // 获取个人信息
      getStudentInfo(){
        Server.post(Path.getStudentInfo,{
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            this.price = data.usablemoney;
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      toDecimal2(x) {
        var f = parseFloat(x);
        if (isNaN(f)) {
          return false;
        }
        var f = Math.round(x * 100) / 100;
        var s = f.toString();
        var rs = s.indexOf('.');
        if (rs < 0) {
          rs = s.length;
          s += '.';
        }
        while (s.length <= rs + 2) {
          s += '0';
        }
        return s;
      },
      // 支付充值
      toPay(type){
        if(!this.amount){
          Toast('请输入充值金额');
          return;
        }
        let code = localStorage.getItem('code');
        Server.post(Path.studentRecharge,{
          'studentid':this.studentid,
          'amount':this.toDecimal2(this.amount),
          'paytype':type,
          'organid':1,
          'source':2,
          'code':code
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.weiInfo = res.data;
            this.pay();
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 微信支付
      pay(){
        if (typeof WeixinJSBridge == "undefined"){
          if( document.addEventListener ){
            document.addEventListener('WeixinJSBridgeReady',that.onBridgeReady,false);
          }else if (document.attachEvent){
            document.attachEvent('WeixinJSBridgeReady', that.onBridgeReady);
            document.attachEvent('onWeixinJSBridgeReady', that.onBridgeReady);
          }
        }else{
          that.onBridgeReady();
        }
      },
      onBridgeReady(){
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId":that.weiInfo.appId,     //公众号名称，由商户传入
            "timeStamp":that.weiInfo.timeStamp,         //时间戳，自1970年以来的秒数
            "nonceStr":that.weiInfo.nonceStr, //随机串
            "package":that.weiInfo.package,
            "signType": that.weiInfo.signType,         //微信签名方式:
            "paySign": that.weiInfo.paySign   //微信签名
          },
          function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok"){
              that.$router.push('/PaymentBack');
            }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
              Toast('取消支付');
            }else{
              Toast('支付失败');
            }
          });
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .infoContent{
    background:rgba(255,255,255,1);
    border-radius:10px;
    width:100%;
    overflow: hidden;
    .title{
      height:120px;
      line-height:120px;
      text-align: center;
      font-size:30px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(77,77,77,1);
    }
    .lineGray{
      width:90%;
      margin:0px auto;
      height:0px;
      border-bottom:1px solid rgba(204,204,204,1);
    }
    .weChat{
      padding:60px 0px;
      .iconWechat{
        padding:0px 155px 60px;
        .iconDiv{
          width:64px;
          height:64px;
          float:left;
          @include bg-image("~@/assets/images/icon_weChat");
          margin-right:32px;
        }
        .iconTitle{
          float:left;
          height:64px;
          line-height: 64px;
          font-size:32px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(61,61,61,1);
        }
      }
      .inputDiv{
        height:55px;
        padding:0px 155px;
        margin-bottom:80px;
        .leftTitle{
          height:55px;
          line-height:55px;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(77,77,77,1);
          float:left;
          margin-right:20px;
        }
        .rightTitle{
          height:55px;
          line-height: 55px;
          float:left;
          input{
            width: 250px;
            padding-left:10px;
            height:50px;
            border:1px solid rgba(204,204,204,1);
            border-radius:10px;
          }
        }
      }
    }
    .bottomDiv{
      clear:both;
      height:194px;
      padding:0px 50px;
      .leftDiv{
        float:left;
        width:50%;
        height:100%;
        display:flex;
        justify-content: center;
        align-items: center;
        .leftButton{
          width:240px;
          height:86px;
          line-height:86px;
          text-align: center;
          background:rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(255,255,255,1);
        }
        .rightButton{
          width:240px;
          height:86px;
          line-height:86px;
          text-align: center;
          border:2px solid rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(57,173,239,1);
        }
      }
    }
  }
  .MyBalance{
    .contentInfo{
      .topTitle{
        height:120px;
        line-height:120px;
        text-align: center;
        background:rgba(255,255,255,1);
        border-radius:10px 10px 0px 0px;
        font-size:30px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(77,77,77,1);
      }
      .payType{
        height:120px;
        line-height:120px;
        text-align: center;
        background:rgba(255,255,255,1);
        font-size:30px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(63,156,232,1);
      }
      .cancel{
        height:120px;
        line-height:120px;
        text-align: center;
        background:rgba(255,255,255,1);
        border-radius:10px 10px 0px 0px;
        font-size:30px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(128,128,128,1);
      }
    }
    .topCard{
      height:500px;
      box-sizing: border-box;
      padding-top:112px;
      background:rgba(255,255,255,1);
      .balanceName{
        height:157px;
        font-size:40px;
        text-align: center;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(102,102,102,1);
      }
      .price{
        height:182px;
        text-align: center;
        font-size:38px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(63,156,232,1);
      }
      .bottomInfo{
        text-align: center;
        font-size:26px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        text-decoration:underline;
        color:rgba(128,128,128,1);
      }
    }
    .bottomButton{

      text-align: center;
      width:400px;
      height:70px;
      line-height: 70px;
      background:rgba(63,156,232,1);
      border-radius:20px;
      margin:230px auto 0px;
      font-size:38px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(255,255,255,1);
    }
  }
</style>



