<template>
  <div id="City">
    <!-- 选择地区 -->
    <div id="headerBack">
      <div class="Headbar-back" @click="closeModal">
      </div>
      <div class="Headbar-title">地区选择</div>
      <span class="confirmArea" v-if="ifConfirm==true" @click="handleArea()" >确定</span>
    </div>
    <div class="city-content">
      <vScroll :data="cityObj">
        <ul class="subject-list-1" >
          <li
            class="list-item border-1px"
            v-for="(el, i) in cityObj.cityList"
            :key="i" @click="childArea(i,el.id,el.name)"
            :class="{'confirmCity':ifConfirm&&checkedItem==i}">{{el.name}}
          </li>
        </ul>
      </vScroll>
    </div>
  </div>
</template>

<script>
  import vScroll from '@/components/VerticalScroll'
  import Server from '@/service/request';
  import Path from '@/service/Path';


  export default {
    name: "Area",
    components: {
      vScroll
    },
    props:{
      popupCity:{
        type:Boolean,
        default(){
          return false;
        }
      },
      from:{
        type:String,
        default(){
          return 'register';
        }
      },
      areaID:'',
    },
    data() {
      return {
        cityName:'',
        cityObj: {cityList: []},
        ifConfirm: false,
        checkedItem: '',
        checkedId: '',
        country: '',
        province: ''
      }
    },
    watch:{
      // 监听变化
      areaID:function(){
        this.getAreaList();
      }
    },
    created() {
      // this.getCityList();
    },
    methods: {
      // 关闭城市选择
      closeModal(){
        this.$emit('closeModal');
      },
      getAreaList() {
        Server.post(Path.getCityList, {
          parentid: this.areaID
        }, (res) => {
          if (res.code === 0) {
            this.cityObj.cityList = res.data;
          }
        });
      },
      handleArea(){
        this.$emit('selectArea',this.checkedId);
        // Server.post(Path.updateAppuserInfo, {
        //   'nickname': '',
        //   'sex': '',
        //   'country': this.country,
        //   'province': this.province,
        //   'city': this.checkedId,
        //   'birth': '',
        //   'profile': '',
        //   'studentid': this.studentid
        // }, (res) => {
        //   let {code, data} = res;
        //   if (code === 0) {
        //     this.$router.go(-3);
        //   }
        // })
      },
      childArea(i, id,name) {
          this.cityName = name;
          this.ifConfirm = true;
          this.checkedId = id;
          this.checkedItem = i;
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "~@/assets/common/variable";
  @import "~@/assets/common/mixin";
  #headerBack {
    z-index: 3;
    position: relative;
    height: 92px;
    line-height: 92px;
    font-size: $font-size-36;
    text-align: center;
    background-color: $color-white;
    color: $color-black;
    .confirmArea {
      font-size: 30px;
      color: #878787;
      position: absolute;
      top:0px;
      right: 31px;
    }

    @extend .boxShadow;
    .Headbar-back {
      position: absolute;
      left: 17px;
      top: 20px;
      display: inline-block;
      width: 48px;
      height: 48px;
      @include bg-image('../assets/nav_icon_back');
      background-repeat: no-repeat;
      background-size: cover;
    }
  }


  .city-content {
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    bottom: 0;
    .subject-list-1 {
      padding: 0 30px 0 30px;
      background-color: $color-white;
      .list-item {
        position: relative;
        height: 90px;
        line-height: 90px;
        font-size: $font-size-30;
        @include border-1px($border-gary);
        // &:before {
        //   position: absolute;
        //   top: 32px;
        //   right: 0;
        //   content: '';
        //   display: block;
        //   width: 28px;
        //   height: 30px;
        //   @include bg-image('../assets/icon_more2');
        //   background-repeat: no-repeat;
        //   background-size: contain;
        //   background-position: center center;
        //   overflow: hidden;
        // }
      }
    }
  }

  .confirmCity {
    color: #5AD2CD !important;
  }
</style>



