<template>
  <div class="PaymentModal">
    <middleToast>
        <div class="middleContent">
          <div class="titleBar">
            选择支付方式
          </div>
          <div class="balance">
            <ul>
              <li  from="weChat" @click="changePrice(2)" v-if="balances>0">
                <div   :class="{'leftIconOk':balance==2,'leftIconNone':balance==1}"  ></div>
                <div class="middleIcon"></div>
                <div class="rightTitle">
                  微信支付
                </div>
              </li>
              <li  from="balance" @click="changePrice(1)">
                <div  :class="{'leftIconOk':balance==1,'leftIconNone':balance==2}"></div>
                <div class="middleIcon"></div>
                <div class="rightTitle">
                  余额支付
                </div>
              </li>
              <li  @click="changePrice(1)">
                余额剩余：￥{{price}}
              </li>
            </ul>
          </div>

          <!--取消确认-->
          <div class="okSave">
            <div class="leftButton" @click="priceStatus('yes')">
              确认
            </div>
            <div class="rightButton"  @click="priceStatus('no')">
              取消
            </div>
          </div>
        </div>
    </middleToast>
  </div>
</template>
<script>
  import middleToast from '@/components/MiddleToast';  //过渡组件
  export default{
    name:'PaymentModal',
    components:{
      middleToast
    },
    data(){
      return {
        balance:1
      }
    },
    props:{
      price:{
        type:String,
        default(){
          return ''
        }
      },
      balances:{
        type:String,
        default(){
          return '';
        }
      }
    },
    methods:{
      // 改变付款方式
      changePrice(value){
        if(value!=this.balance){
          this.balance = value;
        }
      },
      // 父级传参
      priceStatus(value){
        this.$emit('priceStatus',value,this.balance);
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .paymentModal{
  }
  .middleContent{
    width:670px;
    background:rgba(255,255,255,1);
    overflow:hidden;
    border-radius:12px;
    .titleBar{
      height:105px;
      line-height: 105px;
      text-align: center;
      font-size:34px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(61,61,61,1);
      border-bottom:1px solid rgba(204,204,204,1);
    }
    .balance{
      padding:55px 0px 0px 162px;
      margin-bottom:61px;
      border-bottom:1px solid rgba(204,204,204,1);
      li[from='weChat'] .middleIcon{
        height:64px;
        width:64px;
        float:left;
        @include bg-image("~@/assets/images/icon_weChat");
        margin-right:32px;
      }
      li[from='balance'] .middleIcon{
        height:64px;
        width:64px;
        float:left;
        @include bg-image("~@/assets/images/icon_balance");
        margin-right:32px;
      }
      li{
        height:64px;
        margin-bottom:77px;
        .leftIconNone{
          width:34px;
          height:34px;
          margin-top:15px;
          margin-right:57px;
          float:left;
          @include bg-image("~@/containers/Main/assets/icon_buttonNone");
        }
        .leftIconOk{
          width:34px;
          height:34px;
          margin-top:15px;
          margin-right:57px;
          float:left;
          @include bg-image("~@/containers/Main/assets/icon_buttonOk");
        }

        .rightTitle{
          height:64px;
          line-height: 64px;
          text-align: center;
          font-size:32px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(61,61,61,1);
        }
      }
    }
    .okSave{
      padding-bottom:65px;
      overflow: hidden;
      .leftButton{
        width:240px;
        height:86px;
        line-height:86px;
        text-align: center;
        background:rgba(57,173,239,1);
        border-radius:4px;
        float:left;
        margin-left:63px;
        margin-right:73px;
        font-size:32px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(255,255,255,1);
      }
      .rightButton{
        width:240px;
        box-sizing: border-box;
        height:86px;
        line-height:86px;
        text-align: center;
        border:2px solid rgba(57,173,239,1);
        border-radius:4px;
        font-size:32px;
        float:left;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(57,173,239,1);
      }
    }
  }
</style>



