<template>
  <!--我的课表title-->
  <div class="titleBar">
    <div class="leftBlock"></div>
    <div class="rightTitle">
      {{title}}
    </div>
  </div>
</template>
<script>
  export default{
    name:'MyTimeTitle',
    components:{
    },
    props:{
      title:{
        type:String,
        default(){
          return '';
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  .titleBar{
    height:69px;
    padding-left:24px;
    background:rgba(255,255,255,1);
    border-radius:10px 10px 0px 0px;
    font-size:32px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(57,173,239,1);
    margin-top:4px;
    border-bottom:1px solid rgba(217,217,217,1);
    .leftBlock{
      width:10px;
      height:10px;
      float:left;
      margin-top:29.5px;
      margin-right:20px;
      background:rgba(57,173,239,1);
      border-radius:50%;
    }
    .rightTitle{
      height:69px;
      line-height:69px;
      float:left;
      font-size:32px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(57,173,239,1);
    }
  }
</style>



