<template>
  <!--地址管理-->
  <div class="addressSelect myCellSwipe">
    <!--头部导航-->
    <headerBack title="地址管理"></headerBack>
    <!--<div class="item" v-for="(item,key) in addressInfo" :key="key" @click="toPage(key)">-->

    <bScroll
      class="wrapper"
      ref="scroll"
    >
      <div>
        <mt-cell-swipe class="item" v-for="(item,key) in addressInfo" :key="key"
                       :right="[
                      {content: '删除',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.id)}
                      ]"  >
          <div class="leftCon" @click="toPage(key)">
            <div style="margin-bottom:26px;">
              <div class="nameTop text-ellipsis">
                {{item.linkman}}
              </div>
              <div class="numberPhone">
                {{item.mobile}}
                <div class="defaultAddress" v-if="item.isdefault==1">
                  默认
                </div>
              </div>
            </div>
            <div class="addressInfo text-ellipsis">
              {{item.pname}}{{item.cname}}{{item.aname}}{{item.address}}
            </div>
          </div>
          <div class="rightCon"  @click="addAddress(item)">
          </div>
        </mt-cell-swipe>
      </div>
    </bScroll>

    <!--</div>-->
    <div class="addAddress"  @click="addAddress('')">
      +添加地址
    </div>

    <notInfo v-if="infoStatus">
      抱歉，没有收货地址
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import notInfo from '@/components/NotInfo';           //没有数据
  import bScroll from '@/components/Lib/BScroll';           //bScroll
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {CellSwipe} from 'mint-ui';
  export default{
    name:"addressSelect",
    components:{
      headerBack,
      'mt-cell-swipe':CellSwipe,
      notInfo,
      bScroll
    },
    data(){
      return {
        addressInfo:[], //地址信息
        fromInfo:'',
        infoStatus:false, //默认不显示没有数据
      }
    },
    created(){
      // 查询收货地址
      this.getAddressList();
      this.fromInfo = this.$route.query;
    },
    methods:{
      // 删除item
      deleteItem(id){
        Server.post(Path.deleteAddress,{
          'id':id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.getAddressList();
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 前往课程提交页
      toPage(key){
        if(this.fromInfo.from=='course'){
          this.fromInfo.key = key;
          this.$router.push({path:'CourseSelect',query:this.fromInfo})
        }
      },
      getAddressList(){
        Server.post(Path.getAddressList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.addressInfo = data;
            if(data.length==0){
              this.infoStatus = true;
            }

          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 添加地址
      addAddress(item){
        this.$router.push({path:'AddAddress',query:{item:item}});
      }
    }
  }
</script>
<style scoped lang="scss">
  @import "~@/assets/common/mixin";
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 150px;
    background:rgba(255,255,255,1);
  }
  .addressSelect{
    .item{
      height:140px;
      background:rgba(255,255,255,1);
      box-sizing: border-box;
      padding:33px 36px 20px 34px;
      .rightCon{
        width:28px;
        height:27px;
        float:right;
        margin-top:19.5px;
        @include bg-image("~@/assets/images/icon_write");
      }
      .leftCon{
        width:600px;
        float:left;
        .addressInfo{
          width:550px;
          height:25px;
          line-height:25px;
          font-size:20px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(102,102,102,1);
        }
        .nameTop{
          height:30px;
          line-height:30px;
          width:200px;
          float:left;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(51,51,51,1);
        }
        .numberPhone{
          height:30px;
          width:280px;
          line-height:30px;
          float:left;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(51,51,51,1);
          .defaultAddress{
            width:80px;
            height:26px;
            line-height:26px;
            text-align: center;
            margin-top:2px;
            background:rgba(255,164,0,1);
            border-radius:10px;
            font-size:20px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(255,255,255,1);
            float:right;
          }
        }
      }
    }
    .addAddress{
      width:400px;
      height:70px;
      line-height:70px;
      text-align: center;
      background:rgba(63,156,232,1);
      border-radius:20px;
      position:fixed;
      bottom:30px;
      left:calc(50% - 200px);
      font-size:30px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(255,255,255,1);
    }
  }
</style>



