  .notInfo{
    /*position:absolute;*/
    /*top:200px;*/
    /*left:0;*/
    /*right:0;*/
    /*bottom:0;*/
    /*display:flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    .showInfo{
      width:231px;
      position:absolute;
      top:40%;
      left: calc(50% - 115px);
      .imgShow{
        width:231px;
