
<template>
  <div v-if="listInfo.length!=0">
    <div class="titleBar">
      <div class="titleBlock"></div>
      <div class="titleWeight">
        推荐课程
      </div>
      <div class="middleContent">
        <swiper :options="swiperOption" ref="mySwiper"  class="tabOut">
          <swiper-slide  class="tabTab"  :class="{'tabAlive':nowId==k,'tabDefault':nowId!=k}"  ref= "slider"  @click.native="nowId=k" v-for="(item, k) in listInfo" :key="k">
            <!--<div v-for="(item, k) in listInfo" class="tabTab" :class="{'tabAlive':nowId==k,'tabDefault':nowId!=k}" @click="nowId=k"  :key="k">-->
              {{item.categoryname}}
            <!--</div>-->
          </swiper-slide>
        </swiper>
      </div>
      <!--<div class="rightAll" @click="toAllCourse">-->
        <!--更多-->
        <!--<div class="icon">-->
          <!--<img src="~@/assets/images/allIcon.png" width="100%" alt="">-->
        <!--</div>-->
      <!--</div>-->
    </div>
    <div class="ShowCourse ">
          <div class="flexLi"  v-for="(item,k) in courseInfo"  @click="toPage(item.courseid,item.classtypes)" :key="k">
            <div class="flexOut">
              <div class="imgShow">
                <img :src="item.imageurl" width="100%"  height="100%" alt="">
              </div>
              <div class="title">
                <div class="orangeBlock">
                  <template v-if="item.classtypes==2" class="video">直播</template>
                  <template v-if="item.classtypes==1" class="video">录播</template>
                </div>
                <div class="titleInfo text-ellipsis">
                  {{item.coursename}}
                </div>
              </div>
              <div class="teacherInfo">
                <div class="teacher">
                  <template v-if="item.classtypes==1 ||(item.classtypes==2 && item.price==item.maxprice) ">
                    ￥{{item.price}}/每课时
                  </template>
                  <template v-else>
                    ￥{{item.price}}-{{item.maxprice}}/每课时
                  </template>
                </div>
              </div>
            </div>

          </div>
    </div>
  </div>
</template>
<script>
  import  horizScroll from '@/components/HorizScroll';     //整行的课程信息
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import 'swiper/dist/css/swiper.css';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'ShowCourse',
    components:{
      horizScroll,
      swiper,
      swiperSlide
    },

    data(){
      return {
        swiperOption: {
          autoplayDisableOnInteraction: false,
          initialSlide: 0,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          // loop: false,
          // type: 'bullets',
          notNextTick: true,
          direction: 'horizontal',
          // pagination: '.swiper-pagination',
          slidesPerView :'auto',
          // centeredSlides: true,
          paginationClickable: true,
          // centeredSlides:false,
          // spaceBetween: 30,
          onTransitionEnd: function(swiper){
            if(swiper.progress==1){
              swiper.activeIndex=swiper.slides.length-1;
            }
          }
        },
        listInfo:[
          {
            coursearr:[]
          }
        ],    //获取的全部数据
        nowId:0,      //默认的选中数据数组索引
      }
    },
    created(){
      // 获取推荐列表
      this.getRecommendCourser();
    },
    computed:{
      // 同步的所选类别数据
      courseInfo:function(){
        if(this.listInfo.length!=0){
          return this.listInfo[this.nowId].coursearr;
        }
      }
    },
    methods:{
      // 获取数据列表
      getRecommendCourser(){
        Server.post(Path.getRecommendCourser, {

        }, (res) => {
          if (res.code === 0) {
            // 绑定数据
            this.listInfo = res.data;
          }
        });
      },
      toAllCourse(){
        this.$router.push('/AllCourses');
      },
      toPage(courseid,classtypes){
        this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes}});
      }
    },
    watch:{
    }
  }
</script>
<style lang="scss" scoped>
  /*推荐班级*/
  .titleBar{
    height:90px;
    box-sizing: border-box;
    padding:0px 22px;
    background:rgba(255,255,255,1);
    .titleBlock{
      width:6px;
      height:32px;
      float:left;
      margin-top:29px;
      background:rgba(57,173,239,1);
      margin-right:23px;
    }
    .titleWeight{
      font-size:34px;
      width:150px;
      font-family:PingFang-SC-Bold;
      font-weight:bold;
      color:rgba(51,51,51,1);
      height:90px;
      line-height:90px;
      text-align:center;
      font-family:PingFang-SC-Bold;
      font-weight:bold;
      float:left;
      color:rgba(51,51,51,1);
      margin-right:50px;
    }
    /*滑动推荐*/
    .middleContent{
      width: calc(100% - 300px);
      height:44px;
      margin-top:26px;
      float:left;
      overflow-x: hidden;
      box-sizing:border-box;
      .tabTab{
        height:44px;
        line-height:44px;
        box-sizing:border-box;
        width:auto;
        padding:0px 20px;
        float:left;
        margin-right:30px;
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
      }
    }

    .tabDefault{
      color:rgba(51,51,51,1);
      border-bottom:3px solid #fff;
    }
    .tabAlive{
      border-bottom:3px solid rgba(57,173,239,1);
      color:rgba(57,173,239,1);
    }
    .rightAll{
      height:35px;
      line-height:35px;
      margin-top:34px;
      float:right;
      font-size:26px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(51,51,51,1);
      .icon{
        height:29px;
        line-height:29px;
        width:24px;
        float:right;
        margin-left:10px;
      }
    }
  }
  .ShowCourse{
    padding:0px 10px;
    margin-bottom:20px;
    box-sizing: border-box;
    background:rgba(255,255,255,1);
    overflow: hidden;
      .flexLi{
        height:347px;
        float:left;
        width:50%;
        .flexOut{
          width:340px;
          margin:0px auto;
          .imgShow{
            width:340px;
            height:220px;
            border-radius:8px;
            margin-bottom:13px;
            overflow:hidden;
          }
          .title{
            width:340px;
            height:52px;
            .orangeBlock{
              width:50px;
              height:26px;
              margin-top:2px;
              line-height:26px;
              background:rgba(255,152,106,1);
              border-radius:4px;
              text-align: center;
              float:left;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(255,255,255,1);
              margin-right:11px;
              .video{
                font-size:16px;
                font-family:PingFang-SC-Regular;
                font-weight:400;
                color:rgba(255,255,255,1);
              }
            }
            .titleInfo{
              /*height:30px;*/
              /*background:red;*/
              font-size:26px;
              line-height: 30px;
              width:270px;
              font-family:PingFang-SC-Medium;
              font-weight:500;
              color:rgba(51,51,51,1);
              float:left;
            }
          }
          .teacherInfo{
            height:25px;
            .teacher{
              height:25px;
              line-height: 25px;
              font-size:26px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              float:left;
              color:rgba(102,102,102,1);
            }
            .rightTitle{
              float:right;
              height:25px;
              line-height:25px;
              font-size:26px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(57,173,239,1);
            }
          }
        }

    }
  }
</style>



