<template>
  <div id="HorizScroll" class="slider" ref="slider">
    <div class="slider-group" ref="sliderGroup">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import BScroll from 'better-scroll';

  export default {
    name: "HorizScroll",
    props: {
      data: {
        type: Array,
        default: null
      }
    },
    mounted() {
      // this.setSliderWidth();
      this.initScroll();
    },
    methods: {
      scrollTo(x, y, time) {
        this.slider.scrollTo(x, y, time);
      },
      //设置容器宽度
      setSliderWidth() {
        this.children = this.$refs.sliderGroup.children;
        let width = 0;
        let sliderWidth = 0; // 每个列表项的宽度
        let margin = 0;
        for (let i = 0; i < this.children.length; i++) {
          let child = this.children[i];
          sliderWidth += child.offsetWidth;
          margin += Number(this.getStyle(child, 'marginLeft').slice(0, -2)) + Number(this.getStyle(child, 'marginRight').slice(0, -2));
        }
        width = sliderWidth + margin;
        this.$refs.sliderGroup.style.width = width+2 + 'px';
      },
      //初始化scroll
      initScroll() {
        this.slider = new BScroll(this.$refs.slider, {
          scrollX: true,
          scrollY: false,
          click: true,
        });
      },
      addClass(ele, cls) {
        ele.className = ele.className === '' ? cls : ele.className + ' ' + cls;
      },
      getStyle(obj, attr) {
        if (obj.currentStyle) {
          return obj.currentStyle[attr];
        }
        else {
          return document.defaultView.getComputedStyle(obj, null)[attr];
        }
      }
    },
    watch: {
      data() {
        setTimeout(() => {
          this.setSliderWidth();
          this.initScroll();
        }, 100)
      }
    }
  }
</script>

<style scoped lang="scss">
</style>



