<template>
	<div class="payyment">
    <div id="headerBack">
      <div class="Headbar-back" @click="closeModal"></div>
      <div class="Headbar-title">课程选择</div>
      <!--<span class="confirmArea" @click="handleArea()" v-if="confirmFlag">确定</span>-->
    </div>
	<div class="payment">
		<div class="payTitle">
			<img v-lazy="imgUrl" alt=""  class="payImg"/>
			<div class="payP">
				<p class="title">课程价格</p>
				<p class="payNum">￥{{price}}元</p>
			</div>
		</div>
		<div v-if="classtypes==2">
      <div class="payDate">日期</div>
      <div class="paySelect">
        <select  @change="changeTime"  ref="selectDate">
          <option  v-for="(item,key) in dateInfo" :key="key"    :selected="item.date==dateTimes" >{{item.date}}</option>
        </select>
        <img :src="selectt" alt=""  class="selecttImg"/>
      </div>
      <div class="payType">类型</div>
      <ul class="payUlOne" v-if="dateInfo[defaultIndex] && dateInfo[defaultIndex].class">
        <li  @click="changeVs(info)" :class="{'payLiOne':defaultVvs==info,'payOne':defaultVvs!=info}"   v-for="(info,k) in dateInfo[defaultIndex].class"  :key="k">1v{{info}}</li>
      </ul>
      <div class="payClass">班级</div>
      <ul class="payUlTwo">
        <li @click="changeClass(key,item.applystatus)"  v-for="(item,key) in allInfos" class="payLiTwop" :class="{'payLiTwo':key==indexKey,'payLiTwoNo':key!=indexKey}" :key="key">
          <p>{{item.coursename}}</p>
        </li>
      </ul>
      <div class="sendSome">赠送教材</div>
      <label v-for="(item,key) in giftdescribe">{{item.name}}x{{item.num}}&nbsp;&nbsp;</label>
    </div>

    <div class="addressTitle">
      可用优惠
    </div>
    <mt-radio
      v-model="value"
      :options="options" @change="check">
    </mt-radio>
    <div class="addressTitle">
      收货地址
    </div>
    <div class="addressContent">
      <div class="address">
        <div class="topAddress">
          <div class="leftTitle">
            阿尔萨斯
          </div>
          <div class="phoneNum">
            1854547875454
          </div>
          <div class="rightBlock">
            默认
          </div>
        </div>
        <div class="bottomAddress">
          重庆市南岸区南坪西路58号上海城嘉德中心2号7-2
        </div>
      </div>
      <div class="rightIcon">
        <div class="iconShow"></div>
      </div>
    </div>
	</div>

	<div class="paySign" @click="payyClass"  v-if="buttonStatus==true">确定</div>
	<div class="paySigns"   v-if="buttonStatus==false">暂停报名</div>
	</div>
</template>

<script>
	 import selectt from '@/assets/images/select.png';
	 import Order from '@/assets/images/fllItem.png';
   import { Radio,} from 'mint-ui';
	 export default {
		    name: "payment",
		    components: {
          'mt-radio':Radio,
		    },
     data() {
       return {
         value: "A",
         // radio的选项
         options : [{
           label: '选项A',
           value: 'A'
         },
           {
             label: '选项B',
             value: 'B'
           },
           {
             label: '选项C',
             value: 'C'
           },
           {
             label: '选项D',
             value: 'D'
           }],
         modelPayment:false,
         Order,
         selectt,
         defaultIndex:this.dateIndex,    //显示几对几的索引
         defaultVvs:this.defaultVs,       //默认选中的几对几
         dateTimes:this.dateTime,         //第一次进页面的时间
         allInfos:this.allInfo,           //班级信息
         buttonStatus:this.buttonStatuss,     //确认按钮默认可以使用
         indexKey:0,            //班级选择选中
         vsKey:0,               //类型选择选择选中
       };
     },
		    created(){
		    },
		     props: {
		        formData: {
		            type: Object,
		            default: () => {
		                return {}
		            }
		        },
           imgUrl:{   //图片地址
		          type:String,
             default(){
		            return '';
             }
           },
           price:{   //课程价格
             type:String,
             default(){
               return '22';
             }
           },
           dateInfo:{
		          type:Array,
             default(){
		            return [];
             }
           },
           dateTime:{   //默认选中的时间
		          type:String,
             default(){
		            return '';
             }
           },
           dateIndex:{      //时间选中的索引
		          type:Number,
             default(){
		            return 0;
             }
           },
           classtypes:{   //默认录播课
		          type:String,
             default(){
		            return 1;
             }
           },
           defaultVs:{  //选中的几对几
             type:Number,
             default(){
               return 0;
             }
           },
           allInfo:{      //全部的课程信息包括几个班级
		          type:Array,
             default(){
		            return [];
             }
           },
           buttonStatuss:{     //是否可用
		          type:Number,
             default(){
                return 0;
             }
           },
           giftdescribe:{
		          type:Array,
             default(){
		            return [];
             }
           }
		     },

		    methods:{
		      // 选择优惠券
          check: function () {
            console.log(this.value)
          },
          changeTime(){
            // 改变当前date信息
            let now = this.$refs.selectDate;
            this.dateTimes = now.value;
            for(let i in this.dateInfo){
              if(this.dateInfo[i].date==now.value){
                // 判断边框选中
                let now = this.dateInfo[i].class[0];
                this.defaultVvs = now;
                this.defaultIndex = i;
                // 第一个重新赋值为0选中
                this.indexKey = 0;
                // 判断第一个是否可用
                this.buttonStatus = this.allInfos[0].applystatus;
                // 再次请求数据
                this.$emit('restartSend',this.dateTimes,this.defaultVvs);
                return;
              }
            }

          },
          // 关闭模态框
          closeModal(){
            this.$emit('closeModal');
          },
          // 修改班级选中
          changeClass(key,status){
            this.indexKey = key;
            // button样式
            this.buttonStatus = status;
          },
          // 修改类型选中
          changeVs(key){
            if(this.defaultVvs!=key){
              this.defaultVvs = key;
              // 再次请求数据
              this.$emit('restartSend',this.dateTimes,this.defaultVvs);
            }
          },
          // 付款
		    	payyClass(){
            this.$router.push('/SignUp');
		    	}
		    },
     // 监听数据
     watch:{
       defaultVs:function(value){
         this.defaultVvs = value;
       },
       dateIndex:function(value){
         this.defaultIndex = value;
       },
       dateTime:function(value){
         this.dateTimes = value;
       },
       allInfo:function(value){
         this.allInfos = value;
       },
       buttonStatuss:function(value){
         this.buttonStatus = value;
       },
     }
	   }
</script>
<style scoped lang="scss">
