  @import "~@/assets/common/mixin";
  .addressTitle{
    height:62px;
    font-size:32px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(77,77,77,1);
  }
  /*中间部分*/
  .addressContent{
    height:98px;
    margin-bottom:27px;
    /*右侧图标*/
    .address{
      height:98px;
      width:646px;
      float:left;
      padding-left:40px;
      box-sizing: border-box;
      .topAddress{
        height:30px;
        margin-bottom:26px;
        .leftTitle{
          height:30px;
          line-height: 30px;
          font-size:30px;
          float:left;
          font-family:MicrosoftYaHei;
          font-weight:400;
          margin-right:83px;
          color:rgba(51,51,51,1);
        }
        .phoneNum{
          height:30px;
          line-height: 30px;
          float:left;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          margin-right:30px;
          color:rgba(51,51,51,1);
        }
        .rightBlock{
          width:80px;
          height:26px;
          line-height: 26px;
          text-align: center;
          margin-top:2px;
          float:left;
          background:rgba(255,164,0,1);
          border-radius:10px;
          font-size:20px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
        }
      }
      .bottomAddress{
        font-size:20px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(102,102,102,1);
      }
    }
    /*左部分信息*/
    .rightIcon{
      height:98px;
      width:20px;
      display:flex;
      align-items: center;
      justify-content: right;
      float:left;
      .iconShow{
        width:19px;
        height:32px;
        @include bg-image("~@/assets/images/icon_right");
      }
    }
  }
  #headerBack {
    z-index: 3;
    position: relative;
    height: 92px;
    line-height: 92px;
    font-size: $font-size-36;
    text-align: center;
    background-color: $color-white;
    color: $color-black;
  @extend .boxShadow;
  .Headbar-back {
    position: absolute;
    left: 17px;
    top: 20px;
    display: inline-block;
    width: 48px;
    height: 48px;
    transform: rotate(-90deg);
    @include bg-image('~@/components/assets/nav_icon_back');
    background-repeat: no-repeat;
    background-size: cover;
  }
  }
	.payment{
		width: 100%;
		padding: 25px 47px 0 40px ;
	}
  .sendSome{
    padding:10px;
    line-height: 28px;
    border: 1px solid #3f9be7;
    display: inline-block;
    color: #3f9be7;
    font-size: 14px;
    text-align: center;
    margin-right: 8px;
  }
	.payImg{
		width: 328px;
		height: 175px;
		margin-right: 30px;
	}
	.payTitle{
		margin-bottom: 31px;
	}
	.payP{
		display: inline-block;
		vertical-align: bottom;
		line-height: 30px;
	}
	.title{
		display: inline-block;
		line-height: 1;
		vertical-align: middle;
