		font-family:MicrosoftYaHei;
		font-weight:400;
		color:rgba(51,51,51,1);
	}
	.payNum{
		display: inline-block;
		line-height: 1;
		font-size:30px;
		font-family:MicrosoftYaHei;
		font-weight:400;
		color:#E71313;
		vertical-align: middle;
	}
	.payDate, .payType, .payClass{
		height:24px;
		font-size:24px;
		font-family:MicrosoftYaHei;
		font-weight:400;
		color:rgba(128,128,128,1);
		margin-bottom: 20px;
	}
	.paySelect{
		width: 300px;
		height: 70px;
		border:1px solid rgba(179,179,179,1);
		line-height: 70px;
		margin-bottom: 30px;
    padding-left:15px;
		margin-left: 50px;
		position: relative;
	}
	.paySelect select{
		width: 100%;
		height: 100%;
		font-weight:400;
		color:rgba(140,140,140,1);
		-webkit-appearance:none;
		appearance:none；
	}
	.selecttImg{
		width: 29px;
