	}
  /*边框选中*/
	.payLiOne{
		width:132px;
		height:72px;
		line-height: 72px;
		text-align: center;
		border:2px solid rgba(255,164,0,1);
		font-size:30px;
		font-family:MicrosoftYaHei;
