		position: relative;
    color:rgba(255,164,0,1);
	}
  .payLiTwoNo{
    border:2px solid gray;
    font-family:MicrosoftYaHei;
    margin-right: 30px;
    padding: 22px 21px 20px 21px;
    margin-bottom: 20px;
    position: relative;
  }
	.payLiTwop{
		height:30px;
		font-size:30px;
		line-height: 1;
		font-family:MicrosoftYaHei;
		font-weight:400;
	}
	.payLiSpot{
		border:2px solid dashed;
