<template>
  <!--选择课程-->
  <div class="CourseScreening">
    <!--头部导航-->
    <headerBack title="课程分类" ></headerBack>
    <div class="infoContent">
      <router-link to="/SelectInterest">
        <div class="rightGray" >
          跳过
        </div>
      </router-link>
      <div style="margin-right:-87px;padding-top:50px;">
        <ul>
          <li v-for="(item,key) in listInfo" :style="{background:colorInfo[key],border:item.flag?'2px solid green':'none'}"  @click="selectIndex(key)" :key="key">
            {{item.name}}
          </li>
        </ul>
      </div>
      <div class="selectOk"  @click="toPage()">
        选好了
      </div>

    </div>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  export default{
    name:'CourseScreening',
    components:{
      headerBack
    },
    data(){
      return{
        categoryid:'',  //选择的分类
        listInfo:[],
        colorInfo:[
          '#DDA0DD','#FFB6C1','#FF69B4','#A020F0','#EEA2AD','#CD6889','#E0EEE0','#EE6A50',
          '#DDA0DD','#FFB6C1','#FF69B4','#A020F0','#EEA2AD','#CD6889','#E0EEE0','#EE6A50'
        ]
      }
    },
    created(){
      // 获取学生标签列表
      this.getUserTag();
    },
    methods:{
      // 选择标签
      selectIndex(index){
        for(let i in this.listInfo){
          if(i==index){
            this.listInfo[i].flag = true;
            this.categoryid = this.listInfo[i].categoryid;
          }else{
            this.listInfo[i].flag = false;
          }
        }
      },
      // 获取学生标签列表
      getUserTag(){
        Server.post(Path.getUserFavorCategory,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 如果没有数据直接下一页
            if(data.length==0){
              this.$router.push('/SelectInterest');
            }else{
              this.listInfo = data;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      toPage(){
        if(this.categoryid==''){
          Toast('请选择年级');
          return;
        }
        // 选择课程分类
        Server.post(Path.addUserFavorCategory,{
          'categoryid':this.categoryid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            this.$router.push('/SelectInterest');
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })

      }
    }
  }
</script>
<style lang="scss" scoped>
  .selectOk{
    margin: 95px auto 0px;
    width:472px;
    height:92px;
    line-height: 92px;
    text-align: center;
    background:rgba(47,172,250,1);
    opacity:0.7;
    border-radius:46px;
    font-size:32px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
  }
  .rightGray{
    width:100px;
    height:50px;
    line-height:50px;
    text-align:center;
    position:absolute;
    right:43px;
    top:55px;
    background:rgba(107,107,107,0.3);
    border-radius:25px;
    font-size:26px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(255,255,255,1);
  }
  .CourseScreening{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:rgba(255,255,255,1);
    /*有效内容*/
    .infoContent{
      padding:64px 66px 0px 66px;
      position:relative;
      /*课程级别*/
      .title{
        height:66px;
        font-size:32px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(77,77,77,1);
        position:relative;
        /*黑色小点*/
        .blankBlock{
          position:absolute;
          left:-24px;
          top:14px;
          width:8px;
          height:8px;
          background:rgba(77,77,77,1);
          border-radius:50%;
        }
      }
      li{
        width:148px;
        height:76px;
        float:left;
        line-height: 76px;
        text-align: center;
        box-sizing: border-box;
        opacity:0.65;
        border-radius:38px;
        font-size:30px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        margin-right:87px;
        margin-bottom:40px;
        color:rgba(255,255,255,1);
      }
    }
  }
</style>



