<template>
  <!--我的作业未完成-->
  <div class="MyWorkInfo">
    <headerBack v-if="!from" title="我的作业 未完成"></headerBack>
    <headerBack v-if="from" title="我的作业 已批阅"></headerBack>
    <verticalScroll scrollType="Home">
      <div class="infoContent">
        <div class="title">
          {{dataInfo.lessonsname}}
        </div>
        <!--table列表-->
        <!--<tableCard></tableCard>-->
        <div class="tableCard" v-if="from==''">
          <table border="1" class="table">
            <tr >
              <td>题号</td>
              <td v-if="item.length" v-for="(item,key) in dataInfo.subject" :key="key">第{{turnNum(key,dataInfo.subject_count)}}题</td>
              <td>总分</td>
            </tr>
            <tr>
              <td>分数</td>
              <td v-if="item.length" v-for="(item,key) in dataInfo.subject" :key="key">
              <td></td>
            </tr>
          </table>
        </div>

        <div class="tableCard" v-if="from=='show'">
          <table border="1" class="table">
            <tr >
              <td>题号11</td>
              <td v-if="item.length" v-for="(item,key) in dataInfo.subject" :key="key">第{{turnNum(key,dataInfo.subject_count)}}题</td>
              <td>总分</td>
            </tr>
            <tr>
              <td>分数</td>
              <td v-if="item.length" v-for="(item,key) in dataInfo.subject" :key="key">
                {{teacherScore(item,1)}}
              </td>
              <td>{{dataInfo.totalscore}}</td>
            </tr>
          </table>
        </div>
        <!--题目类型-->
        <div v-for="(item,key) in dataInfo.subject" :key="key">
          <!--单选题-->
          <div  v-if="key==1 && item.length" >
            <div class="workTitle">
              {{turnNum(key,dataInfo.subject_count)}}、单选题。（{{teacherScore(item,2)}}）分
            </div>
            <div style="margin-bottom:40px" v-for="(info,k) in item" :key="k">
            <span class="subTitle" v-if="!from">
            {{k+1}}.{{info.name}}
            </span>
            <span :class="{'subTitle':info.teacherscore!=0,'subError':info.teacherscore==0}" v-if="from">
              {{k+1}}.{{info.name}}
            </span>
              <div style="margin-right:-60px;">
                <div class="imgShow" v-if="info.imageurl.length" v-for="(img,i) in info.imageurl" :key="i">
                  <img v-lazy="'http://'+img" height="100%" width="100%" alt="">
                </div>
              </div>
              <div style="clear:both;">
                <mt-radio  v-if="!from"
                  v-model="info.nowCon"
                  :options="info.options" >
                </mt-radio>
                <ul v-if="from"  :style="{'color':info.teacherscore!=0?'':'red'}">
                  <li class="errorLi" v-for="(item,key) in info.options" :key="key">
                    {{item}}
                  </li>
                  <li class="errorLi">学生答案：{{info.answers}}</li>
                  <li class="errorLi">老师答案：{{info.correctanswer}}</li>
                  <li class="teacherScore" >老师评分：{{info.teacherscore}}</li>
                </ul>
              </div>
            </div>
          </div>
          <!--多选题-->
          <div v-if="key==2 && item.length" v-for="(info,k) in item" :key="k">
            <!--题目类型-->
            <div class="workTitle">
              {{turnNum(key,dataInfo.subject_count)}}、多选题。（{{teacherScore(item,2)}}）分
            </div>
            <div style="margin-bottom:40px">
            <span class="subTitle" v-if="!from">
            {{k+1}}.{{info.name}}
            </span>
            <span :class="{'subTitle':info.teacherscore!=0,'subError':info.teacherscore==0}" v-if="from">
              {{k+1}}.{{info.name}}
            </span>
              <div style="margin-right:-60px;">
                <div class="imgShow" v-if="info.imageurl.length" v-for="(img,i) in info.imageurl" :key="i">
                  <img v-lazy="'http://'+img" height="100%" width="100%" alt="">
                </div>
              </div>
              <div style="clear:both">
                <mt-checklist  v-if="!from"
                  v-model="info.nowCon"
                  :options="info.options"  >
                </mt-checklist>
                <ul v-if="from" :style="{'color':info.teacherscore!=0?'':'red'}">
                  <li class="errorLi" v-for="(item,key) in info.options" :key="key">
                    {{item}}
                  </li>
                  <li class="errorLi">学生答案：{{info.answers}}</li>
                  <li class="errorLi">老师答案：{{info.correctanswer}}</li>
                  <li class="teacherScore" >老师评分：{{info.teacherscore}}</li>
                </ul>
              </div>
            </div>
          </div>
          <!--填空题-->
          <div v-if="key==3 && item.length" v-for="(info,k) in item" :key="k">
            <!--题目类型-->
            <div class="workTitle">
              {{turnNum(key,dataInfo.subject_count)}}、填空题。（{{teacherScore(item,2)}}）分
            </div>
            <span class="subTitle" v-if="!from">
            {{k+1}}.{{info.name}}
            </span>
            <span :class="{'subTitle':info.teacherscore!=0,'subError':info.teacherscore==0}" v-if="from">
              {{k+1}}.{{info.name}}
            </span>
            <div style="margin-right:-60px;">
              <div class="imgShow" v-if="info.imageurl.length" v-for="(img,i) in info.imageurl" :key="i">
                <img v-lazy="'http://'+img" height="100%" width="100%" alt="">
              </div>
            </div>
            <input  v-if="!from" type="text" v-model="info.nowCon"   class="inputText" placeholder="答案" style="">
            <ul v-if="from" style="clear:both;">
              <li class="studentLi">学生答案：{{info.answers}}</li>
              <li class="studentLi">老师答案：{{info.correctanswer}}</li>
            </ul>
          </div>
          <!--作文题-->
          <div v-if="key==4 && item.length" v-for="(info,k) in item" :key="k" style="clear:both">
            <div class="workTitle">
              {{turnNum(key,dataInfo.subject_count)}}、作文题。（{{teacherScore(item,2)}}）分
            </div>
            <div  >
            <span class="subTitle" >
              {{k+1}}.{{info.name}}
            </span>
              <div style="margin-right:-60px;">
                <div class="imgShow" v-if="info.imageurl.length" v-for="(img,i) in info.imageurl" :key="i">
                  <img v-lazy="'http://'+img" height="100%" width="100%" alt="">
                </div>
              </div>
              <div style="margin-top:42px;clear:both">
                <div v-if="!from">
                  <div class="leftTitle" >
                    答案：
                  </div>
                  <textarea  name="" id="" class="textDiv"  v-model="info.nowCon" >
                  </textarea>
                </div>
                <ul v-if="from"  >
                  <li  class="studentLi">解析：{{info.analysis}}</li>
                  <li class="studentLi">学生答案：{{info.answers}}</li>
                  <li class="teacherScore">老师评分：{{info.teacherscore}}</li>
                  <li class="teacherScore">老师评语：{{info.comment}}</li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <!--按钮-->
        <div class="submitButton"  v-if="!from"  >
          <div class="blueSubmit" @click="submitInfo">
            提交
          </div>
        </div>

        <!--<div class="submitButton"  v-if="true==true">-->
          <!--<div class="blueSubmitNone">-->
            <!--您有未完成的习题-->
          <!--</div>-->
        <!--</div>-->
      </div>
    </verticalScroll>

  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import verticalScroll from '@/components/VerticalScroll';   //better-scrollz
  import {Toast, Radio,Checklist } from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyWorkInfo',
    components:{
      headerBack,
      'mt-radio':Radio,
      'mt-checklist':Checklist,
      verticalScroll
    },
    data(){
      return {
        lessonid:'',  //课时id
        classid:'',  //班级ID
        dataInfo:{},  //作业数据
        from:'',   //页面来源
      }
    },
    created(){
      this.from = this.$route.query.from;
      this.lessonid = this.$route.query.lessonid;
      this.classid = this.$route.query.classid;

      if(this.from){
        this.getCompleteQuestionList();
      }else{
        this.getQuestionList();
      }

    },
    methods: {
      // 提交
      submitInfo(){
        let nowArr = [];
        for(let key in this.dataInfo.subject){
          if(key==1){
            for(let item of this.dataInfo.subject[1]){
              let obj ={
                'subjectid':item.subjectid,
                'answer':item.nowCon?item.nowCon[0]:''
              };
              nowArr.push(obj);
            }
          }else if(key==2){
            for(let item of this.dataInfo.subject[2]){
              if(item.nowCon.length>0){
                for(let i in item.nowCon){
                  item.nowCon[i] = item.nowCon[i][0];
                }
              }
              let obj ={
                'subjectid':item.subjectid,
                'answer':item.nowCon?item.nowCon.sort().join(','):''
              };
              nowArr.push(obj);
            }
          }else{
            for(let item of this.dataInfo.subject[key]){
              let obj ={
                'subjectid':item.subjectid,
                'answer':item.nowCon
              };
              nowArr.push(obj);
            }
          }
        }
        Server.post(Path.submitQuestions,{
          'answers': nowArr,
          'classid':this.dataInfo.classid,
          'lessonid':this.dataInfo.lessonsid,
          'homeworkid':this.dataInfo.homeworkid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$router.go(-1);
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 单选
      checkRadio(index,subjectid){
        // 记录ID
        this.radioList[index]['subjectid'] = subjectid;
      },
      // 多选
      checkCheck(index,subjectid){
        // 记录ID
        this.checkList[index]['subjectid'] = subjectid;
      },
      // 填空
      inputCheck(index,subjectid){
        // 记录ID
        this.inputList[index]['subjectid'] = subjectid;
      },
      // 作文
      areaCheck(index,subjectid){
        // 记录ID
        this.areaList[index]['subjectid'] = subjectid;
      },
      // 转换阿拉伯数字
      turnNum(num,length){
        let cnum = ['零', '一', '二', '三', '四', '五', '六', '七', '八', '九'];
        if(num<=length){
          return cnum[num];
        }else{
          return cnum[Math.abs(num-length)];
        }
      },
       // 查询我的题库
      getQuestionList(){
        Server.post(Path.getQuestionList,{
          'lessonid':this.lessonid,
          'classid':this.classid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.dataInfo = data;
            for(let key in this.dataInfo.subject){
              if(key==2){
                for(let item of this.dataInfo.subject[2]){
                  item.nowCon = [];
                }
              }
            }

          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 查询老师批阅过的作业
      getCompleteQuestionList(){
        Server.post(Path.getCompleteQuestionList,{
          'lessonid':this.lessonid,
          'classid':this.classid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.dataInfo = data;
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      check: function () {
        console.log(this.value)
      },
      // 老师评分
      teacherScore(info,type){
        let allNum = 0;
        for(let item of info){
          if(type==1){
            allNum+=item.teacherscore;
          }else{
            allNum+=item.score;
          }
        }
        return allNum;
      }
    },
    watch:{
      value:function(value){
        console.log(value);
      }
    }
  }
</script>
<style lang="scss" scoped>
  .studentLi{
    line-height: 50px;
    font-size:24px;
    text-indent:32px;
  }
  .teacherScore{
    line-height: 50px;
    font-size:30px;
    text-indent:32px;
  }
  .errorLi{
    line-height: 50px;
    font-size:24px;
    text-indent:32px;
  }
  .textDiv{
    border:2px solid rgba(217,217,217,1);
    height:500px;
    width:500px;
    padding:15px;
  }
  .imgShow{
    width:300px;
    height:200px;
    float:left;
    overflow: hidden;
    border-radius: 10px;
    margin-right:60px;
    margin-top:20px;
    margin-bottom:20px;
  }
  .tableCard{
    /*table*/
    .table{
      font-size:26px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(61,61,61,1);
      box-sizing: border-box;
      width: 100%;
      margin-bottom:56px;
      tr{
        height:50px;
        td{
          text-align: center;
        }
      }
    }
  }
  .inputText{
    border:1px solid gray;
    border-radius: 5px;
    padding:5px;
    width:600px;
  }
  .MyWorkInfo{
    /*position:absolute;*/
    /*left:0;*/
    /*right:0;*/
    /*top:0;*/
    /*padding-bottom:200px;*/
    /*bottom:0;*/
    background:rgba(255,255,255,1);
    .infoContent{
      background:rgba(255,255,255,1);
      box-sizing: border-box;
      padding:69px 53px 172px 37px;
      .title{
        height:81px;
        text-align: center;
        font-size:34px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(61,61,61,1);
      }
      /*题目类型*/
      .workTitle{
        height:68px;
        font-size:30px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(61,61,61,1);
      }
      /*提交按钮*/
      .submitButton{
        padding-top:46px;
        clear:both;
        font-size:30px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(255,255,255,1);
        .blueSubmit{
          line-height:88px;
          text-align: center;
          width:300px;
          height:88px;
          margin:0px auto;
          background:rgba(57,173,239,1);
          border-radius:8px;
        }
        .blueSubmitNone{
          line-height:88px;
          text-align: center;
          width:300px;
          height:88px;
          background:gray;
          margin:0px auto;
          border-radius:8px;
          color:white;
        }
      }
      /*题目名称*/
      .subTitle{
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:42px;
      }
      .subError{
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:red;
        line-height:42px;
      }
      .leftTitle{
        height:46px;
        line-height: 46px;
        font-size:30px;
        float:left;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(61,61,61,1);
      }
      .textArea{
        width:545px;
        float:left;
        box-sizing: border-box;
        height:501px;
        padding:30px;
        border:1px solid rgba(179,179,179,1);
        border-radius:2px;
      }
    }

  }
</style>



