<template>
  <div id="headerBack">
    <div class="Headbar-back"  v-if="loginStatus==true"  @click="back()">
    </div>
    <div class="Headbar-title">{{title}}</div>
    <slot></slot>
    <div class="Headbar-search"  v-if="typeStatus=='icon'"  @click="openBottom">
      <img src="~@/assets/images/searchIcon.png" height="100%" alt="">
    </div>
    <div class="Headbar-search"  v-if="typeStatus=='tittle'"  @click="openBottom">
      {{rightTitle}}
    </div>
  </div>
</template>
<script>
import {Toast} from 'Mint-ui';
let that={};
export default{
  name:'HeaderBack',
  components:{

  },
  mounted(){
    if(this.$route.path=='/Login'){
      this.loginStatus = false;
    }
  },
  created(){
    that = this;
  },
  data(){
    return {
      loginStatus:true,
      backAgain:false,   //双击退出
      setTimeout:{},  //定时器
    }
  },
  props:{
    title:{
      type:String,
      default(){
        return '';
      }
    },
    typeStatus:{
      type:String,
      default(){
        return '';
      }
    },
    rightTitle:{
      type:String,
      default(){
        return '';
      }
    },
  },
  methods:{
    back(){
      let  nowPath = this.$route.path;
      if(nowPath=='/'){
        if(!this.backAgain){
          Toast('再次点击退出');
          this.backAgain = true;
          this.setTimeout = setTimeout(function(){
            that.backAgain = false;
            clearTimeout(that.setTimeout);
          }, 3000);
        }else{
          localStorage.removeItem('appstudentinfo');    //清楚用户缓存信息
          localStorage.removeItem('appstudentkey');     //清楚用户key值
          localStorage.removeItem('code');     //清楚用户code值
          let url = window.location.protocol + '//' + window.location.host + '/appstudentmic/#/Login';
          window.location.href = url;
          // this.$router.replace('Login');
        }
        return;
      }
      if(this.$route.query.orderstatus == 0) {
        this.$router.go(-1);
      }
      this.$router.go(-1);
    },
    openBottom(){
      this.$emit('clickMe');//select事件触发后，自动触发showCityName事件
    },
  },

}
</script>
<style  scoped lang="scss">
  @import "~@/assets/common/variable";
  @import "~@/assets/common/mixin";
  #headerBack {
    z-index: 3;
    position: relative;
    height: 92px;
    line-height: 92px;
    font-size: $font-size-36;
    text-align: center;
    background-color: $color-white;
    color: $color-black;
    @extend .boxShadow;
    .Headbar-back {
      position: absolute;
      left: 17px;
      top: 20px;
      display: inline-block;
      width: 48px;
      height: 48px;
      @include bg-image('./assets/nav_icon_back');
      background-repeat: no-repeat;
      background-size: cover;
    }
    .Headbar-title {
      display: inline-block;
    }
    .Headbar-search {
      position: absolute;
      right: 17px;
      top: 20px;
      display: inline-block;
      width: 100px;
      height: 48px;
      line-height: 48px;
      background-repeat: no-repeat;
      background-size: cover;
      font-size:32px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(61,61,61,1);
    }

  }
</style>



