<template>
  <!--我的作业-->
  <div class="MyHomework">
    <headerBack title="我的作业"></headerBack>
    <mt-navbar :value="selected" v-model="selected" >
      <mt-tab-item id="0" value="1">未完成</mt-tab-item>
      <mt-tab-item id="1">未批阅</mt-tab-item>
      <mt-tab-item id="2">已批阅</mt-tab-item>
    </mt-navbar>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="0">
            <div infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content">
              <!--未提交-->
              <myWorkNone :data="notInfo"></myWorkNone>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div  infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <!--未批阅-->
              <myWorkNone :data="alreadyInfo" :type="1"  @aginSend="getHomeworkList"></myWorkNone>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div  infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <!--已订阅-->
              <myWorkNone :data="alreadyContent" :type="2"></myWorkNone>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <notInfo v-if="infoStatus">
      抱歉，没有作业
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import myWorkNone from '../components/MyWorkNone';      // 未结束
  import {Loadmore, Navbar,Toast, TabItem ,TabContainer, TabContainerItem} from 'mint-ui';
  import notInfo from '@/components/NotInfo';           //没有数据
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyHomework',
    components:{
      headerBack,
      myWorkNone,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-loadmore':Loadmore
    },
    data(){
      return{
        notInfo:[],         //未使用的作业数据
        alreadyInfo:[],     //已完成的作业数据
        alreadyContent:[],     //已订阅数据
        selected:'0',   //作业类型

        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      if(window.localStorage.getItem('selectedWork') && window.localStorage.getItem('selectedWork')!=='0'){
        this.selected = window.localStorage.getItem('selectedWork');
        return;
      }
      // 查询已经完成的作业
      this.getHomeworkList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.getHomeworkList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.getHomeworkList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 查询已经完成的作业
      getHomeworkList(){
        Server.post(Path.getHomeworkList,{
          'pagenum':this.pagenum,
          'status':this.selected
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = data.pageinfo.total;
            this.limit = data.pageinfo.limit;
            if (this.pagenum != 1 ) {
              if(this.selected==='0'){
                this.notInfo = this.notInfo.concat(data.data);
              }else if(this.selected==='1'){
                this.alreadyInfo = this.alreadyInfo.concat(data.data);
              }else{
                this.alreadyContent = this.alreadyContent.concat(data.data);
              }
            } else {
              if(this.selected==='0'){
                this.notInfo = data.data;
              }else if(this.selected==='1'){
                this.alreadyInfo = data.data;
              }else{
                this.alreadyContent = data.data;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedWork', this.selected);
        this.pagenum = 1;
        // 再次请求列表数据
        this.getHomeworkList();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyHomework{

  }
</style>



