  export default{
    name:"LoginIcon"
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .LoginIcon{
    .topIcon{
      height:350px;
      display:flex;
      align-items: center;
      justify-content: center;
      .imgShow{
        height:130px;
        width:350px;
        @include bg-image("../../containers/Main/assets/icon_logo");
      }
    }
