import Vue from 'vue'
import Router from 'vue-router'
// 主体
import Main from '@/containers/Main';
// 登陆
import Login from '@/containers/Main/Login';
// 注册
import Register from '@/containers/Main/Register';
// 个人信息编辑
import DataEditing from '@/containers/Main/DataEditing';
// 课程筛选
import CourseScreening from '@/containers/Main/CourseScreening';
// 兴趣推荐
import SelectInterest from '@/containers/Main/SelectInterest';
// 修改个性签名
import Autograph from '@/containers/Main/Autograph';
// 修改手机号
import ChangePhone from '@/containers/Main/ChangePhone';
// 修改密码
import ChangePass from '@/containers/Main/ChangePass';

// 首页
import Home from '@/containers/Home';
// 微信授权页
import Author from '@/containers/Home/Author';
// 三级分类下的课程
import CourseList from '@/containers/Home/CourseList';
// 名师列表
import TeacherList from '@/containers/Home/TeacherList';
// 名师详情
import TeacherInfo from '@/containers/Home/TeacherInfo';
// 课程详情
import CourseDetails from '@/containers/Home/CourseDetails';
// 课程选择支付
import CourseSelect from '@/containers/Home/CourseSelect';
// 全部课程
import AllCourses from '@/containers/Home/AllCourses';
// 课程搜索
import SearchCourse from '@/containers/Home/SearchCourse';
// 立即报名
import SignUp from '@/containers/Home/SignUp';
// 支付反馈
import PaymentBack from '@/containers/Home/PaymentBack';
// 套餐详情
import MealInfo from '@/containers/Home/MealInfo';
// 套餐订单显示
import ShowOrder from '@/containers/Home/ShowOrder';
// 套餐列表
import MealList from '@/containers/Home/MealList';


// 课程


// 我的
import Mine from '@/containers/Mine';
// 我的余额
import MyBalance from '@/containers/Mine/MyBalance';
// 钱财使用明细
import AccountDetails from '@/containers/Mine/AccountDetails';
// 我的课表
import MyTimetable from '@/containers/Mine/MyTimetable';
// 我的课程
import MyCourse from '@/containers/Mine/MyCourse';
// 我的课程录播课评价
import EndEvaluate from '@/containers/Mine/EndEvaluate';
// 我的录播课详情
import MyEndInfo from '@/containers/Mine/MyEndInfo';
// 我的订单
import MyOrder from '@/containers/Mine/MyOrder';
// 我的作业
import MyHomework from '@/containers/Mine/MyHomework';
// 我的作业详情
import MyWorkInfo from '@/containers/Mine/MyWorkInfo';
// 我的收藏
import MyCollection from '@/containers/Mine/MyCollection';
// 我的消息
import MyNews from '@/containers/Mine/MyNews';
// 课堂反馈
import WonderfulMoment from '@/containers/Mine/WonderfulMoment';
// 我的套餐
import SetMeal from '@/containers/Mine/SetMeal';
// 精彩瞬间详情
import MarveInfo from '@/containers/Mine/MarveInfo';
// 点评详情
import ReviewInfo from '@/containers/Mine/ReviewInfo';
// App学习
import AppDownload from '@/containers/Mine/AppDownload';
// 地址管理
import AddressSelect from '@/containers/Mine/AddressSelect';
// 添加地址
import AddAddress from '@/containers/Mine/AddAddress';
// 签到成功
import SignInSuccess from '@/containers/Mine/SignInSuccess'
// 更改背景
import SelectBackground from '@/containers/Mine/SelectBackground'
// 历史签到
import HistorySignIn from '@/containers/Mine/HistorySignIn'

Vue.use(Router)

export default new Router({
  routes: [
    // 登陆
    {
      path:'/Login',
      name:'Login',
      component:Login
    },
    // 注册
    {
      path:'/Register',
      name:'Register',
      component:Register
    },
    // 信息编辑
    {
      path:'/DataEditing',
      name:'DataEditing',
      component:DataEditing
    },
    // 课程筛选
    {
      path:'/CourseScreening',
      name:'CourseScreening',
      component:CourseScreening
    },
    // 兴趣推荐
    {
      path:'/SelectInterest',
      name:'SelectInterest',
      component:SelectInterest
    },
    // 修改个性签名 昵称
    {
      path:'/Autograph',
      name:'Autograph',
      component:Autograph
    },
    // 修改手机号
    {
      path:"/ChangePhone",
      name:'ChangePhone',
      component:ChangePhone
    },
    // 修改密码
    {
      path:'/ChangePass',
      name:'ChangePass',
      component:ChangePass
    },
    {
      // 根目录
      path: '/',
      component: Main,
      children:[
        {
          // 首页
          path:'',
          name:'Home',
          component:Home
        },
        {
          // 课程
          path:'/AllCourses',
          name:'AllCourses',
          component:AllCourses
        },
        {
          // 我的
          path:'Mine',
          name:'Mine',
          component:Mine
        }
      ]
    },

    // 首页

    // 三级分类下的课程
    {
      path:'/CourseList',
      name:'CourseList',
      component:CourseList
    },
    // 微信授权页
    {
      path:'/Author',
      name:'Author',
      component:Author
    },
    // 名师列表
    {
      path:'/TeacherList',
      name:'TeacherList',
      component:TeacherList
    },
    // 名师详情
    {
      path:'/TeacherInfo',
      name:'TeacherInfo',
      component:TeacherInfo
    },
    // 课程详情
    {
      path:'/CourseDetails',
      name:'CourseDetails',
      component:CourseDetails
    },
    // 课程选择支付
    {
      path:'/CourseSelect',
      name:'CourseSelect',
      component:CourseSelect
    },
    // 课程搜索
    {
      path:"/SearchCourse",
      name:'SearchCourse',
      component:SearchCourse
    },
    // 立即报名
    {
      path:'/SignUp',
      name:'SignUp',
      component:SignUp
    },
    // 支付反馈
    {
      path:'/PaymentBack',
      name:'PaymentBack',
      component:PaymentBack
    },
    // 套餐详情
    {
      path:'/MealInfo',
      name:'MealInfo',
      component:MealInfo
    },
    // 套餐订单显示
    {
      path:'/ShowOrder',
      name:'ShowOrder',
      component:ShowOrder
    },
    // 套餐列表
    {
      path:'/MealList',
      name:'MealList',
      component:MealList
    },
    // 我的
    // 我的余额
    {
      path:'/MyBalance',
      name:'MyBalance',
      component:MyBalance
    },
    // 余额明细
    {
      path:'/AccountDetails',
      name:"AccountDetails",
      component:AccountDetails
    },
    // 我的课表
    {
      path:'/MyTimetable',
      name:'MyTimetable',
      component:MyTimetable
    },
    // 我的课程
    {
      path:'/MyCourse',
      name:'MyCourse',
      component:MyCourse
    },
    // 我的课程录播课详情
    {
      path:"/MyEndInfo",
      name:'MyEndInfo',
      component:MyEndInfo
    },
    // 我的课程录播课评价
    {
      path:'/EndEvaluate',
      name:'EndEvaluate',
      component:EndEvaluate
    },
    // 我的订单
    {
      path:'/MyOrder',
      name:'MyOrder',
      component:MyOrder
    },
    // 我的作业
    {
      path:'/MyHomework',
      name:'MyHomework',
      component:MyHomework
    },
    // 我的作业详情
    {
      path:'/MyWorkInfo',
      name:'MyWorkInfo',
      component:MyWorkInfo
    },
    // 我的收藏
    {
      path:'/MyCollection',
      name:'MyCollection',
      component:MyCollection
    },
    // 我的消息
    {
      path:'/MyNews',
      name:'MyNews',
      component:MyNews
    },
    // 课堂反馈
    {
      path:'/WonderfulMoment',
      name:'WonderfulMoment',
      component:WonderfulMoment
    },
    // 我的套餐
    {
      path:'/SetMeal',
      name:'SetMeal',
      component:SetMeal
    },
    // 精彩瞬间详情
    {
      path:'/MarveInfo',
      name:'MarveInfo',
      component:MarveInfo
    },
    // 点评详情
    {
      path:'/ReviewInfo',
      name:'ReviewInfo',
      component:ReviewInfo
    },
    // App学习
    {
      path:'/AppDownload',
      name:'AppDownload',
      component:AppDownload
    },
    // 地址管理
    {
      path:'/AddressSelect',
      name:'AddressSelect',
      component:AddressSelect
    },
    // 添加地址
    {
      path:'/AddAddress',
      name:'AddAddress',
      component:AddAddress
    },
    // 签到成功
    {
      path:'/SignInSuccess',
      name:'SignInSuccess',
      component:SignInSuccess
    },
    // 历史签到
    {
      path:'/HistorySignIn',
      name:'HistorySignIn',
      component:HistorySignIn
    },
    // 更改背景
    {
      path:'/SelectBackground',
      name:'SelectBackground',
      component:SelectBackground
    }
  ]
})




