<template>
  <!--课程下的三级分类列表-->
  <div >
    <!--返回导航-->
    <headerBack title="全部课程"></headerBack>
      <div class="wrapper" ref="scroll">
        <mt-loadmore :top-method="loadTop"
                     :bottom-method="loadBottom"
                     :bottom-all-loaded="allLoaded"
                     ref="loadmore"  >
          <div class="MyCollCourse"  v-for="(item,key) in dataInfo" :key="key" @click="toPage(item.courseid,item.classtypes)">
            <div class="imgShow">
              <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
              <div class="classType" v-if="item.classtypes==2">
                直播
              </div>
              <div class="classBlue" v-if="item.classtypes==1">
                录播
              </div>
            </div>
            <div class="infoContent">
              <div class="title text-ellipsis">
                {{item.coursename}}
              </div>
              <div class="subTitle text-ellipsis">
                {{item.subhead}}
              </div>
              <div class="teacher">
                <!--{{data.teachername}}-->
              </div>
              <div class="price">
                <template v-if="item.classtypes==1 ||(item.classtypes==2 && item.price==item.maxprice) ">
                  ￥{{item.price}}/元
                </template>
                <template v-else>
                  ￥{{item.price}}-{{item.maxprice}}/元
                </template>
              </div>
            </div>
          </div>
        </mt-loadmore>
      </div>

    <notInfo v-if="infoStatus">
      抱歉，没有指定分类数据
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import notInfo from '@/components/NotInfo';     //返回组件
  import { Loadmore } from 'mint-ui';
  export default{
    name:'CourseList',
    components:{
      headerBack,
      notInfo,
      'mt-loadmore':Loadmore
    },
    data(){
      return{
        categoryid:'',      //分类集合ID
        dataInfo:[],   //数组集合
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      this.categoryid = this.$route.query.id;
      // 请求三级信息
      this.getThreeCourseList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.getThreeCourseList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.getThreeCourseList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 前往课程详情页
      toPage(courseid,classtypes){
        this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes}});
      },
      // 请求三级信息
      getThreeCourseList(){
        Server.post(Path.getFilterCourserList, {
          'category_id':this.categoryid,
          'is_free':0,
          'pagenum':this.pagenum
        }, (res) => {
          if (res.code === 0) {
            if(res.data.schedulist.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              //页码不为一，追加
              this.dataInfo = this.dataInfo.concat(res.data.schedulist);
            } else {
              //否则从第一页开始，覆盖列表数据
              this.dataInfo = res.data.schedulist;
            }
          }
        });
      }
    },
    mounted: function(){
      this.getThreeCourseList();
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyCollCourse {
    height: 200px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 20px 36px 18px 25px;
    border-bottom: 1px solid rgba(217, 217, 217, 1);
  }
  .MyOrderCourse{
    height: 200px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 20px 36px 18px 25px;
  }
  .imgShow{
    width:280px;
    height:160px;
    border-radius:8px;
    float:left;
    margin-right:21px;
    overflow:hidden;
    position:relative;
    .classType{
      width:74px;
      height:40px;
      line-height:40px;
      text-align: center;
      border-top-left-radius: 8px;
      left:0px;
      top:0px;
      background:rgba(255,152,106,1);
      position:absolute;
      font-size:24px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(255,254,253,1);
    }
    .classBlue{
      width:74px;
      height:40px;
      line-height:40px;
      text-align: center;
      left:0px;
      top:0px;
      background:rgba(47,172,250,1);
      border-top-left-radius: 8px;
      position:absolute;
      font-size:24px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(255,254,253,1);
    }
  }
  .infoContent{
    height:160px;
    width: calc(100% - 362px);
    float:left;
    /*background:red;*/
    .title{
      height:49px;
      font-size:30px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(77,77,77,1);
    }
    .subTitle{
      height:43px;
      font-size:26px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(77,77,77,1);
    }
    .teacher{
      height:46px;
      font-size:26px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(107,107,107,1);
    }
    .price{
      font-size:26px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      text-align: right;
      color:rgba(231,19,19,1);
    }
  }
</style>



