<template>
  <div class="banner" id="Banner"  v-if="listImg.length!=0">
    <swiper :options="swiperOption" ref="banner">
      <swiper-slide v-for="(item, i) in listImg" :key="i" v-if="item.imagepath!=''">
          <div style="height:100%; width:100%;"  @click="toPage(item.urltype,item.teacherid,item.courseid,item.coursetype)">
            <img :src="item.imagepath" width="100%" height="100%" alt="" v-if="i==0">
            <img v-lazy="item.imagepath" width="100%" height="100%" alt="" v-else>
          </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import 'swiper/dist/css/swiper.css'
  import Server from '@/service/request';
  import Path from '@/service/Path';

  export default {
    name: "Banner",
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {
        swiperOption: {
          autoplayDisableOnInteraction: false,
          autoplay: 3000,
          initialSlide:0,
          loop: true,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          type: 'bullets',
          notNextTick: true,
          direction: 'horizontal',
          pagination: '.swiper-pagination',
          // slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 30,
          onSlideChangeEnd: swiper => {
            this.page = swiper.realIndex + 1;
            this.index = swiper.realIndex;
          }
        },
        listImg: [],
      }
    },
    created() {
      // this.organid = JSON.parse(window.localStorage.getItem('appstudentinfo')).organid;
      // 获取轮播图
      this.getBanners();
    },
    methods: {
      // 前往详情页
      toPage(type,teacherid,courseid,classtypes){
        if(type==1){      //课程详情
          this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes}});
        }else if(type==2){ //老师详情
          this.$router.push({path:'/TeacherInfo',query:{teacherId:teacherid}});
        }else{

        }
      },
      getBanners() {
        Server.post(Path.getSlideList, {
        }, (res) => {
          if (res.code === 0) {
            // 绑定数据
            this.listImg = res.data;
          }
        });
      },
    },
    //定义这个sweiper对象
    computed: {
      swiper() {
        return this.$refs.banner.swiper;
      },
    }
  }
</script>

<style scoped lang="scss">
  .swiper-container {
    width: 100%;
    height: 340px;
    overflow: hidden;
    .swiper-wrapper {
      width: 100%;
      height: 340px;
      .swiper-slide {
        width: 100%;
        height: 100%;
        img {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

</style>



