<template>
  <div v-if="listInfo.length!=0">
    <itemTitle title="学习套餐" router="/MealList"></itemTitle>
    <div class="MealItem">
      <div style="display:flex">
          <div  class="liFlex"  @click="toPage(item.packageid)" v-for="(item,key) in listInfo" :key="key">
            <div class="flexOut">
              <div class="imgShow">
                <img v-lazy="item.setimgpath" width="100%" height="100%" alt="">
              </div>
              <div class="infoInfo text-ellipsis">
                {{item.setmeal}}
              </div>
              <div class="price">
                ￥{{item.setprice}}元
              </div>
            </div>
          </div>
      </div>
    </div>
  </div>

</template>
<script>
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import itemTitle from '../ItemTitle';   //查看全部
  export default{
    name:'MealItem',
    components:{
      itemTitle
    },
    data(){
      return {
        listInfo:[]     //数据列表
      }
    },
    created() {
      // 获取前三条数据
      this.getHomePackageList();
    },
    methods:{
      // 获取前三条数据
      getHomePackageList(){
        // 获取列表数据
        Server.post(Path.getHomePackageList, {
        }, (res) => {
          if (res.code === 0) {
            // 绑定数据
            this.listInfo = res.data;
          }
        });
      },
      toPage(id){
        this.$router.push({'path':'MealInfo',query:{'packageid':id}})
      }
    }
  }
</script>
<style scoped lang="scss">
  .MealItem{
    height:244px;
    box-sizing: border-box;
    padding:10px 14px 0px 14px;
    background:rgba(255,255,255,1);
    .liFlex{
      height:234px;
      width:100/3*100%;
      .flexOut{
        width:220px;
        margin:0px auto;
        .imgShow{
          width:220px;
          height:126px;
        }
        .infoInfo{
          margin-top:20px;
          height:36px;
          font-size:26px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(51,51,51,1);
        }
        .price{
          text-align: right;
          font-size:24px;
          font-family:MicrosoftYaHeiLight;
          font-weight:300;
          color:rgba(231,19,19,1);
        }
      }

    }
  }

</style>



