<template>
  <!--名师详情-->
  <div>
    <!--头部返回-->
    <headerBack title="柠檬教育"></headerBack>
    <verticalScroll>
      <!--老师名片-->
      <teacherInfo @teacherCollect="teacherCollect" :data='teacherInfo'></teacherInfo>
      <!--他的课程列表-->
      <div class="TeacherInfo">
        <!--圆点item-->
        <circleBlock :title="teacherInfo.sex==1?'他的课程':'她的课程'"></circleBlock>
        <!--<verticalScroll :data="courseInfo" scrollType="Tab">-->
          <fullItem :data="courseInfo"  :teacherid="teacherInfo.teacherid"  :show-status="0"></fullItem>
        <!--</verticalScroll>-->
      </div>
    </verticalScroll>

    <notInfo v-if="infoStatus">
      抱歉，该老师没有更多课程
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import teacherInfo from '../components/TeacherInfo';        //老师名片
  import fullItem from '../components/FullItem';     //整行的课程信息
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import circleBlock from '@/components/CircleBlock';     //整行的课程信息
  import notInfo from '@/components/NotInfo';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  export default{
    name:'TeacherInfo',
    components:{
      headerBack,
      teacherInfo,
      fullItem,
      circleBlock,
      verticalScroll,
      notInfo
    },
    data(){
      return{
        infoStatus:false, // 默认没有数据不显示
        teacherInfo:{},   //老师信息
        studentid:'',  //学生ID
        teacherid:'',  //老师ID
        // 他的课程信息
        courseInfo:[
        ]
      }
    },
    created() {
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 教师ID
      this.teacherId = this.$route.query.teacherId;
      // 请求老师详情信息
      this.getTeacherCurriculum(this.teacherId);
      // 请求老师的课程
      this.getTeacherClass(this.teacherId);
    },
    methods:{
      // 老师信息详情
      getTeacherCurriculum(id){
        // 获取名师堂数据
        Server.post(Path.getTeacherCurriculum, {
          'teacherid':id
        }, (res) => {
          if (res.code === 0) {
            this.teacherInfo = res.data;
          }
        });
      },
      // 老师的课程
      getTeacherClass(id){
        // 获取老师的课程
        Server.post(Path.getTeacherClass, {
          'teacherid':id,
          'classtype':2
        }, (res) => {
          console.log(res.data);
          if (res.code === 0) {
            if(res.data.data.length==0){
              this.infoStatus = true;
            }else{
              this.courseInfo = res.data.data;
            }

          }
        });
      },
      // 收藏老师
      teacherCollect(){
        Server.post(Path.teacherCollect, {
          'teacherid':this.teacherId,
          'studentid':this.studentid
        }, (res) => {
          if (res.code === 0) {
            this.teacherInfo.is_collect = 1;
            Toast('收藏成功');
          }
        });
      }
    },
    mounted(){

    }
  }
</script>
<style lang="scss" scoped>
.TeacherInfo{
  margin-top:-23px;
  background:rgba(255,255,255,1);
  border-radius:20px 20px 0px 0px;
  position:relative;
  overflow:hidden;
}

</style>



