            三年级看图写话-考前冲刺班
            如果字数多就换行
          </div>
          <div class="teacher">
            芒果老师
          </div>
          <div class="date">
            时间：18:00~18:30
            <div class="blueBlock">
              查看点评
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name:'MyTimeItem'
  }
</script>
<style lang="scss" scoped>
  .MyTimeItem{
    box-sizing:border-box;
    height:258px;
    background:rgba(255,255,255,1);
    border-bottom:1px solid rgba(217,217,217,1);
    padding:0px 24px;
    .title{
      height:68px;
      line-height:68px;
      font-size:30px;
      font-family:PingFang-SC-Medium;
      font-weight:normal;
      color:rgba(61,61,61,1);
      .rightTitle{
