    .imgOut{
      height:160px;
      clear:both;
      .imgShow{
        width:280px;
        height:160px;
        border-radius:8px;
        background:red;
        float:left;
        margin-right:13px;
      }
      .infoContent{
        height:160px;
        width:400px;
        float:left;
        .subTitle{
          height:65px;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(77,77,77,1);
          line-height:34px;
        }
        .teacher{
          height:45px;
          line-height: 45px;
          font-size:24px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(107,107,107,1);
        }
        .date{
          height:50px;
          line-height: 50px;
          font-size:24px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(102,102,102,1);
          .blueBlock{
            height:50px;
            width:120px;
            line-height: 50px;
            float:right;
            background:rgba(57,173,239,1);
            border-radius:4px;
            text-align: center;
          }
        }
      }
    }
  }
</style>



