<template>
  <!--套餐详情-->
  <div class="MealInfo">
    <headerBack title="套餐详情"></headerBack>
    <verticalScroll scrollType='Home'>
      <!--顶部课程宣传画-->
      <courseImg :imgUrl="contentInfo.setimgpath"></courseImg>
      <!--优惠套餐A-->
      <circleBlock  style="margin-top:8px;font-size:34px;"   :title="contentInfo.setmeal"></circleBlock>
      <div class="infoInfo">
        <div style="margin-bottom:10px;">
          套餐内包含{{contentInfo.bughour}}课时直播
          <span v-if="contentInfo.givestatus==1">
              <span v-if="contentInfo.sendvideo!==0">
                （赠送{{contentInfo.sendvideo}}课时录播课）
              </span>
            <span v-if="contentInfo.sendlive!==0">
                （赠送{{contentInfo.sendlive}}课时直播课）
            </span>
          </span>,<br>
          有效期为
          <span v-if="contentInfo.efftype==1" >
            ({{formatDateTime(contentInfo.effstarttime)}} - {{formatDateTime(contentInfo.effendtime)}})
          </span>
          <!--//时间范围-->
          <span v-if="contentInfo.efftype==2" >({{contentInfo.efftime}} 天)</span>
          <!--//固定时间-->

        </div>
        <div>
          购买须知：
        </div>
        <div>
          <!--{{contentInfo.content}}-->
          本套餐内容仅限指定课程可使用，需在有效期截止前使用，逾期将无法使用。
        </div>
      </div>
      <!--可用课程-->
      <circleBlock   title="可用课程">
      </circleBlock>
      <div class="showAll"  v-if="contentInfo.trialtype!='3'">
        <router-link to="/AllCourses">
          <span class="allCourse"  v-if="contentInfo.trialtype=='1'">
            全部课程
          </span>
        </router-link>
        <ul v-if="contentInfo.trialtype=='2'">
          <li v-for="(item,key) in typeInfo"  @click="toCourseList(item.id[item.id.length-1])">
            {{item.name}}
          </li>
        </ul>
      </div>
      <mealSwiper  v-if="contentInfo.trialtype=='3'" :data="contentInfo.courselist"></mealSwiper>
      <!--赠送-->
      <div v-if="contentInfo.givestatus==1">
        <circleBlock   title="赠送"></circleBlock>
        <div class="bottomContent">
          <div>
            <div class="imgShow">
              <img src="~@/assets/images/give.png" width="100%" alt="">
            </div>
            <div class="infoContent">
              <div class="title">
                <template v-if="contentInfo.sendvideo!==0">
                {{contentInfo.sendvideo}}课时录播课
              </template>
                <template v-if="contentInfo.sendlive!==0">
                {{contentInfo.sendlive}}课时直播课
               </template>
              </div>
              <div class="middleInfo">
                本套餐内容购买课程
                <template v-if="contentInfo.giftthreshold==0">没有使用门槛</template>
                <template v-if="contentInfo.giftthreshold!=0">满{{contentInfo.giftthreshold}}元可以使用</template>，每人
                <template v-if="contentInfo.limitbuy==0">不限购</template>
                <template v-if="contentInfo.limitbuy!=0">限购{{contentInfo.limitbuy}}次</template>
                <template v-if="contentInfo.giftefftype==0">全部课程可用</template>
                <template v-if="contentInfo.giftefftype==1">指定分类课程可用</template>
                <template v-if="contentInfo.giftefftype==2">指定课程可用</template>
               ，需在有效期截止前使用，逾期将无法使用
              </div>
              <div class="price">
                <div class="leftNum">
                  有效期至:<template v-if="contentInfo.giftefftype==1" >
                  {{formatDateTime(contentInfo.effstarttime)}} - {{formatDateTime(contentInfo.effendtime)}}
                  </template>
                  <!--//时间范围-->
                  <template v-if="contentInfo.giftefftype==2" >({{contentInfo.giftefftime}} 天)</template>
                  <!--//固定时间-->
                </div>
                <!--<div class="rightNum">-->
                  <!--888元-->
                <!--</div>-->
              </div>
            </div>
          </div>
          <!--适用范围-->
          <!--<div class="grayBlock">-->
            <!--<div class="grayShow">-->
              <!--<div class="leftInfo">-->
                <!--适用范围：-->
              <!--</div>-->
              <!--<div class="rightInfo">-->
                <!--一至三年级的所有录播课以及三年级妙思阅读-->
                <!--课程体系下的1v6直播课-->
              <!--</div>-->
            <!--</div>-->
          <!--</div>-->
        </div>
      </div>
    </verticalScroll>
    <bottomButton v-if="!from" :buttonL="'￥'+toDecimal2(contentInfo.setprice)"   buttonR="立即报名" leftColor="rgba(235,28,28,1)" @selectedTab="selectedTab"  backgroundL="rgba(255,255,255,1)" backgroundR="rgba(57,173,239,1)"></bottomButton>
  </div>

</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import circleBlock from '@/components/CircleBlock';     //整行的课程信息
  import courseImg from '@/components/CourseImg';     //顶部宣传画
  import mealSwiper from '../components/MealSwiper'; //年级分类
  import bottomButton from '@/components/BottomButton';     //顶部宣传画
  import {Toast} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:"MealInfo",
    components:{
      headerBack,
      verticalScroll,
      courseImg,
      circleBlock,
      mealSwiper,
      bottomButton
    },
    data(){
      return {
        packageid:'',  //套餐ID
        contentInfo:{},   //信息数据
        typeInfo:[],      //数组数据
        from:'',    //判断是否是从订单页过来数据
      }
    },
    created(){
      // 记录套餐ID
      this.packageid = this.$route.query.packageid;
      this.from = this.$route.query.from;
      // 获取套餐详情
      this.getPackageDetail();
    },
    methods:{
      toDecimal2(x) {
        var f = parseFloat(x);
        if (isNaN(f)) {
          return false;
        }
        var f = Math.round(x * 100) / 100;
        var s = f.toString();
        var rs = s.indexOf('.');
        if (rs < 0) {
          rs = s.length;
          s += '.';
        }
        while (s.length <= rs + 2) {
          s += '0';
        }
        return s;
      },
      formatDateTime(timeStamp) {
        var date = new Date();
        date.setTime(timeStamp * 1000);
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        var second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '/' + m + '/' + d;
        },

      // 获取套餐详情
      getPackageDetail(){
        Server.post(Path.getPackageDetail,{
          'packageid':this.packageid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            this.contentInfo = data;
            // 判断是不是分类
            if(data.trialtype==2){
              for(var i in data.categoryarr){
                // 第一层没有子集
                  if(data.categoryarr[i].child.length==0){
                    let idArr = [];
                    idArr.push(data.categoryarr[i].id);
                    let obj = {
                      name:data.categoryarr[i].categoryname,
                      id:idArr
                    };
                    this.typeInfo.push(obj);
                  }else{
                    for(var a in data.categoryarr[i].child){
                      // 判断第二层没有子集
                      if(data.categoryarr[i].child[a].child.length==0){
                        let idArr = [];
                        // 存入父级ID
                        idArr.push(data.categoryarr[i].id);
                        // 存入自己ID
                        idArr.push(data.categoryarr[i].child[a].id);
                        let obj = {
                          name:data.categoryarr[i].categoryname+'-'+data.categoryarr[i].child[a].categoryname,
                          id:idArr
                        };
                        this.typeInfo.push(obj);
                      }else{
                        for(var b in data.categoryarr[i].child[a].child){
                          let idArr = [];
                          // 存入祖父级ID
                          idArr.push(data.categoryarr[i].id);
                          // 存入父级ID
                          idArr.push(data.categoryarr[i].child[a].id);
                          // 存入自己ID
                          idArr.push(data.categoryarr[i].child[a].child[b].id);
                          let obj = {
                            name:data.categoryarr[i].categoryname+'-'+data.categoryarr[i].child[a].categoryname+'-'+data.categoryarr[i].child[a].child[b].categoryname,
                            id:idArr
                          };
                          this.typeInfo.push(obj);
                        }
                      }
                    }
                  }
              }
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 套餐下单
      selectedTab(value){
        if(value=='right'){
          Server.post(Path.gotoOrder,{
            'packageid':this.packageid,
            'ordersource':'2'
          },(res)=>{
            let { code, data } = res;
            if(code===0){
              this.$router.push({path:"/ShowOrder",query:{orderNum:data.ordernum}})
            }else{
              Toast({
                message: res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }
          })
        }
      },
      // 前往课程展示页
      toCourseList(id){
        console.log(id);
        this.$router.push({path:'CourseList',query:{id:id}});
      }
    },
  }
</script>

<style lang="scss" scoped>
  .showAll{
    padding:44px 0px 44px 45px;
    background:rgba(255,255,255,1);
    margin-bottom:20px;
    ul{
      li{
        height:62px;
        float:left;
        margin-bottom:20px;
        margin-right:20px;
        line-height:62px;
        text-align:center;
        border:2px solid rgba(63,156,232,1);
        border-radius:20px;
        font-size:24px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        padding:0px 10px;
        color:rgba(63,156,232,1);
      }
    }
    /*background:red;*/
  }
  .allCourse{
    display:inline-block;
    height:62px;
    margin-top:44px;
    line-height:62px;
    text-align: center;
    padding:0px 10px;
    border:2px solid rgba(63,156,232,1);
    border-radius:20px;
    font-size:24px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(63,156,232,1);
  }
  .MealInfo{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom: 110px;
    overflow: hidden;
    .infoInfo{
      padding:20px 60px 22px 36px;
      background:rgba(255,255,255,1);
      font-size:20px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(153,153,153,1);
      margin-bottom:20px;
      line-height:30px;

    }
    .bottomContent{
      height:439px;
      box-sizing: border-box;
      background:rgba(255,255,255,1);
      padding:20px 36px 41px 37px;
      .imgShow{
        width:226px;
        height:132px;
        margin-right:20px;
        float:left;
      }
      .infoContent{
        width: calc(100% - 320px);
        float:left;
        height:30px;
        .title{
          height:36px;
          font-size:26px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(102,102,102,1);
        }
        .middleInfo{
          height:68px;
          width:425px;
          font-size:20px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(166,166,166,1);
          line-height:24px;
        }
        .price{
          height:27px;
          position:relative;
          .leftNum{
            padding-top:15px;
            height:27px;
            float:left;
            line-height: 18px;
            font-size:18px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(102,102,102,1);
            line-height:18px;
          }
          .rightNum{
            height:27px;
            line-height: 27px;
            position:absolute;
            right:0px;
            top:0px;
            font-size:20px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(255,164,0,1);
            line-height:46px;
          }
        }
      }
      .grayBlock{
        padding-top:39px;
        clear:both;
        .grayShow{
          width:677px;
          overflow: hidden;
          box-sizing: border-box;
          padding:20px 14px 20px 14px;
          background:rgba(245,245,245,1);
          .leftInfo{
            float:left;
            font-size:26px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            margin-right:9px;
            color:rgba(153,153,153,1);
            line-height:46px;
          }
          .rightInfo{
            float:left;
            font-size:26px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(153,153,153,1);
            line-height:46px;
            width:500px;
          }
        }
      }
    }

  }
  .courseNum{
    width:122px;
    height:62px;
    line-height: 62px;
    text-align: center;
    margin-top:15px;
    float:left;
    margin-right:32px;
    border:2px solid rgba(63,156,232,1);
    border-radius:20px;
  }
</style>



