<template>
    <div class="hisClass">
      <div class="circleBlock"></div>
      <div class="rightTitle">
        {{title}}
      </div>
      <div style="float:left;">
        <slot></slot>
      </div>
    </div>
</template>
<script>
  export default{
    name:'CircleBlock',
    props:{
      title:{
        type:String,
        default(){
          return '';
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
    .hisClass{
      /*vertical-align:middle;*/
      height:99px;
      padding-left:37px;
      /*line-height:99px;*/
      background:rgba(255,255,255,1);

      border-bottom:1px solid rgba(217,217,217,1);
      .circleBlock{
        /*vertical-align:middle;*/
        /*display:inline-block;*/
        width:10px;
        height:10px;
        float:left;
        margin-top:45px;
        background:rgba(102,102,102,1);
        border-radius:50%;
        margin-right:20px;
      }
      .rightTitle{
        height:99px;
        line-height: 99px;
        font-size:32px;
        font-family:PingFang-SC-Bold;
        font-weight:bold;
        float:left;
        color:rgba(77,77,77,1);

      }
    }
</style>



