<template>
  <!--我的消息-->
  <div class="MyNews myCellSwipe">
    <headerBack title="我的消息"></headerBack>
    <mt-navbar :value="selected" v-model="selected" >
      <mt-tab-item id="1" value="1">系统提醒</mt-tab-item>
      <mt-tab-item id="2">课程通知</mt-tab-item>
    </mt-navbar>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="1" v>
            <div
              infinite-scroll-disabled="loading"
              infinite-scroll-distance="10"
              class="content"
            >
              <mt-cell-swipe class="orderItem" v-for="(item,key) in newsInfo" :key="key"
                             :right="[
                      {content: '删除',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.id,key)}
                      ]" >
                <myNewsItem :data="item"></myNewsItem>
              </mt-cell-swipe>

            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div
              infinite-scroll-disabled="loadingReceive"
              infinite-scroll-distance="10"
              class="content"
            >
              <mt-cell-swipe class="orderItem" v-for="(item,key) in courseInfo" :key="key"
                             :right="[
                      {content: '删除',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.id,key)}
                      ]" >
                <myNewsItem :data="item"></myNewsItem>
              </mt-cell-swipe>

            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <notInfo v-if="infoStatus">
      抱歉，没有信息
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import myNewsItem from '../components/MyNewsItem';      // item
  import notInfo from '@/components/NotInfo';           //没有数据
  import { Loadmore,Navbar, TabItem ,TabContainer, TabContainerItem,CellSwipe} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyNews',
    components:{
      headerBack,
      myNewsItem,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-loadmore':Loadmore,
      'mt-cell-swipe':CellSwipe,
    },
    data(){
      return{
        selected:'1',
        studentid:'',     //学生ID
        newsInfo:[],   //系统通知
        courseInfo:[],  //课程通知

        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      if(window.localStorage.getItem('selectedNews') && window.localStorage.getItem('selectedNews')==2){
        this.selected = window.localStorage.getItem('selectedNews');
        return;
      }
      // 请求我的消息
      this.messageList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.messageList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.messageList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 请求我的消息
      messageList(){
        Server.post(Path.messageList,{
          'studentid':this.studentid,
          'pagenum':1,
          'limit':10,
          'type':this.selected
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.limit;
            if (this.pagenum != 1 ) {
              if(this.selected==='2'){
                this.courseInfo = this.courseInfo.concat(data.data);
              }else{
                this.newsInfo = this.newsInfo.concat(data.data);
              }
            } else {
              if(this.selected==='2'){
                this.courseInfo = data.data;
              }else{
                this.newsInfo = data.data;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 删除一条数据
      deleteItem(id,key){
        Server.post(Path.deleteMsg,{
          'messageids':id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 判断删除的是哪条数据
            if(this.selected=='1'){
              this.newsInfo.splice(key,1);
              // 全部删除无数据显示
              if(this.newsInfo.length==0){
                this.infoStatus = true;
              }
            }else{
              this.courseInfo.splice(key,1);
              // 全部删除无数据显示
              if(this.courseInfo.length==0){
                this.infoStatus = true;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedNews', this.selected);
        this.pagenum = 1;
        // 再次请求列表数据
        this.messageList();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyNews{

  }
</style>



