<template>
  <!--我的课表-->
  <div class="MyTimetable">
    <headerBack title="我的课表" typeStatus="tittle" rightTitle="调课"  @clickMe="clickMe"></headerBack>
    <div style="height:98px;">
      <mt-navbar :value="selected" v-model="selected" >
        <mt-tab-item id="0" value="1">待上课</mt-tab-item>
        <mt-tab-item id="1">已结束</mt-tab-item>
      </mt-navbar>
    </div>
    <!--<verticalScroll scrollType="Tab"  :data="vScrollData">-->
    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="0">
            <div infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content">
              <div v-for="(item,k) in beginInfo">
                <!--待上课-->
                <!--title组件-->
                <myTimeTitle :title="item.time"></myTimeTitle>
                <!--<myTimeTitle></myTimeTitle>-->
                <myTimeBegin :data="item.arr"></myTimeBegin>
              </div>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <div infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content"  >
                <div v-for="(item,k) in endInfo">
                  <!--已结束-->
                  <!--title组件-->
                  <myTimeTitle :title="item.time"></myTimeTitle>
                  <!--<myTimeTitle></myTimeTitle>-->
                  <myTimeBegin  @showReview="showReview"  @gotoComment="gotoComment" :data="item.arr" type="end"></myTimeBegin>
                </div>
              </div>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>


    <mt-popup
      v-model="popupVisible"
      position="bottom"
      style="padding-bottom:20px;width:100%"
    >
      <div class="bottomPopup">
        <div class="offIcon" @click="closeModal">
          <img src="~@/assets/images/icon_add.png" width="100%" alt="">
        </div>
        <div class="selectTop">
            课程名称：
          <div class="selectOption">
            <select style="width:100%;height:40px;"  @change="getClassInfo(selectValue)" v-model="selectValue" >
              <option value="0" disabled>请选择</option>
              <option  v-for="(items, index) in lecture" :key="index" :value="items">{{items.coursename}}</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <div class="selectTop">
            原班级名称:
          <div class="selectOption">
            <select style="width:100%;height:40px;" v-model="a1"  @change="getClassTimes(a1)">
              <option value="0" disabled>请选择</option>
              <option v-for="(item, index) in curriculum" :key="index" :value="item">{{item.gradename}}</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <div class="selectTop">
            课时名称:
          <div class="selectOption">
            <select style="width:100%;height:40px;" v-model="a2" @change="getNewClass(a2)">
              <option value="0" disabled >请选择</option>
              <option v-if="selectFlade" v-for="(item, index) in classTimesName" :key="index" :value="item">{{item.periodname}}</option>
              <option v-if="selectFlade && !classTimesName.length" disabled>抱歉，没有可调课时</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <div class="selectTop">
            新班级名称:
          <div class="selectOption">
            <select style="width:100%" v-model="a3" @change="getNewTeacher(a3)">
              <option value="0" disabled >请选择</option>
              <option v-if="lessonFade" v-for="(items, index) in newClass" :key="index" :value="items">{{items.gradename}}</option>
              <option v-if="lessonFade && !newClass.length"  disabled>抱歉，没有可调班级</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <div class="selectTop" v-if = "timesFlade">
            老师/时间:
          <div class="selectOption">
            <div >{{classTimesss}}</div>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <div class="buttonOut">
          <div style="float:left;width:50%;">
            <div class="leftButton" @click="okModal">确定</div>
          </div>
          <div style="float:left;width:50%;">
            <div class="rightButton" @click="closeModal">取消</div>
          </div>
        </div>
      </div>
    </mt-popup>


    <middleToast v-if="toastStatus==true" >
      <div class="infoContent">
        <div class="topIcon">
          <img src="~@/assets/images/icon_success.png" height="100%" alt="">
        </div>
        <div class="titleInfo">
          申请提交成功
        </div>
        <div class="infoInfo">
          我们会在12小时内通过系统消息或短信的方式通知您，请注意查收。
        </div>
        <div class="okOut">
          <div class="okShow" @click="closeToast">
            确定
          </div>
        </div>
      </div>
    </middleToast>

    <middleToast v-if="toastType==true" >
      <div class="infoContent">
        <div class="topIcon">
          <img src="~@/assets/images/icon_paymentError.png" height="100%" alt="">
        </div>
        <div class="titleInfo">
          申请调班失败
        </div>
        <div class="infoError">
          原因:
          1.已学习三次课以上不可申请调班；
          2.每个课程有两次调班机会；
          3.若需其他帮助请联系客服：023-6235196
        </div>
        <div class="okOut">
          <div class="okShow" @click="toastType=false;">
            确定
          </div>
        </div>
      </div>
    </middleToast>

    <notInfo v-if="infoStatus">
      抱歉，没有课程
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import middleToast from '@/components/MiddleToast';     //自定义模态弹框
  import myTimeTitle from '../components/MyTimeTitle';      //title
  import myTimeItem from '../components/MyTimeItem';      // item
  import myTimeBegin from '../components/MyTimeBegin';      //待上课
  import notInfo from '@/components/NotInfo';           //没有数据
  import { Navbar,Toast, Popup,TabItem ,TabContainer,Loadmore, TabContainerItem} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyTimetable',
    components:{
      headerBack,
      myTimeTitle,
      myTimeItem,
      myTimeBegin,
      middleToast,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-popup':Popup,
      'mt-loadmore':Loadmore
    },
    created(){
      if(window.localStorage.getItem('selectedTime') && window.localStorage.getItem('selectedTime')==1){
        this.selected = window.localStorage.getItem('selectedTime');
        return;
      }
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      this.studentLessonsList();
    },
    data(){
      return {
        beginInfo:[],       //待上课课表
        endInfo:[],        //已结束课程列表
        selected:'0',       //0  待上课 已结束
        studentid:'',       //登陆学生ID
        toastStatus:false,    //添加后状态默认关闭
        toastType:false,   //调班失败默认不显示
        popupVisible:false, //默认底部弹框关闭
        lecture:[],         //原班级信息
        newClass:[],        //新班级信息
        curriculum: [],     //当前班级课程信息
        selectFlade:false,  //课时默认不显示，显示请选择
        lessonFade:false,   //新班级默认不显示，显示请选择
        timesFlade:false,
        a1:0,    //班级ID
        a2:0,    //课时ID
        a3:0,    //新班级ID
        lessons:0,//当前选择课时信息
        curriculumid:0,//当前选中课程id
        selectValue:0,//当前班级下标值
        schedulingid:0,//当前选中的原班级id
        classTimesName:'',//目标课时名称

        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    methods:{
      /**
       *      调课开始
       */
    // 打开模态框
      clickMe(){
        // 请求原课程和班级信息
        Server.post(Path.getBuyCourseList,{
        },res => {
          let {code, data} = res;
          if (code === 0) {
            if(data.length==0){
              Toast('抱歉，没有可调课程');
            }else{
              this.lecture = data;
              this.popupVisible = true;
            }

          }
        })
      },
      // 关闭模态框
      closeModal(){
        this.popupVisible = false;
      },
      // 确认
      okModal(){
        if(this.a1 == 0 || this.a2== 0 || this.a3== 0 || this.newClass == []){
          Toast('请选择详细的调课信息');
        }else {
          Server.post(Path.submitApplylession,{
            'newlessonsid':this.newlessonsid,
            'oldlessonsid':this.lessons.lessonsid,
            'curriculumid':this.curriculumid
          },res => {
            let {code, data} = res;
            if (code === 0) {
              // 原班级信息
              this.lecture = data;
              // 关闭模态框
              this.popupVisible = false;
              // 开启弹框
              this.toastStatus = true;
            }else{
              this.popupVisible = false;
              this.toastType = true;
            }
          })
        }
      },
      //需要调节的课程
      getClassInfo(item){//点击课程 返回 班级信息
        this.selectFlade = false;
        this.timesFlade = false;
        this.lessonFade = false;
        this.curriculum = item.classinfo;
        this.curriculumid = item.curriculumid;
      },
      //点击班级返回课程
      getClassTimes(item) {
        this.selectFlade = true;
        this.classTimesName = item.lessons;
        this.schedulingid = item.schedulingid;
      },
      //点击课时 调用获取新班级接口
      getNewClass (item) {
        this.lessons = item;
        this.newClassLIist();
      },
      // 获取可选择的课时
      newClassLIist() {
        Server.post(Path.getSelectableLessons,{
          'lessonsid':this.lessons.lessonsid,
          'schedulingid':this.schedulingid,
          'curriculumid':this.curriculumid,
          'periodid':this.lessons.periodid
        },res => {
          let { data, code} = res;
          if (code === 0) {
            this.newClass = res.data;
            this.lessonFade = true;
          }
        })
      },
      //点击课时获取选中时间信息
      getNewTeacher(item) {
        this.newlessonsid = item.newlessonsid;
        this.classTimesss = this.stamp(item.starttime).slice(0,10) + '~' + this.stamp(item.endtime).slice(0,10);
        this.timesFlade = true;
      },
      //解析时间戳
      stamp (times) {
        let bugtimes = new Date( times*1000 );
        return	 bugtimes.getFullYear() + "-" + (bugtimes.getMonth() + 1) + "-" + bugtimes.getDate() + " " + bugtimes.getHours() + ":" + bugtimes.getMinutes() + ":" + bugtimes.getSeconds();
      },


      // 未上课的课程
      studentLessonsList(){
        Server.post(Path.studentLessonsList,{
          'status':this.selected,
          'pagenum':this.pagenum,
          'limit':this.limit
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.limit;
            if (this.pagenum != 1 ) {
              if(this.selected==='0'){
                this.beginInfo = this.beginInfo.concat(data.data);
              }else{
                this.endInfo = this.beginInfo.concat(data.data);
              }
            } else {
              if(this.selected==='0'){
                this.beginInfo = data.data;
              }else{
                this.endInfo = data.data;
              }
            }

          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.studentLessonsList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.studentLessonsList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },


      // 关闭toast
      closeToast(){
        this.toastStatus = false;
      },

      // 查看课表评论
      showReview(lessonID,classID,title,name,coursename){
        this.$router.push({'path':'ReviewInfo',query:{'lessonsid':lessonID,'schedulingid':classID,'teacherName':name,'coursename':coursename,'from':'show'}});
      },
      // 去评论
      gotoComment(info){
        this.$router.push({'path':'ReviewInfo',query:{info:info,from:'add'}});
      }
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedTime', this.selected);
        this.pagenum = 1;
        // 再次请求列表数据
        this.studentLessonsList();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .titleSpan{
    font-size:32px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(51,51,51,1);
  }
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyTimetable{
    /*添加后信息显示*/
      .infoContent{
        position:relative;
        width:580px;
        background:rgba(255,255,255,1);
        border-radius:10px;
        padding:20px 55px 30px 55px;
        z-index:6;
        .topIcon{
          height:64px;
          text-align:center;
          margin-bottom:20px;
        }
        .titleInfo{
          height:48px;
          text-align:center;
          font-size:30px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(51,51,51,1);
        }
        .infoInfo{
          font-size:26px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(51,51,51,1);
          line-height:40px;
          margin-bottom:47px;
        }
        .infoError{
          font-size:20px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(51,51,51,1);
          line-height:26px;
          margin-bottom:47px;
        }
        .okOut{
          height:52px;
          display:flex;
          justify-content: center;
          .okShow{
            width:122px;
            height:52px;
            line-height: 52px;
            text-align: center;
            border:1px solid rgba(63,156,232,1);
            border-radius:20px;
            font-size:30px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(63,156,232,1);
          }
        }
      }
    /*模态框*/
    .bottomPopup{
      box-sizing: border-box;
      padding:95px 40px 77px 39px;
      background:rgba(255,255,255,1);
      border-radius:12px 12px 0px 0px;
      position:relative;
      /*关闭按钮*/
      .offIcon{
        position:absolute;
        top:21px;
        right:25px;
        width:19px;
        height:19px;
        transform: rotate(45deg);
      }
      /*下拉框*/
      .selectTop{
        height:88px;
        line-height: 88px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        clear:both;
        margin-bottom:62px;
        .selectOption{
          box-sizing: border-box;
          padding-left:20px;
          float:right;
          width:450px;
          position:relative;
          height:88px;
          background:rgba(250,250,252,1);
          border:1px solid rgba(217,217,217,1);
          border-radius:4px;
          font-size:28px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(77,77,77,1);
          .rightBottom{
            position:absolute;
            transform: rotate(90deg);
            height:20px;
            width:20px;
            top:32px;
            right: 22px;
          }
        }
      }
      .buttonOut{
        height:86px;
        margin-top:79px;
        .leftButton{
          height:86px;
          line-height: 86px;
          text-align: center;
          width:254px;
          background:rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(255,255,255,1);
          margin:0px auto;
        }
        .rightButton{
          width:254px;
          height:86px;
          line-height: 86px;
          text-align: center;
          border:2px solid rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(57,173,239,1);
          margin:0px auto;
        }
      }
      .subItem{
        height:30px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:33px;
        margin-bottom:63px;
      }


    }
  }
</style>



