<template>
  <!--微信授权页-->
  <div class="Author">
    <!--<headerBack title="微信授权"></headerBack>-->
    <img src="~@/assets/images/sigin.png" height="100%" width="100%" alt="">
  </div>
</template>
<script>

  import headerBack from '@/components/HeaderBack';     //返回组件
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  let that = {};
  export default{
    name:'Author',
    components:{
      headerBack
    },
    data(){
      return{
        mobile:'',  //手机号
      }
    },
    created(){

      that = this;
      if (!this.GetQueryString('code')) {
        // Toast('微信授权中……');
        let currentUrl = encodeURIComponent(window.location.href);
        var url = "https://open.weixin.qq.com/connect/oauth2/authorize";
        url += "?appid=wx4dde403819aa20f9&redirect_uri=" + currentUrl + "&response_type=code&scope=snsapi_base&state=1#wechat_redirect";
        window.location.replace(url);
      } else {
        // 获取code
        this.code = this.GetQueryString('code');
        // 将code码存入缓存
        localStorage.setItem('code', this.code);
        this.getOpenid(this.code);
        // that.$router.replace('/');
        // 成功就返回
        // history.back();
        // Toast('授权成功');
        // 获取授权前的页面
        // let fromUrl = localStorage.getItem('beforeLoginUrl');
        // let nowUrl = JSON.parse(fromUrl);
        // // 跳回原先的支付页面  大写不跳转
        // that.$router.replace({'path':nowUrl.path.toLowerCase(),query:nowUrl.query});
      }
    },
    methods:{

      // 获取openId
      getOpenid(code){
        // 学生ID
        this.mobile = JSON.parse(this.$loca.getItem("appstudentinfo")).mobile;
        Server.post(Path.getOpenid, {
          'code':code,
          'mobile':this.mobile
        }, (res) => {
          let {
            code,
            data
          } = res;
          if (code === 0) {
            that.$router.replace('/');
          } else {
            that.$router.replace('/');
          }
        })
      },
      GetQueryString (name){
        let url = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
        let newUrl = decodeURI(window.location.search).substr(1).match(url);
        if (newUrl != null) {
          return unescape(newUrl[2])
        } else {
          return false
        }
      }
    }
  }
</script>
<style scoped lang="scss">
.Author{
  position:absolute;
  top:0;
  right:0;
  left:0;
  bottom:0;
}
</style>



