<template>
  <!--我的点评详情-->
  <div class="ReviewInfo">
    <headerBack title="课堂分析反馈"></headerBack>
    <div class="infoContent"  v-if="!infoStatus">
      <div class="title">
        <template >
          {{reviewInfo.periodname}}
        </template>
        <template v-if="from=='show'">
          {{coursename}}
        </template>
      </div>
      <div class="teacher" v-if="from!='add'">
        <template v-if="from=='list'">
          老师：{{reviewInfo.nickname}}
        </template>
        <template v-if="from=='show'">
          老师：{{teacherName}}
        </template>
      </div>

      <div style="margin:0px auto;">
        <!--评论不显示-->
        <div v-if="from!='add'"  class="borderOut">
          <div class="start">
            <div class="leftTitle">
              评分：
            </div>
            <div v-for="v in 5" :class="{'spanStar':v<=reviewInfo.score,'spanNone':v>reviewInfo.score}" ></div>
          </div>
          <div class="teacherInfo">
            老师点评：
          </div>
          <div contenteditable='true' class="borderBox">
            {{reviewInfo.comment}}
          </div>
        </div>
        <!--评论显示-->
        <div v-if="from=='add'" class="textOut">
          <div class="startAdd">
            <div class="leftAdd">
              评分：
            </div>
            <div v-for="v in 5" :class="{'spanStarAdd':v<=starIndex,'spanNoneAdd':v>starIndex}"  @click="starIndex=v">
            </div>
          </div>
          <div class="teacherInfoAdd">
            评价内容：
          </div>
          <textarea  name="" id="" class="textDiv"  v-model="content" >
          </textarea>
          <div class="saveButton"  @click="gotoComment">
            提交点评
          </div>
        </div>
      </div>

    </div>

    <notInfo v-if="infoStatus">
      抱歉，点评数据
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import notInfo from '@/components/NotInfo';           //没有数据
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  export default{
    name:'ReviewInfo',
    components:{
      headerBack,
      notInfo
    },
    data(){
      return {
        schedulingid:'',
        lessonsid:'',
        reviewInfo:{},
        infoStatus:false,  //无数据显示
        from:'',   //判断过来的页面
        addInfo:{}, //评论过来的数据
        studentid:'',   //学生ID
        nickname:'',   //学生昵称
        starIndex:0,  //默认star没有选中
        content:'',   //评价的信息
        coursename:'',  //课程名称
      }
    },
    created(){
      this.from = this.$route.query.from;
      // 课堂反馈过来的数据
      if(this.from=='list'){
        this.myCommentMsg(this.$route.query.id);
      }else if(this.from=='show'){
        // 课表过来查看点评
        this.schedulingid = this.$route.query.schedulingid;
        this.lessonsid = this.$route.query.lessonsid;
        this.teacherName = this.$route.query.teacherName;
        this.coursename = this.$route.query.coursename;
        this.getFeedback();
      }else if(this.from = 'add'){
        // 学生ID
        this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
        this.addInfo = this.$route.query.info;
        // 请求个人信息
        this.getStudentInfo();
      }

    },
    methods:{
      // 提交点评
      gotoComment(){
        Server.post(Path.gotoComment,{
          'nickname':this.nickname,
          'curriculumid':this.addInfo.curriculumid,
          'classtype':2,
          'studentid':this.studentid,
          'teacherid':this.addInfo.teacherid,
          'score':this.starIndex,
          'schedulingid':this.addInfo.schedulingid,
          'content':this.content,
          'lessonsid':this.addInfo.lessonsid,
          'toteachid':this.addInfo.toteachid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$router.go(-1);
            Toast('点评成功');
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 获取个人信息
      getStudentInfo(){
        Server.post(Path.getStudentInfo,{
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            if(data){
              this.nickname = data.nickname;
            }
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      getFeedback(){
        Server.post(Path.getFeedback,{
          'lessonsid':this.lessonsid,
          'schedulingid':this.schedulingid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            if(data==''){
              Toast({
                message: '抱歉,没有点评',
                iconClass: 'icon icon-error',
                duration: '1800'
              });
              this.infoStatus = true;
            }else{
              this.reviewInfo = data;
              console.log(this.reviewInfo);
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 获取课堂反馈点评详情
      myCommentMsg(id){
        Server.post(Path.myCommentMsg,{
          'id':id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.reviewInfo = data;
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .spanStar{
    width:21px;
    height:19px;
    @include bg-image("~@/assets/images/icon_starBg");
    float:left;
    margin-right:5px;
    margin-top:2.5px;
  }

  .spanNone{
    width:21px;
    height:19px;
    @include bg-image("~@/assets/images/icon_starBorder");
    float:left;
    margin-right:5px;
    margin-top:2.5px;
  }
  /*添加评论*/
  .startAdd{
    height:45px;
    margin-bottom:51px;
    .leftAdd{
      height:45px;
      line-height: 45px;
      font-size:34px;
      float:left;
      font-weight:500;
      color:rgba(77,77,77,1);
    }
    .spanStarAdd{
      width:41px;
      height:41px;
      @include bg-image("~@/assets/images/icon_starBg");
      float:left;
      margin-right:5px;
    }

    .spanNoneAdd{
      width:41px;
      height:41px;
      @include bg-image("~@/assets/images/icon_starBorder");
      float:left;
      margin-right:5px;
    }

  }
  .teacherInfoAdd{
    font-size:34px;
    font-weight:500;
    color:rgba(77,77,77,1);
    margin-bottom:43px;
  }
  .saveButton{
    width:670px;
    height:100px;
    line-height:100px;
    text-align: center;
    background:rgba(47,172,250,1);
    border-radius:6px;
    font-size:34px;
    font-weight:400;
    color:rgba(255,255,255,1);
  }

  .textOut{
    width:670px;
    margin:0px auto;
    box-sizing: border-box;
  }
  .textDiv{
    border:2px solid rgba(217,217,217,1);
    height:300px;
    box-sizing: border-box;
    width:670px;
    padding:15px;
    margin-bottom:70px;
  }
  .ReviewInfo{
    position:absolute;
    left:0;
    top:0;
    bottom:0;
    right:0;
    background:rgba(255,255,255,1);
    .infoContent{
      padding:21px 40px ;
      .title{
        height:52px;
        font-size:32px;
        text-align: center;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
      }
      .teacher{
        height:43px;
        font-size:24px;
        text-align:center;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
      }
      .start{
        height:24px;
        margin-bottom:20px;
        .leftTitle{
          height:24px;
          font-size:24px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          float:left;
          color:rgba(51,51,51,1);
          line-height:24px;
          margin-right:10px;
        }

      }
      .teacherInfo{
        height:34px;
        font-size:24px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:24px;
      }
      .borderOut{
        width:670px;
        margin:0px auto;
        box-sizing: border-box;
      }
      .borderBox{
        box-sizing: border-box;
        padding:10px 7px;
        width:670px;
        font-size:20px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
        min-height:380px;
        line-height:34px;
        border:1px solid rgba(217,217,217,1);
      }
    }
  }
</style>



