
<template>
  <div  class="studentClass">
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="(info,i) in data" :key="i">
        <div class="StudentClass">
          <div class="classItem"  v-for="(item,key) in info"  @click="toPage(item.category_id)" :key="key">
            <div class="imgOut">
              <div class="imgShow" >
                <img v-if="item.imgs" :src="item.imgs" width="100%"  height="100%" alt="">
                <img v-if="!item.imgs" src="~@/assets/images/student.png" width="100%"  height="100%" alt="">
              </div>
              <div class="spanName">
                <span class="spanShow">
                  {{item.categoryname}}
                </span>
              </div>
            </div>
          </div>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import 'swiper/dist/css/swiper.css'
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default {
    name:'StudentClass',
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {
        swiperOption: {
          autoplayDisableOnInteraction: false,
          // autoplay: 3000,
          initialSlide:0,
          // loop: true,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          paginationType : 'progress',
          notNextTick: true,
          direction: 'horizontal',
          pagination: '.swiper-pagination',
          // slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 30,
          onSlideChangeEnd: swiper => {
            this.page = swiper.realIndex + 1;
            this.index = swiper.realIndex;
          }
        },
        data: [],
      }
    },
    created() {
      this.getThreeCategroyList();
    },
    methods: {
      getThreeCategroyList() {
        Server.post(Path.getThreeCategroyList, {
        }, (res) => {
          if (res.code === 0) {
            // 绑定数据
            this.data = res.data;
          }
        });
      },
      // 前往课程三级分类
      toPage(id){
        this.$router.push({path:'CourseList',query:{id:id}});
      }
    },
    //定义这个sweiper对象
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper;
      },
    }
  }
</script>

<style scoped lang="scss">

  .swiper-container {
    width: 100%;
    height: 223px;
    overflow: hidden;
    margin-bottom:12px;
    background:rgba(255,255,255,1);
    .swiper-wrapper {
      width: 100%;
      height: 223px;
      .swiper-slide {
        width: 100%;
        height: 100%;
        .StudentClass{
          font-size:24px;
          color:#525252;
          padding:36px 35px 0px 35px;
          height:223px;
          box-sizing: border-box;
          margin-bottom:20px;
          .classItem{
            display:flex;
            width:20%;
            float:left;
            justify-content: center;
            .imgOut{
              width:96px;
              .imgShow{
                height:96px;
                width:96px;
                border-radius: 50%;
                overflow: hidden;
              }
              .spanName{
                padding-top:26px;
                width:96px;
                height:84px;
                text-align:center;
              }
            }

          }
        }
      }
    }
  }

</style>




