<template>
  <!--精彩瞬间详情-->
  <div>
    <div class="EndEvaluate">
      <headerBack title="录播课" typeStatus="icon" @clickMe="clickMe"></headerBack>
        <!--视频-->
        <videoControl :src="'http://'+contentInfo.cosurl"></videoControl>
        <div class="infoContent">

        </div>
    </div>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import videoControl from '../components/VideoControl';      // video 控制
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:"MarveInfo",
    components:{
      headerBack,
      videoControl,
      verticalScroll,
    },
    data(){
      return {
        pagenum:1,       //默认第一页
        studentid:'',   //学生ID
        lessonsid:"",   //课时ID
        courseid:'',    //课程ID,
        contentInfo:{},
        commentInfo:[], //评论数据
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      this.courseid = this.$route.query.courseid;
      this.lessonsid = this.$route.query.lessonsid;
      // 请求我的视频
      this.watchPlayback();
      // 请求评论
      this.getRecordComment();
    },
    methods:{
      // 请求我的视频
      watchPlayback(){
        Server.post(Path.watchPlayback,{
          'lessonsid':this.lessonsid,
          'courseid':this.courseid,
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            this.contentInfo = data;
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 请求我的评论
      getRecordComment(){
        console.log(this.lessonsid);
        console.log(this.pagenum);
        Server.post(Path.getRecordComment,{
          'lessonsid':this.lessonsid,
          'pagenum':this.pagenum,
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      clickMe(){
        this.popupVisible = true;
      }
    }
  }
</script>
<style lang="scss" scoped>
  .EndEvaluate{
  }
  .infoContent{
    position:absolute;
    top:495px;
    left:0;
    right:0;
    bottom:0;
    z-index:30;
    border-radius:12px 12px 0px 0px;
    background:rgba(255,255,255,1);
  }
</style>



