<template>
  <div>
    <div class="MyTimeBegin" v-for="item in data">
      <div v-if="item.buttonstatus===0" class="courseType notStart">
        未开始
      </div>
      <!--<div v-else-if="item.buttonstatus===1" class="courseType nowStart">-->
        <!--进教室-->
      <!--</div>-->
      <!--<div v-else class="courseTypeEnd ">-->
        <!--已结束-->
      <!--</div>-->
      <div class="title">
        <div class="leftTitle text-ellipsis">
          {{item.periodname}}
        </div>
        <div class="rightTitle" v-if="type=='end' && item.buttonstatus==2"    @click="gotoComment(item.curriculumid,item.teacherid,item.schedulingid,item.lessonsid,item.toteachid)">
          评价
        </div>
        <div class="rightTitle" v-if="type=='end' && item.buttonstatus==3" >
          已评价
        </div>
      </div>
      <div class="content">
        <div class="imgShow">
          <img :src="item.imageurl" width="100%" height="100%" alt="">
        </div>
        <div class="infoInfo">
          <div class="subTitle">
            {{item.gradename}}
          </div>
          <div class="teacher text-ellipsis">
            {{item.teachername}}
          </div>
          <div class="date  text-ellipsis"  v-if="type=='start'">
            时间：{{item.starttime}}
          </div>
          <div class="date"  v-if="type=='end'">
            <div class="blueBlock" @click="showReview(item.lessonsid,item.schedulingid,item.periodname,item.teachername,item.periodname)">
              查看点评
            </div>
            <div class="endTime ">
              时间：{{item.starttime}}
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:"MyTimeBegin",
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      },
      type:{
        type:String,
        default(){
          return 'start'
        }
      }
    },
    methods:{
      // 去点评
      gotoComment(curriculumid,teacherid,schedulingid,lessonsid,toteachid){
        let info = {
          'curriculumid':curriculumid,
          'teacherid':teacherid,
          'schedulingid':schedulingid,
          'lessonsid':lessonsid,
          'toteachid':toteachid
        }
        this.$emit('gotoComment',info);
      },
      // 查看点评
      showReview(lessonID,classID,title,name,coursename){
        this.$emit('showReview',lessonID,classID,title,name,coursename);
      }
    }

  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .MyTimeBegin{
    height:258px;
    box-sizing: border-box;
    background:rgba(255,255,255,1);
    box-shadow:2px 0px 6px rgba(147,147,147,0.1);
    padding:20px 24px 30px 24px;
    position:relative;
    .courseType{
      height:48px;
      line-height:48px;
      text-align: center;
      width:104px;
      float:right;
      position:absolute;
      top:0px;
      right:24px;
      font-size:24px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(255,255,255,1);
    }
    .courseTypeEnd{
      height:48px;
      line-height:48px;
      text-align: center;
      width:104px;
      float:right;
      position:absolute;
      top:0px;
      right:24px;
      font-size:24px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(128,128,128,1);
    }
    .notStart{
      @include bg-image("~@/assets/images/icon_notStart");
    }
    .nowStart{
      @include bg-image("~@/assets/images/icon_nowStart");
    }
    .title{
      height:48px;
      .leftTitle{
        width: calc(100% - 250px);
        height:48px;
        line-height:48px;
        float:left;
        font-size:30px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(61,61,61,1);
      }
      .rightTitle{
        height:48px;
        line-height:48px;
        float:right;
        font-size:30px;
        font-weight:500;
        color:rgba(255,152,106,1);
      }
    }
    .content{
      height:160px;
      .imgShow{
        width:280px;
        height:160px;
        border-radius:8px;
        overflow:hidden;
        float:left;
        margin-right:21px;
      }
      .infoInfo{
        height:160px;
        width: calc(100% - 310px);
        float:left;
        .subTitle{
          height:44px;
          font-size:26px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(77,77,77,1);
        }
        .teacher{
          height:77px;
          font-size:26px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(107,107,107,1);
        }
        .date{
          text-align: right;
          font-size:20px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          position:relative;
          color:rgba(102,102,102,1);
          .blueBlock{
            position:absolute;
            right:0px;
            top:-13px;
            width:120px;
            height:50px;
            line-height:50px;
            text-align: center;
            background:rgba(57,173,239,1);
            border-radius:4px;
            font-size:20px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(255,255,255,1);
          }
          .endTime{
            width:250px;
            text-align: left;
            float:left;
            font-size:20px;
            font-family:Adobe Heiti Std R;
            font-weight:normal;
            color:rgba(102,102,102,1);
          }
        }
      }
    }
  }
</style>



