<template>
  <div>
      <div class="MyCourseEnd" v-for="item in data" @click="toPage(item.schedulingid,item.curriculumid,item.learned)">
        <div class="imgShow">
          <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
        </div>
        <div class="infoContent">
          <div class="title">
            {{item.coursename}}
          </div>
          <div class="teacher">
            {{item.teachername}}
            <!--<div class="blueBlock"  @click.stop="toTeacher(item.teacherid)">-->
              <!--续班-->
            <!--</div>-->
          </div>
        </div>
      </div>
  </div>
</template>
<script>
  export default{
    name:'MyCourseEnd',
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    methods:{
      toPage(schedulingid,courseid,learned){
        this.$emit('MyEndInfo',schedulingid,courseid,learned);
      },
      // 前往老师详情
      toTeacher(teacherId){
        this.$router.push({path:'/TeacherInfo',query:{teacherId:teacherId}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  .MyCourseEnd{
    height:200px;
    background:rgba(255,255,255,1);
    padding:20px 56px 20px 25px;
    border-radius:10px;
    .imgShow{
      width:280px;
      float:left;
      height:160px;
      border-radius:8px;
      overflow: hidden;
      margin-right:18px;
    }
    .infoContent{
      height:160px;
      width: calc(100% - 379px);
      float:left;
      .title{
        height:101px;
        font-size:30px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(77,77,77,1);
        line-height:34px;
      }
      .teacher{
        height:50px;
        line-height: 50px;
        font-size:26px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(107,107,107,1);
        .blueBlock{
          width:100px;
          height:50px;
          line-height:50px;
          text-align: center;
          float:right;
          background:rgba(57,173,239,1);
          border-radius:4px;
          font-size:26px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
        }
      }
    }
  }
</style>



