<template>
  <!--全部课程-->
  <div class="AllCourses">
    <!--返回导航-->
    <headerBack title="全部课程"></headerBack>
    <!--头部tab-->
    <allCourseTab @selectType="selectType" :category_id="categoryID"  @twoPage="twoPage"></allCourseTab>
    <div class="infoInfo"  v-if="topStatus" @click="imgShowStatus = true;">
      {{describe}}
      <div class="topShadow" ></div>
    </div>

    <div class="imgShow" v-if='imgShowStatus' @click="imgShowStatus=false">
      <img :src="imgShow" height="100%"  @error="_imageError" width="100%" alt="">
    </div>

    <div :class="{'wrapper':topStatus,'wrapperNone':!topStatus}" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <!--列表显示-->
        <allCourseItem :data="dataInfo"  ></allCourseItem>
      </mt-loadmore>
    </div>

    <!--<mt-popup v-model="popupStatus" position="right"  class="nowPoup" >-->
    <mt-popup v-model="popupStatus" position="right"  style="width:100%;height:100%;">
      <!--二级swiper-->
      <!--<swiper :options="swiperOption" ref="mySwiper" class="nowPoup">-->
        <!--<swiper-slide v-for="(info,i) in twoInfo" :key="i" :class="{'swiperNow':swiperKey!==i,'swiperEnd':swiperKey===i}"  @click.native="changeID(info.category_id,i)">-->
          <!--{{info.categoryname}}-->
        <!--</swiper-slide>-->
        <!--<div v-if="nowStatus" class="swiperNow">-->
          <!--抱歉，没有分类数据-->
        <!--</div>-->
      <!--</swiper>-->
      <lessonClass @closeModal="closeModal" @changeID="changeID"  :category_id="categoryID"  :categoryname="categoryname"></lessonClass>
    </mt-popup>

    <notInfo v-if="infoStatus">
      抱歉，没有指定分类数据
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import lessonClass from '@/components/LessonClass';     //返回组件
  import allCourseTab from '../components/AllCourseTab';        //头部tab
  import allCourseItem from '../components/AllCourseItem';        //老师名片
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import notInfo from '@/components/NotInfo';     //返回组件
  import { Loadmore,Popup,Toast } from 'mint-ui';
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import 'swiper/dist/css/swiper.css'
  export default{
    name:'AllCourses',
    components:{
      headerBack,
      allCourseTab,
      allCourseItem,
      notInfo,
      'mt-loadmore':Loadmore,
      'mt-popup':Popup,
      swiper,
      swiperSlide,
      lessonClass
    },
    data(){
      return{
        imgShowStatus:false,  //默认不显示
        swiperOption: {
          autoplayDisableOnInteraction: false,
          initialSlide: 0,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          // loop: false,
          // type: 'bullets',
          notNextTick: true,
          direction: 'vertical',
          // pagination: '.swiper-pagination',
          slidesPerView :'auto',
          // centeredSlides: true,
          paginationClickable: true,
          // centeredSlides:false,
          // spaceBetween: 30,
          onTransitionEnd: function(swiper){
            if(swiper.progress==1){
              swiper.activeIndex=swiper.slides.length-1;
            }
          }
        },
        topStatus:false,  //默认显示内容不显示
        category_id:'',  //制定ID
        dataInfo:[],   //数组集合
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
        popupStatus:false,  //默认二级页面关闭
        twoInfo:[],         //二级索引数据
        nowStatus:false,    //没有二级数据显示
        categoryID:'',
        swiperKey:'',
        describe:"",  //类别概述
        categoryname:'',  //一级的类名
        imgShow:'',   //图片
      }
    },
    created(){
      // 记录'全部'跳转过来得到数据
      this.category_id = this.$route.query.category_id;
      // 记录一级头的显示
      this.categoryID = this.category_id;
      // 如有页面传参才请求
      if(this.category_id){
        // 请求数据
        this.getList();
      }
    },

    methods:{
      // 关闭莫泰狂
      closeModal(){
        this.popupStatus = false;
      },
      // 二级类选择
      changeID(id){
        this.dataInfo = [];
        // 请求的id
        this.category_id = id;
        this.getList();
      },
      // 打开二级页面
      twoPage(){
        this.imgShowStatus = false;
        this.nowStatus = false;
        this.infoStatus = false;
        this.twoInfo = [];
        this.popupStatus = true;
        Server.post(Path.getTopCategoryChild, {
          'categoryid':this.categoryID,
        }, (res) => {
          if (res.code === 0) {
            if(res.data.length==0){
              this.nowStatus = true;
              return;
            }
            this.twoInfo = res.data;
          }
        });
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.getList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.getList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 选中类别
      selectType(id,content,name,type,url){
        if(type){
          if(!this.topStatus){
            this.topStatus = true;
          }
        }

        this.imgShow = url;

        // 一级类名
        this.categoryname = name;
        // 课程概述
        this.describe = content;
        // 一级选中时二级默认没有选中
        this.swiperKey = '';
        this.dataInfo = [];
        // 记录ID
        this.category_id = id;
        this.categoryID = this.category_id;
        // 第一页显示
        this.pagenum = 1;
        this.getList();
      },
      getList(){
        if(this.infoStatus){
          this.infoStatus = false;
        }
        Server.post(Path.getFilterCourserList, {
          'category_id':this.category_id,
          'is_free':0,
          'pagenum':this.pagenum
        }, (res) => {
          if (res.code === 0) {
            if(res.data.schedulist.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              //页码不为一，追加
              this.dataInfo = this.dataInfo.concat(res.data.schedulist);
            } else {
              //否则从第一页开始，覆盖列表数据
              this.dataInfo = res.data.schedulist;
            }
          }
        });
      }
    },
    //定义这个sweiper对象
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper;
      },
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  .imgShow{
    position:absolute;
    top:172px;
    left:0;
    right:0;
    bottom:0;
    z-index:9999;
    background:white;
  }
  .swiperNow{
    height:80px!important;
    line-height:80px;
    text-align: center;
    padding:5px 5px;
    border-bottom:1px solid darkgray;
  }
  .swiperEnd{
    height:80px!important;
    line-height:80px;
    text-align: center;
    padding:5px 5px;
    border-bottom:1px solid darkgray;
    background:gray;
    color:white;
  }
  .nowPoup{
    /*right:5px;*/
    width:250px;
    /*height:100px;*/
    max-height: 800px;
    min-height:400px;
  }
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 350px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .wrapperNone{
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .AllCourses{
    @extend .main-child;
  }
  .infoInfo{
    padding:14px 24px 18px 24px;
    font-size:22px;
    height:160px;
    overflow-y:scroll;
    box-sizing: border-box;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(92,92,92,1);
    background:rgba(255,255,255,1);
    text-indent: 2em;
    position:relative;
    /*box-shadow:0px 2px 5px 0px rgba(83,89,93,0.1);*/
    .topShadow{
      position:fixed;
      height:60px;
      top:276px;
      left:0px;
      width:100%;
      background: linear-gradient(rgba(255,255,255,0.2), rgba(255,255,255,1));
    }
  }
</style>



