<template>
  <!--签到成功-->
  <div class="SignInSuccess">
    <headerBack title="每日签到" typeStatus="icon"  @clickMe="clickMe"></headerBack>
    <div class="infoContent">

      <!--<bScroll-->
        <!--class="wrapper"-->
        <!--ref="scroll"-->
      <!--&gt;-->
        <div class="innerDiv">
          <div class="month">
            <div class="monthDiv">
              {{day}}
            </div>
            <div class="day">
              {{month}}
            </div>
          </div>
          <div class="total">
            总签到数：{{homeInfo.signdata.total}} 天
          </div>
          <div class="consecutive">
            连续签到：{{homeInfo.signdata.consecutive}} 天
          </div>
          <div class="signType"  v-if="homeInfo.knowledge">
            【{{homeInfo.knowledge.typename}}】
          </div>
          <div class="siginInfo" v-if="homeInfo.knowledge">
            {{homeInfo.knowledge.content}}
          </div>
          <div class="answer"  v-if="homeInfo.knowledge">
            谜语答案：{{homeInfo.knowledge.answer}}
          </div>
          <div class="logo">
            <img :src="'http://'+homeInfo.qrcode" height="100%" width="100%" alt="">
          </div>
        </div>
      <!--</bScroll>-->

    </div>

    <div  class="imgShows">
      <img :src="imgUrl" width="100%"  height="100%"  alt="">
    </div>
    <div class="floatFloat"></div>

    <mt-popup
      v-model="popupVisible"
      position="bottom"  style="width:100%">
      <div class="bottomPopup">
        <div style="margin-right:-70px">
          <ul>
            <li  @click="toPage('/SelectBackground')">
              <div class="imgOut">
                <div class="imgShow">
                </div>
                <div class="infoContents">
                  更换背景
                </div>
              </div>
            </li>
            <li @click="toPage('/HistorySignIn')">
              <div class="imgOut">
                <div class="imgShow">
                </div>
                <div class="infoContents">
                  历史签到
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </mt-popup>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import bScroll from '@/components/Lib/BScroll';           //bScroll
  import { Popup,Toast } from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import imgUrl from '@/assets/images/sigin.png';
  export default{
    name:'SignInSuccess',
    components:{
      headerBack,
      'mt-popup':Popup,
      bScroll
    },
    data(){
      return{
        signAgain:false,
        month:'',
        day:'',
        popupVisible:false,
        imgUrl,
        homeInfo:{
          signdata:{}
        },  //首页内容
      }
    },
    created(){
      let nowData = new Date();
      this.month = nowData.getMonth()+1;
      this.day = nowData.getDate();
      // 获取签到首页内容
      this.signinHome();
    },
    methods:{
      // 签到
      signin(){
        Server.post(Path.signin,{
          'knowledgeid':this.homeInfo.knowledge?this.homeInfo.knowledge.id:''
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            Toast('签到成功');
            this.signinHome();
          }else{
            // Toast({
            //   message: res.info,
            //   iconClass: 'icon icon-error',
            //   duration: '1800'
            // });
          }
        })
      },
      toPage(route){
        this.$router.push({path:route});
      },
      clickMe(){
        this.popupVisible = true;
      },
      // 获取签到首页内容
      signinHome(){
        Server.post(Path.signinHome,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.homeInfo = data;
            if(data.signinimage){
              this.imgUrl = 'http://'+data.signinimage;
            }
            if(!this.signAgain){
              this.signAgain = true;
                this.signin();  //进行签到
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  li:nth-of-type(1) .imgShow{
    @include bg-image("../assets/icon_changeBg");
  }
  li:nth-of-type(2) .imgShow{
    @include bg-image("../assets/icon_signIn");
  }
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    bottom: 0px;
    background:red;
    /*background:rgba(255,255,255,1);*/
  }
  .infoContent{
    overflow: scroll;
    width:100%;
    /*margin-top:40px;*/
    padding:0 36px  20px;
    box-sizing: border-box;
    position:absolute;
    top:92px;
    left:0px;
    right:0px;
    bottom:0px;
    /*display:flex;*/
    /*justify-content: center;*/
    /*align-items: center;*/
    /*border:1px solid red;*/
    .innerDiv{
      margin-top:50px;
      width:100%;
      box-sizing: border-box;
      border:3px solid rgba(255,255,255,1);
      box-shadow:0px 0px 0px rgba(26,54,75,0.1);
      padding:40px 35px 50px;
      position:relative;
      .monthDiv::after{
        content:'';
        position:absolute;
        left:70px;
        bottom:-40px;
        height:90px;
        width:0px;
        border-left:3px solid white;
        transform: rotate(45deg);
      }
      .month{
        margin-bottom:95px;
        position:relative;
        .monthDiv{
          font-size:70px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(255,255,255,1);
          position:relative;
        }
        .day{
          padding-left:30px;
          position:relative;
          left:50px;
          font-size:48px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(255,255,255,1);
        }
      }
      .total{
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
        margin-bottom:30px;
      }
      .consecutive{
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
        margin-bottom:300px;
      }
      .signType{
        font-size:34px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(255,255,255,1);
        margin-bottom:40px;
      }
      .siginInfo{
        font-size:32px;
        line-height: 50px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
        margin-bottom:90px;
      }
      .answer{
        font-size:24px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
      .logo{
        position:absolute;
        right:35px;
        bottom:40px;
        height:108px;
        width:108px;
      }
    }
  }


  .imgShows{
    position:absolute;
    top:92px;
    left:0;
    right:0;
    bottom:0;
    background:rgba(444,444,444,0.7);
    z-index: -5;
  }
  .floatFloat{
    position:absolute;
    top:92px;
    left:0;
    right:0;
    bottom:0;
    background:rgba(0,0,0,0.5);
    z-index: -2;
  }


  .bottomPopup{
    padding:32px 70px 0px 70px;
    box-sizing: border-box;
    height:450px;
    background:rgba(242,242,242,1);
    li{
      height:180px;
      width:100px;
      float:left;
      margin-right:70px;
      margin-bottom:30px;
      .imgOut{
        width:100px;
        height:100px;
        display:flex;
        align-items: center;
        justify-content: center;
        background:rgba(255,255,255,1);
        border-radius:10px;
        position:relative;
        .imgShow{
          width:79px;
          height:64px;
        }
        .infoContents{
          position:absolute;
          width:200px;
          left:-50px;
          bottom:-29px;
          text-align: center;
          height:20px;
          line-height:20px;
          font-size:20px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(61,61,61,1);
        }
      }

    }
  }
</style>



