<template>
  <!--注册选择兴趣-->
  <div class="SelectInterest">
    <headerBack title="兴趣推荐"></headerBack>
    <verticalScroll>
      <div class="infoContent">
        <router-link to="/">
          <div class="rightGray" >
            跳过
          </div>
        </router-link>
        <div class="title">
          {{listInfo.fathername}}
        </div>
        <div class="listItem">
          <ul>
            <li  :class="{'liDefault':item.flag}" v-for="(item,key) in listInfo.childarr"  @click="selectTag(item.childtagid,key)">
              <div class="imgOut">
                <div>
                  <div class="imgShow">
                  </div>
                  <div style="text-align: center;">
                    {{item.childname}}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>

        <!--选好了-->
        <div class="selectOk"  @click="toPage">
          选好了
        </div>
      </div>
    </verticalScroll>

  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  export default {  //getUserFavorCategory
    name: 'SelectInterest',
    components:{
      headerBack,
      verticalScroll
    },
    data(){
      return {
        listInfo:[],
        selectArr:[]
      }
    },
    created(){
      // 获取学生标签列表
      this.getUserTag();
    },
    methods:{
      // 选择标签
      selectTag(id,key){
        for(let i in this.listInfo.childarr){
          if(key==i){
            this.listInfo.childarr[i].flag = !this.listInfo.childarr[i].flag;
          }
        }
      },
      // 获取学生标签列表
      getUserTag(){
        Server.post(Path.getUserTag,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.$router.push('/');
            }else{
              this.listInfo = data;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      toPage(){
        this.selectArr = [];
        for(let item of this.listInfo.childarr){
          if(item.flag==true){
            this.selectArr.push(item.childtagid);
          }
        }
        if(this.selectArr.length>0){
          let nowString = this.selectArr.join(',');
          Server.post(Path.addUserTag,{
            'tagid':this.listInfo.tagid,'childtags':nowString
          },(res)=>{
            console.log(res);
            let { code, data } = res;
            if(code===0){
              this.$router.push('/');
            }else{
              Toast({
                message: res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }
          })
        }else{
          Toast('请选择推荐类型');
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .SelectInterest{
    .infoContent{
      background:rgba(255,255,255,1);
      padding:113px 100px 158px 100px;
      position:relative;
      .rightGray{
        width:100px;
        height:50px;
        line-height:50px;
        text-align:center;
        position:absolute;
        right:43px;
        top:35px;
        background:rgba(107,107,107,0.3);
        border-radius:25px;
        font-size:26px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
      .title{
        height:96px;
        text-align: center;
        font-size:38px;
        font-family:PingFang-SC-Bold;
        font-weight:bold;
        color:rgba(77,77,77,1);
      }
      .listItem{
        font-size:30px;
        margin-right:-30px;
        color:orange;
        .imgShow{
          height:120px;
          line-height:120px;
          text-align: center;
          width:120px;
          margin:0px auto;
        }
        li:nth-child(1) .imgShow{
          @include bg-image("../assets/icon_ability");
        }
        li:nth-child(2) .imgShow{
          @include bg-image("../assets/icon_knowledge");
        }
        li:nth-child(3) .imgShow{
          @include bg-image("../assets/icon_writing");
        }
        li:nth-child(4) .imgShow{
          @include bg-image("../assets/icon_read");
        }
        li:nth-child(5) .imgShow{
          @include bg-image("../assets/icon_increase");
        }
        li:nth-child(6) .imgShow{
          @include bg-image("../assets/icon_other");
        }
        li{
          width:260px;
          height:240px;
          box-sizing: border-box;
          background:rgba(47,172,250,0.3);
          margin-bottom:30px;
          margin-right:30px;
          float:left;
          border-radius:10px;
          .imgOut{
            height:186px;
            display:flex;
            justify-content: center;
            align-items: center;
          }
        }
        .liDefault{
          border:5px solid orange;
        }
      }

      .selectOk{
        margin-top:95px;
        margin:0px auto;
        width:472px;
        height:92px;
        line-height: 92px;
        text-align: center;
        background:rgba(47,172,250,1);
        opacity:0.7;
        border-radius:46px;
        font-size:32px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(255,255,255,1);
      }

    }
  }
</style>



