<template>
    <div class="MineCard" >
      <div class="imgOut">
        <div class="imgShow" @click="cardInfo">
          <img :src="cardObj.imageurl"  @error="_imageError" width="100%" height="100%" alt="">
        </div>
        <div class="cardInfo" @click="cardInfo">
          <div  class="name">
            <div class="nameName">
              {{cardObj.nickname}}
            </div>
            <div class="sexIcon"  :class="{'sexMan':cardObj.sex==1,'sexWoman':cardObj.sex==2}">
              <span v-if='cardObj.sex==0'>保密</span>
            </div>
            <div class="signIn" @click.stop="toPage">
              签到
            </div>
          </div>
          <div class="address">
            <div class="addressInfo text-ellipsis">
              <span style="padding-right:3px;">{{getCity(cardObj.country)}}</span>
              <span style="padding-right:3px;">{{getCity(cardObj.province)}}</span>
              <span >{{getCity(cardObj.city)}}</span>
            </div>
            <div class="line"></div>
            <div class="ageNum">
              {{nowAge(cardObj.year)}}岁
            </div>
          </div>
          <div class="infoInfo" >
            <div class="leftTitle text-ellipsis">
              {{cardObj.profile}}
            </div>
            <div class="rightIcon"></div>
          </div>
        </div>
      </div>
    </div>
</template>
<script>
  import CityJSON from '@/assets/city/city.json';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MineCard',
    data(){
      return {
        studentid:'',  //学生ID
        cardObj:{
          sex:1,      //男生
          nickname:'',
          year:2000,
          profile:'',
          country:7,
          province:'',
          city:''
        },
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 请求个人信息
      this.getStudentInfo();
    },
    methods:{
      // 现在的年龄
      nowAge(year){
        let nowDate = new Date();
        let nowYear = nowDate.getFullYear();
        return nowYear-year;
      },
      //城市转换
      getCity: function(val) {
        if(val == 0 || val == null){
          return
        }else{
          CityJSON.map((el,index) => {
            if(el.id == val){
              val =el.name
            }
          })
          return val;
        }
      },
      // 获取个人信息
      getStudentInfo(){
        Server.post(Path.getStudentInfo,{
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            this.cardObj = data;
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      cardInfo(){
        this.$router.push({'path':'/DataEditing',query:{'from':'mineCard'}});
      },

      toPage(){
        this.$router.push({'path':'/SignInSuccess'})
      }
    }

  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .MineCard{
    box-sizing: border-box;
    height:276px;
    background:rgba(255,255,255,1);
    margin-bottom:20px;
    padding:56px 0px 60px 40px;
    .imgOut{
      height:160px;
      .imgShow{
        width:160px;
        height:160px;
        float:left;
        overflow:hidden;
        border-radius:50%;
        margin-right:38px;
      }
      .cardInfo{
        width: calc(100% - 198px);
        height:160px;
        box-sizing: border-box;
        padding:8px 0px;
        float:left;
        .name{
          height:50px;
          .nameName{
            float:left;
            margin-right:32px;
            font-size:36px;
            font-family:PingFang-SC-Bold;
            font-weight:bold;
            color:rgba(51,51,51,1);
          }
          .sexIcon{
            float:left;
            height:36px;
            line-height:36px;
            font-size:34px;
            color:#2facfa;
          }
          .sexMan{
            width:36px;
            @include bg-image("~@/containers/Main/assets/icon_man");
          }
          .sexWoman{
            width:36px;
            @include bg-image("~@/containers/Main/assets/icon_woman");
          }
          .signIn{
            width:104px;
            height:54px;
            line-height:54px;
            text-align: center;
            float:right;
            background:rgba(47,172,250,1);
            border-radius:  27px 0 0 27px;
            font-size:28px;
            font-family:PingFang-SC-Medium;
            font-weight:normal;
            color:rgba(255,255,255,1);
          }
        }
        .address{
          height:70px;
          line-height:70px;
          font-size:27px;
          font-family:PingFang-SC-Regular;
          font-weight:normal;
          color:rgba(77,77,77,1);
          .addressInfo{
            height:70px;
            line-height:70px;
            float:left;
            max-width:calc(100% - 200px);
            margin-right:34px;
            font-size:24px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            color:rgba(102,102,102,1);
          }
          .line{
            height:28px;
            width:0px;
            float:left;
            margin-right:28px;
            margin-top:21px;
            border-right:1px solid rgba(102,102,102,1);
          }
          .ageNum{
            height:70px;
            line-height:70px;
            float:left;
            font-size:24px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            color:rgba(102,102,102,1);
          }
        }
        .infoInfo{
          .leftTitle{
            width:396px;
            height:30px;
            line-height:30px;
            float:left;
            font-size:27px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(77,77,77,1);
            margin-right:14px;
          }
          .rightIcon{
            width:11px;
            height:22px;
            float:right;
            margin-right:80px;
            margin-top:3px;
            @include bg-image("~@/assets/images/icon_right");
          }
        }
      }
    }
  }
</style>



