<template>
  <!--立即报名-->
  <div class="signUp">
    <headerBack title="立即报名"></headerBack>

    <verticalScroll>
      <!--订单编号-->
      <div class="titleBar">
        <span class="circleBlock"></span>
        订单编号：{{dataInfo.ordernum}}
      </div>
      <!--内容详情-->
      <div class="infoContent">
        <!--图片栏-->
        <div class="imgOut">
          <div class="imgShow">
            <img v-lazy="dataInfo.imageurl" width="100%"  height="100%" alt="">
          </div>
          <div class="infoInfo text-ellipsis">
            <div class="title text-ellipsis">
              {{dataInfo.coursename}}
            </div>
            <div class="subTitle text-ellipsis">
              {{dataInfo.classname}}
            </div>
            <div class="teacher text-ellipsis">
              {{dataInfo.teachername}}
            </div>
          </div>
        </div>
        <!--订单倒计时-->
        <div class="courseTime">
          <div class="courseNumber">
            下单时间：{{dataInfo.ordertime}}
          </div>
          <div class="setTime" v-if="overplusTime">
            该订单还有{{overplusTime>30?30:overplusTime}}分钟失效，请尽快支付
          </div>
          <div class="setTime" v-else>
            该订单还有30分钟失效，请尽快支付
          </div>
        </div>
        <!--地址选择-->
        <div class="bottomContent">
          <!--<div class="addressTitle">-->
            <!--收货地址-->
          <!--</div>-->
          <!--<div class="addressContent">-->
            <!--<div class="address">-->
              <!--<div class="topAddress">-->
                <!--<div class="leftTitle">-->
                  <!--阿尔萨斯-->
                <!--</div>-->
                <!--<div class="phoneNum">-->
                  <!--1854547875454-->
                <!--</div>-->
                <!--<div class="rightBlock">-->
                  <!--默认-->
                <!--</div>-->
              <!--</div>-->
              <!--<div class="bottomAddress">-->
                <!--重庆市南岸区南坪西路58号上海城嘉德中心2号7-2-->
              <!--</div>-->
            <!--</div>-->
            <!--<div class="rightIcon">-->
              <!--<div class="iconShow"></div>-->
            <!--</div>-->
          <!--</div>-->

          <div style="border-bottom:1px solid rgba(217,217,217,1);margin-bottom:51px;">
            <ul>
              <li>
                <div class="leftItem">
                  订单金额：
                </div>
                <div class="rightItem" >
                  ￥{{dataInfo.originprice}}
                </div>
              </li>
              <li>
                <div class="leftItem">
                  折扣金额：
                </div>
                <div class="rightItem" >
                  -￥{{dataInfo.discount}}
                </div>
              </li>
              <li>
                <div class="leftItem">
                  实付金额：
                </div>
                <div class="rightItem">
                  ￥{{toDecimal2(dataInfo.originprice-dataInfo.discount)}}
                </div>
              </li>
            </ul>
          </div>
          <div class="signButton"  @click="signUp">
            立即支付
          </div>
        </div>
      </div>
    </verticalScroll>
    <!--支付Modal-->
    <paymentModal v-if="modalStatus"  :balances="toDecimal2(dataInfo.originprice-dataInfo.discount)"  @priceStatus="priceStatus"  :price="dataInfo.usablemoney"></paymentModal>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import paymentModal from '@/components/PaymentModal';   //支付Modal
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Radio,Toast} from 'mint-ui';
  let that = {};
  export default{
    name:'signUp',
    components:{
      headerBack,
      verticalScroll,
      paymentModal
    },
    data(){
      return {
        ordernum:'',   //订单号
        dataInfo:{},   //订单信息
        modalStatus:false,  //默认付钱modal关闭
        paytype:'',     //支付方式   1余额支付  2微信支付
        weiInfo:{},     //返回的微信数据
        overplusTime:30,  //剩余多少分钟订单过期
        studentid:'',   //学生id
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      that = this;
      // 获取订单ID
      this.ordernum = this.$route.query.ordernum;
      console.log(this.ordernum);
      // 显示订单详情
      this.showOrderDetail();
    },
    methods:{
      toDecimal2(x) {
        var f = parseFloat(x);
        if (isNaN(f)) {
          return false;
        }
        var f = Math.round(x * 100) / 100;
        var s = f.toString();
        var rs = s.indexOf('.');
        if (rs < 0) {
          rs = s.length;
          s += '.';
        }
        while (s.length <= rs + 2) {
          s += '0';
        }
        return s;
      },
      // 查询订单详情
      showOrderDetail(){
        Server.post(Path.showOrderDetail, {
          'ordernum':this.ordernum
        }, (res) => {
          if (res.code === 0) {
            if(res.data){
              this.dataInfo = res.data;
              let orderTime = new Date(res.data.ordertime).getTime();
              let nowTime = new Date().getTime();
              this.overplusTime = Math.floor(30-(nowTime-orderTime)/1000/60);
            }

          }else{
            Toast(res.info);
          }
        });
      },
      // 立即支付  开启Modal
      signUp(){
        this.modalStatus = true;
      },
      // 是否提交支付
      priceStatus(value,type){
        this.paytype = type;
        // 关闭modal
        this.modalStatus = false;
        if(value=='yes'){
          this.gotoPay();
        }else{
          console.log('取消');
        }
      },
      // 确认支付
      gotoPay(){
        let code = localStorage.getItem('code');
        Server.post(Path.gotoPayCourse, {
          'ordernum':this.dataInfo.ordernum,
          'paytype':this.paytype,
          'studentid':this.studentid,
          'code':code
        }, (res) => {
          if (res.code === 0) {
            if(this.paytype==1){
              this.$router.push('/PaymentBack');
            }else{
              this.weiInfo = res.data;
              this.pay();
            }
          }else{
            Toast(res.info);
          }
        });
      },
      // 微信支付
      pay(){
        if (typeof WeixinJSBridge == "undefined"){
          if( document.addEventListener ){
            document.addEventListener('WeixinJSBridgeReady',that.onBridgeReady,false);
          }else if (document.attachEvent){
            document.attachEvent('WeixinJSBridgeReady', that.onBridgeReady);
            document.attachEvent('onWeixinJSBridgeReady', that.onBridgeReady);
          }
        }else{
          that.onBridgeReady();
        }
      },
      onBridgeReady(){
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId":that.weiInfo.appId,     //公众号名称，由商户传入
            "timeStamp":that.weiInfo.timeStamp,         //时间戳，自1970年以来的秒数
            "nonceStr":that.weiInfo.nonceStr, //随机串
            "package":that.weiInfo.package,
            "signType": that.weiInfo.signType,         //微信签名方式:
            "paySign": that.weiInfo.paySign   //微信签名
          },
          function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok"){
              that.$router.push('/PaymentBack');
            }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
              Toast('取消支付');
            }else{
              Toast('支付失败');
            }
          });
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
    .signUp{
      position:absolute;
      left:0;
      right:0;
      top:0;
      bottom:0;
      background:rgba(255,255,255,1);
      .titleBar{
        vertical-align:middle;
        height:91px;
        padding-left:37px;
        line-height:91px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(61,61,61,1);
        border-radius:12px 12px 0px 0px;
        border-bottom:1px solid rgba(217,217,217,1);
        .circleBlock{
          vertical-align:middle;
          display:inline-block;
          width:10px;
          height:10px;
          background:rgba(102,102,102,1);
          border-radius:50%;
        }
      }
      .infoContent{
        padding:44px 37px 131px 37px;
        .imgOut{
          height:160px;
          margin-bottom:44px;
          .imgShow{
            width:280px;
            height:160px;
            float:left;
            overflow: hidden;
            margin-right:34px;
            border-radius:8px;
          }
          .infoInfo{
            width:350px;
            float:left;
            .title{
              height:66px;
              font-size:32px;
              font-family:PingFang-SC-Medium;
              font-weight:500;
              color:rgba(51,51,51,1);
            }
            .subTitle{
              height:58px;
              font-size:30px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(102,102,102,1);
            }
            .teacher{
              font-size:30px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(102,102,102,1);
            }
          }
        }
        /*订单倒计时*/
        .courseTime{
          height:127px;
          border-bottom:1px solid rgba(217,217,217,1);
          .courseNumber{
            height:55px;
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(61,61,61,1);
          }
          .setTime{
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(57,173,239,1);
          }
        }
        /*底部详情*/
        .bottomContent{
          padding-top:19px;
          .addressTitle{
            height:62px;
            font-size:32px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(77,77,77,1);
          }
          /*中间部分*/
          .addressContent{
            height:98px;
            margin-bottom:27px;
            /*右侧图标*/
            .address{
              height:98px;
              width:646px;
              float:left;
              padding-left:96px;
              box-sizing: border-box;
              .topAddress{
                height:30px;
                margin-bottom:26px;
                .leftTitle{
                  height:30px;
                  line-height: 30px;
                  font-size:30px;
                  float:left;
                  font-family:MicrosoftYaHei;
                  font-weight:400;
                  margin-right:83px;
                  color:rgba(51,51,51,1);
                }
                .phoneNum{
                  height:30px;
                  line-height: 30px;
                  float:left;
                  font-size:30px;
                  font-family:MicrosoftYaHei;
                  font-weight:400;
                  margin-right:30px;
                  color:rgba(51,51,51,1);
                }
                .rightBlock{
                  width:80px;
                  height:26px;
                  line-height: 26px;
                  text-align: center;
                  margin-top:2px;
                  float:left;
                  background:rgba(255,164,0,1);
                  border-radius:10px;
                  font-size:20px;
                  font-family:MicrosoftYaHei;
                  font-weight:400;
                  color:rgba(255,255,255,1);
                }
              }
              .bottomAddress{
                font-size:20px;
                font-family:MicrosoftYaHei;
                font-weight:400;
                color:rgba(102,102,102,1);
              }
            }
            /*左部分信息*/
            .rightIcon{
              height:98px;
              width:20px;
              display:flex;
              align-items: center;
              justify-content: right;
              float:right;
              .iconShow{
                width:19px;
                height:32px;
                @include bg-image("~@/assets/images/icon_right");
              }
            }
          }

          li{
            height:80px;
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            .leftItem{
              float:left;
              color:rgba(77,77,77,1);
            }
            .rightItem{
              float:right;
              color:rgba(233,29,29,1);
            }
          }

          /*支付按钮*/
          .signButton{
            width:676px;
            height:108px;
            line-height:108px;
            text-align: center;
            background:rgba(57,173,239,1);
            border-radius:10px;
            font-size:34px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            margin:0px auto;
            color:rgba(254,255,255,1);
          }
        }
      }
    }
</style>



