
<template>
  <div>
      <div class="FullItem" v-for="(info,key) in data" @click="toPage(info.courseid,info.classtypes)"  :key="key">
        <div class="itemInfo" :style="{'border-bottom':key==data.length-1?'none':'1px solid rgba(217,217,217,1)'}">
          <div class="imgShow">
            <img v-lazy="info.imageurl" width="100%" height="100%" alt="">
          </div>
          <div class='infoContent'>
            <div class="title">
              <div class="rightBlock" >
                <template v-if="info.classtypes==2" class="video">直播</template>
                <template v-if="info.classtypes==1" class="video">录播</template>
              </div>
              {{info.coursename}}
            </div>
            <div class="teacher" :class="{'hidden':true}">
            </div>
            <div class="priceOut">
              <!--<div :class="{'priceLeft':showStatus==3,'priceRight':showStatus==0}">-->
                <!--￥{{info.price}}-{{info.maxprice}}/每课时-->
              <!--</div>-->
              <div class="priceLeft">
                ￥{{info.price}}-{{info.maxprice}}/每课时
              </div>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>
<script>
  import itemTitle from '../ItemTitle';   //查看全部
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'FullItem',
    components:{
      itemTitle
    },
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      },
      teacherid:{
        type:Number,
        default(){
          return ;
        }
      },
      showStatus:{
        type:Number,
        default(){      //3 首页的显
                        //1  AllCourses  全部课程
          return 3;    //0  TeacherInfo 老师的课程
        }
      }
    },
    created() {
    },
    methods:{
      toPage(courseid,classtypes){
        this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes,from:'teacher',teacherid:this.teacherid}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  .FullItem{
    height:216px;
    padding:28px 24px 28px 24px;
    background:rgba(255,255,255,1);
    clear:both;
    .itemInfo{
      height:216px;
      box-sizing: border-box;
      /*border-bottom:1px solid rgba(217,217,217,1);*/
      .imgShow{
        width:280px;
        height:160px;
        border-radius:8px;
        float:left;
        overflow:hidden;
        margin-right:34px;
      }
      .infoContent{
        float:left;
        width: calc(100% - 362px);
        height:160px;
        .title{
          height:60px;
          line-height:60px;
          font-size:30px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(51,51,51,1);
          .rightBlock{
            width:62px;
            height:28px;
            margin-top:16px;
            text-align: center;
            line-height:28px;
            float:left;
            background:rgba(255,152,106,1);
            border-radius:4px;
            font-size:20px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            margin-right:5px;
            color:rgba(255,255,255,1);
            .video{
              font-size:16px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(255,255,255,1);
            }
          }
        }
        .teacher{
          height:67px;
          font-size:28px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
        }
        .hidden{
          visibility: hidden
        }
        .priceOut{
          height:28px;
          .priceLeft{
            margin-top:3px;
            float:left;
            height:22px;
            line-height:22px;
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(235,31,21,1);
          }
          .priceRight{
            margin-top:3px;
            float:right;
            height:22px;
            margin-right:25px;
            line-height:22px;
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(235,31,21,1);
          }

        }
      }
    }
  }
</style>



