<template>
  <div class="banner" id="Banner" v-if="listImg.length!=0">
    <itemTitle title="名师推荐" router="/TeacherList"></itemTitle>
    <swiper :options="swiperOption" ref="famousTeacher">
      <swiper-slide  v-for="(item,i) in listImg" :key="i" v-if="item.length>0">
            <div class="flexLi" v-for="(info,key) in item" :key="key"  @click="toPage(info.teacherid)" >
              <div class="flexOut">
                <div class="imgShow">
                  <img :src="info.imageurl ||  defaultTeacher"   @error="_imageError"   height="100%" width="100%">
                </div>
                <div class="title text-ellipsis">
                  {{info.teachername}}
                </div>
              </div>
            </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import defaultTeacher from '@/assets/images/defaultTeacher.png';      //图片加载页面失败显示
  import 'swiper/dist/css/swiper.css'
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import itemTitle from '../ItemTitle';   //查看全部

  export default {
    name: "Banner",
    components: {
      swiper,
      swiperSlide,
      itemTitle
    },
    data() {
      return {
        defaultTeacher,
        swiperOption: {
          slidesPerView: 1,     //一次显示3个
          autoplayDisableOnInteraction: false,
          autoplay: 3000,
          initialSlide: 0,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          loop: true,
          type: 'bullets',
          notNextTick: true,
          direction: 'horizontal',
          lazyLoading : true,//懒加载开启
          pagination: '.swiper-pagination',
          // slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 67,
          onSlideChangeEnd: swiper => {
            this.page = swiper.realIndex + 1;
            this.index = swiper.realIndex;
          }
        },
        listImg: [],
        // organid: this.$oid
      }
    },
    created() {
      // 获取轮播图
      this.getRecommendTeacher();
    },
    methods: {
      toPage(teacherId){
        this.$router.push({path:'/TeacherInfo',query:{teacherId:teacherId}});
      },
      // 老师轮播图
      getRecommendTeacher(){
      // 获取轮播图数据
        Server.post(Path.getRecommendTeacher, {

        }, (res) => {
          console.log(res.data);
          if (res.code === 0) {
            // 绑定数据
            this.listImg = res.data;
          }
        });
      }
    },
    //定义这个sweiper对象
    computed: {
      swiper() {
        return this.$refs.famousTeacher.swiper;
      },
    }
  }
</script>

<style scoped lang="scss">
  .swiper-container {
    width: 100%;
    height: 348px;
    padding:48px 20px 71px 20px;
    overflow: hidden;
    box-sizing: border-box;
    background:rgba(255,255,255,1);
    .swiper-wrapper {
      width: 100%;
      height: 348px;
      box-sizing: border-box;
      overflow:hidden;
      .swiper-slide {
        width: 100%;
        height: 100%;
          .flexLi{
            width:33.3%;
            float:left;
            .flexOut{
              width:170px;
              margin:0px auto;
              .imgShow{
                width:170px;
                height:170px;
                overflow:hidden;
                border-radius:50%;
                margin-bottom:31px;
              }
              .title{
                height:28px;
                line-height:28px;
                width:170px;
                text-align: center;
                font-size:30px;
                font-family:PingFang-SC-Regular;
                font-weight:400;
                color:rgba(51,51,51,1);
              }
            }
          }

      }
    }
  }

</style>



