<template>
  <div>
    <div class="MyCourseNow"  v-for="item in data" @click="toPage(item.schedulingid,item.curriculumid,item.learned)">
      <div class="imgShow">
        <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
        <div class="classType" v-if="item.ischangestatus==1">
          已调班
        </div>
      </div>
      <div class="infoContent">
        <div class="title">
          <span >
            直播
          </span>
          {{item.coursename}}
        </div>
        <div class="teacher text-ellipsis">
          <span class="title">
            {{item.classname}}
          </span>
          <span class="titleMiddle"></span>
          <span class="teacher">
            {{item.teachername }}
          </span>
        </div>
        <div class="progress">
          <span style="margin-right:15px;">
            进度:
          </span>
          <div  class="progressLine" >
            <div class="orangeLine"  :style="{'width':item.learned}"></div>
          </div>
          <span class="progressNumber">
            {{item.learned}}
          </span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'MyCourseNow',
    props:{
      width:{
        type:Number,
        default(){
          return 22;
        }
      },
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    methods:{
      toPage(schedulingid,courseid,learned){
        this.$emit('MyEndInfo',schedulingid,courseid,learned);
      },
    },
    filters: {

    }
  }
</script>
<style lang="scss" scoped>
  .MyCourseNow{
    height:200px;
    background:rgba(255,255,255,1);
    padding:20px 0px 20px 25px;
    box-sizing:border-box;
    border-radius:10px;
    .imgShow{
      width:280px;
      height:160px;
      float:left;
      overflow: hidden;
      margin-right:21px;
      border-radius:8px;
      position:relative;
      .classType{
        width:120px;
        height:40px;
        line-height:40px;
        text-align: center;
        border-top-left-radius: 8px;
        left:0px;
        top:0px;
        background:rgba(255,152,106,1);
        position:absolute;
        font-size:24px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,254,253,1);
      }
      .classBlue{
        width:74px;
        height:40px;
        line-height:40px;
        text-align: center;
        left:0px;
        top:0px;
        background:rgba(47,172,250,1);
        border-top-left-radius: 8px;
        position:absolute;
        font-size:24px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,254,253,1);
      }
    }
    .infoContent{
      height:160px;
      width: calc(100% - 326px);
      float:left;
      .title{
        height:85px;
        font-size:26px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(77,77,77,1);
        line-height:36px;
        span{
          display:inline-block;
          width:62px;
          height:28px;
          line-height: 28px;
          background:rgba(255,152,106,1);
          border-radius:4px;
          text-align: center;
          font-size:20px;
          font-family:PingFang-SC-Regular;
          font-weight:normal;
          margin-right:2px;
          color:rgba(255,255,255,1);
        }
      }
      .teacher{
        height:52px;
        .title{
          font-size:24px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(128,128,128,1);
          line-height:36px;
          margin-right:10px;
        }
        .titleMiddle{
          display:inline-block;
          width:2px;
          height:20px;
          background:rgba(179,179,179,1);
          margin-right:10px;
        }
        .teacher{
          font-size:24px;
          font-family:Adobe Heiti Std R;
          font-weight:normal;
          color:rgba(128,128,128,1);
        }
      }
      .progress{
        height:22px;
        font-size:24px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(102,102,102,1);
        .progressLine{
          display:inline-block;
          width:100px;
          height:5px;
          background:rgba(179,179,179,1);
          border:1px solid rgba(179,179,179,1);
          border-radius:3px;
          margin-right:15px;
          .orangeLine{
            height:5px;
            background:rgba(255,164,0,1);
            border-radius:3px;
          }
        }
      }
    }
  }
</style>



