<template>
  <div class="nowNow">
    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
      <ul>
        <li class="teacherItem" v-for="(item,index) in dataInfo"  @click="toPage(item.teacherid)">
          <div class="imgOut">
            <div class="imgShow">
              <div class="imgNow">
                <img v-lazy="item.imageurl || defaultTeacher" width="100%"  height="100%" alt="">
              </div>
            </div>
          </div>
          <div class="teacherName text-ellipsis">
            {{item.teachername}}
          </div>
          <div class="teacherInfo text-ellipsis">
            {{item.profile}}
          </div>
          <div class="rightBorder" v-if="index%2==0"></div>
          <div class="lefterBorder"></div>
          <div class="bottomBorder"></div>
        </li>
        <li class="teacherItem" v-if="dataInfo.length%2==1"></li>
      </ul>
      </mt-loadmore>
    </div>
    <notInfo v-if="infoStatus">
      抱歉，没有老师列表
    </notInfo>
  </div>
</template>
<script>
  import Server from '@/service/request';
  import defaultTeacher from '@/assets/images/defaultTeacher.png';      //图片加载页面失败显示
  import Path from '@/service/Path';
  import notInfo from '@/components/NotInfo';     //返回组件
  import { Loadmore } from 'mint-ui';
  export default{
    name:'TeacherList',
    components:{
      'mt-loadmore':Loadmore,
      notInfo
    },
    data(){
      return{
        dataInfo:[],   //数组集合
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
        defaultTeacher
      }
    },
    created() {
      // 名师堂数据
      this.getAllTeacherList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.getAllTeacherList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.getAllTeacherList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      getAllTeacherList(){
        // 获取名师堂数据
        Server.post(Path.getAllTeacherList, {
          'pagenum':this.pagenum
        }, (res) => {
          if (res.code === 0) {
            if(res.data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              //页码不为一，追加
              this.dataInfo = this.dataInfo.concat(res.data.data);
            } else {
              //否则从第一页开始，覆盖列表数据
              this.dataInfo = res.data.data;
            }
          }
        });
      },
      toPage(teacherId){
        this.$router.push({path:'/TeacherInfo',query:{teacherId:teacherId}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .nowNow{
    background:rgba(255,255,255,1);
  }
  .infoShow{
    padding:30px 20px;
    text-align: center;
    height:200px;
    line-height:200px;
    font-size:28px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(102,102,102,1);
    background:rgba(255,255,255,1);
  }
  .teacherItem{
    width:50%;
    height:443px;
    padding-top:69px;
    box-sizing: border-box;
    background:rgba(255,255,255,1);
    position:relative;
    float:left;
    .bottomBorder{
      width:251px;
      height:0px;
      border-bottom:1px solid rgba(221,223,227,1);
      position:absolute;
      right:62px;
      bottom:0px;
    }

    .rightBorder{
      width:0px;
      height:251px;
      border-right:1px solid rgba(242,242,242,1);
      /*border:1px solid rgba(221,223,227,1);*/
      position:absolute;
      right:0px;
      top:96px;
    }
    .lefterBorder{
      width:251px;
      height:1px;
      background:rgba(242,242,242,1);
      /*border:1px solid rgba(221,223,227,1);*/
      position:absolute;
      bottom:0px;
      left:62px;
    }
    .imgOut{
      height:169px;
      .imgShow{
        overflow: hidden;
        box-sizing:border-box;
        display:flex;
        justify-content: center;
        align-items: center;
        width:169px;
        height:169px;
        margin:0px auto;
        border:2px solid rgba(221,223,227,1);
        border-radius:50%;
        .imgNow{
          width:140px;
          height:140px;
          overflow: hidden;
          border-radius:50%;
        }
      }
    }
    .teacherName{
      width:100%;
      height:102px;
      line-height:102px;
      text-align: center;
      margin:0px auto;
      font-size:30px;
      font-family:PingFang-SC-Medium;
      font-weight:normal;
      color:rgba(77,77,77,1);
    }
    .teacherInfo{
      text-align:center;
      font-size:28px;
      font-family:PingFang-SC-Regular;
      font-weight:normal;
      color:rgba(128,128,128,1);
    }
  }
</style>



