
<template>
  <div>
    <div class="FullItem" v-for="item in data" @click="toPage(item.courseid,item.classtypes)">
      <div class="itemInfo">
        <div class="imgShow">
          <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
          <div class="classType" v-if="item.classtypes==2">
            直播
          </div>
          <div class="classBlue" v-if="item.classtypes==1">
            录播
          </div>
        </div>
        <div class='infoContent text-ellipsis'>
          <div class="title">
            {{item.coursename}}
          </div>
          <div class="teacher text-ellipsis" >
            {{item.subhead}}
          </div>
          <div class="priceOut">
            <div class="priceRight" >
              <template v-if="item.classtypes==1 ||(item.classtypes==2 && item.price==item.maxprice) ">
                ￥{{item.price}}/每课时
              </template>
              <template v-else>
                ￥{{item.price}}-{{item.maxprice}}/每课时
              </template>
            </div>
            <div class="leftBlock" >
              <!--已满-->
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'FullItem',
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      },
      showStatus:{
        type:Number,
        default(){      //3 首页的显
          //1  AllCourses  全部课程
          return 3;    //0  TeacherInfo 老师的课程
        }
      }
    },
    methods:{
      toPage(courseid,classtypes){
        this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  .FullItem{
    height:216px;
    padding:28px 24px 28px 24px;
    .itemInfo{
      height:216px;
      box-sizing: border-box;

      border-bottom:1px solid rgba(217,217,217,1);
      .imgShow{
        width:280px;
        height:160px;
        overflow: hidden;
        border-radius:8px;
        float:left;
        margin-right:34px;
        position:relative;
        .classType{
          width:74px;
          height:40px;
          line-height:40px;
          text-align: center;
          border-top-left-radius: 8px;
          left:0px;
          top:0px;
          background:rgba(255,152,106,1);
          position:absolute;
          font-size:24px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(255,254,253,1);
        }
        .classBlue{
          width:74px;
          height:40px;
          line-height:40px;
          text-align: center;
          left:0px;
          top:0px;
          background:rgba(47,172,250,1);
          border-top-left-radius: 8px;
          position:absolute;
          font-size:24px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(255,254,253,1);
        }
      }
      .infoContent{
        float:left;
        width: calc(100% - 362px);
        height:160px;
        .title{
          height:60px;
          font-size:30px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(51,51,51,1);
        }
        .teacher{
          height:67px;
          font-size:28px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(102,102,102,1);
        }
        .hidden{
          visibility: hidden
        }
        .priceOut{
          height:28px;
          .priceRight{
            margin-top:3px;
            float:right;
            height:22px;
            margin-right:25px;
            line-height:22px;
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(235,31,21,1);
          }
          .leftBlock{
            height:28px;
            text-align: center;
            line-height:28px;
            float:left;
            font-size:24px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(128,128,128,1);
          }
        }
      }
    }
  }
</style>



