<template>
  <!--全部课程头部tab-->
  <div>
    <div class="AllCourseTab">
      <div class="tabOut">
        <swiper :options="swiperOption" ref="mySwiper"  class="tabInner">
            <swiper-slide  class="itemTab"  :class="{'selected':tabSelected==item.category_id}"  ref= "slider"  @click.native="selectType(item.category_id,item.describe,item.categoryname,item.imgs)" v-for="(item,key) in dataInfo" :key="key">
              <div class="bottomBlock" v-if="tabSelected==item.category_id"></div>
              {{item.categoryname}}
            </swiper-slide>
        </swiper>
      </div>
      <div class="iconDiv">
        <div>
          <img src="~@/assets/images/searchIcon.png" width="100%" alt=""  @click="twoPage">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast} from 'mint-ui';
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import 'swiper/dist/css/swiper.css';
  export default{
    name:'AllCourseTab',
    components:{
      swiper,
      swiperSlide
    },
    data(){
      return {
        swiperOption: {
          autoplayDisableOnInteraction: false,
          initialSlide: 0,
          observer:true,//修改swiper自己或子元素时，自动初始化swiper
          // loop: false,
          // type: 'bullets',
          notNextTick: true,
          direction: 'horizontal',
          // pagination: '.swiper-pagination',
          slidesPerView :'auto',
          // centeredSlides: true,
          paginationClickable: true,
          // centeredSlides:false,
          // spaceBetween: 30,
          onTransitionEnd: function(swiper){
            if(swiper.progress==1){
              swiper.activeIndex=swiper.slides.length-1;
            }
          }
        },
        tabSelected:this.category_id,
        dataInfo:[],  //数据
      }
    },
    props:{
      category_id:{
        type:Number,
        default(){
          return 0;
        }
      },
    },
    created(){
      // 获取全部的一级分类
      this.getTopCategory();
    },
    methods:{
      // 打开二级页面
      twoPage(){
        this.$emit('twoPage');
      },
      // 选中一级类并选中
      selectType(id,content,categoryname,url){
        this.tabSelected = id;
        this.$emit('selectType',id,content,categoryname,'top',url);
      },
    // 获取全部的一级分类
      getTopCategory(){
        Server.post(Path.getTopCategory,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.dataInfo = data;
            if(this.category_id===0){
              if(data.length>0){
                this.$emit('selectType',data[0].category_id,data[0].describe,data[0].categoryname,'','');
              }
            }

          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    },
    watch:{
      category_id:function(value){
        this.tabSelected = value;
      }
    },
    computed: {
      swiper () {
        return this.$refs.MySwiper.swiper;
      }
    }

  }
</script>
<style lang="scss" scoped>
  .headTab{
    padding: 0 0 0 35px;
    position: relative;
    //   display: flex;
    //   flex-direction: row;
    //   justify-content:flex-start;
    //   flex-wrap: nowrap;
    overflow:hidden;
    padding-bottom:18px ;
    white-space:nowrap;
    .tabP{
      display: inline-block;
      line-height:1;
      color:#FFFFFF ;
      opacity:0.7;
      font-size:30px;
      padding: 0 10px;
      margin-right:48px;
      width: auto;
    }
    .active{
      opacity:1;
      border: none;
    }
    .headcontent{
      position: absolute;
      left: 0;
      top: 76px;
      width: 100%;
      height: 184px;
      overflow: hidden;
      border-bottom: 1px solid rgba(147,147,147,.6);
      box-shadow:2px 0px 6px rgba(147,147,147,0.1);
      .headIcon{
        height: 27px;
        position:relative;
        .triangle{
          position: absolute;
          top: 0;
          left: 80px;
          z-index: 99;
          width:0px;
          height:0px;
          border-right:14px solid  rgba(0,0,0,0);
          border-bottom:14px solid #FFFFFF;
          border-left:14px solid  rgba(0,0,0,0);
        }
      }
      .headData{
        /*margin-top: 27px;*/
        padding: 0 24px;
        font-size:22px;
        height: 139px;
        line-height: 35px;
        text-indent:44px;
        font-family:PingFang-SC-Regular;
        background: #FFFFFF;
        font-weight:400;
        color:rgba(92,92,92,1);
      }
    }
  }
  .AllCourseTab{
    box-sizing: border-box;
    height:80px;
    padding:0px 21px 0px 30px;
    font-size:28px;
    font-family:PingFang-SC-Medium;
    font-weight:normal;
    color:rgba(77,77,77,1);
    background:rgba(255,255,255,1);
    .tabInner{
      height:80px;
      overflow-x: hidden;
      box-sizing:border-box;
    }
    .tabOut{
      height:80px;
      width:calc(100% - 120px);
      float:left;
      overflow-x: hidden;
      box-sizing:border-box;
      /*.tabP{*/
        /*display: inline-block;*/
        /*line-height:1;*/
        /*color:#FFFFFF ;*/
        /*opacity:0.7;*/
        /*font-size:30px;*/
        /*padding: 0 10px;*/
        /*margin-right:48px;*/
        /*width: auto;*/
      /*}*/
      .itemTab{
        width:auto;
        padding:0px 20px;
        height:70px;
        line-height:70px;
        text-align:center;
        float:left;
        margin-right:36px;
        position:relative;
        .bottomBlock{
          width: 0px;
          height: 0px;
          position:absolute;
          left:50%-8;
          bottom:-16px;
          border-color:    rgba(63,145,197,1)  transparent    transparent  transparent ;
          border-width: 8px 8px 8px 8px;
          border-style: solid;
        }
      }

      .selected{
        box-sizing:border-box;
        color:rgba(57,173,239,1);
        border-bottom:3px solid rgba(57,173,239,1);
        border-radius:2px;
      }
    }

    .iconDiv{
      float:right;
      height:80px;
      text-align: right;
      width:38px;
      div{
        display:inline-block;
        width:38px;
        height:38px;
        /*background:red;*/
        margin-top:15px;
      }
    }
  }
</style>



