<template>
  <!--我的套餐-->
  <div class="SetMeal myCellSwipe">
    <headerBack title="我的套餐"></headerBack>
    <mt-navbar :value="selected" v-model="selected" >
      <mt-tab-item id="0" value="1">未使用</mt-tab-item>
      <mt-tab-item id="1">已使用</mt-tab-item>
      <mt-tab-item id="2">已过期</mt-tab-item>
    </mt-navbar>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="0">
            <div infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <mt-cell-swipe class="orderItem" v-for="(item,key) in notInfo" :key="key"
                             :right="[
                      {content: '删除',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.packageuseid,key)}
                      ]" >
                <mealList :data="item"  ></mealList>
              </mt-cell-swipe>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <mt-cell-swipe class="orderItem" v-for="(item,key) in alreadyInfo" :key="key"
                             :right="[
                      {content: '删除',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.packageuseid,key)}
                      ]" >
                <mealList :data="item" type="1"></mealList>
              </mt-cell-swipe>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="2">
            <div infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <mt-cell-swipe class="orderItem" v-for="(item,key) in expiredInfo" :key="key"
                             :right="[
                      {content: '删除',style: { background: 'red', color: '#fff',width:'60px',textAlign:'center' },handler:() => deleteItem(item.packageuseid,key)}
                      ]" >
                <mealList :data="item" type="2"></mealList>
              </mt-cell-swipe>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <router-link to="/MealList">
      <div class="bottomButton" >
        更多课时套餐
      </div>
    </router-link>


    <notInfo v-if="infoStatus">
      抱歉，没有套餐数据
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import mealList from '../components/MealList';      //列表
  import notInfo from '@/components/NotInfo';           //没有数据
  import {Loadmore, Navbar, TabItem ,TabContainer, TabContainerItem,CellSwipe,Toast} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'SetMeal',
    components:{
      headerBack,
      mealList,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-loadmore':Loadmore,
      'mt-cell-swipe':CellSwipe,
    },
    data(){
      return {
        selected: '0',     //默认请求的数据    //0 待使用 1 已使用 2 已过期
        notInfo:[],         //未使用的套餐数据
        alreadyInfo:[],     //已使用的套餐数据
        expiredInfo:[],     //过期的数据

        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      if(window.localStorage.getItem('selectedMeal') && window.localStorage.getItem('selectedMeal')!=='0'){
        this.selected = window.localStorage.getItem('selectedMeal');
        return;
      }
      // 请求我的套餐列表
      this.packageUseList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.packageUseList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.packageUseList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 请求我的套餐列表
      packageUseList(){
        Server.post(Path.packageUseList,{
          'status':this.selected,
          'pagenum':this.pagenum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              if(this.selected==='0'){
                this.notInfo = this.notInfo.concat(data.data);
              }else if(this.selected==='1'){
                this.alreadyInfo = this.alreadyInfo.concat(data.data);
              }else{
                this.expiredInfo = this.expiredInfo.concat(data.data);
              }
            } else {
              if(this.selected==='0'){
                this.notInfo = data.data;
              }else if(this.selected==='1'){
                this.alreadyInfo = data.data;
              }else{
                this.expiredInfo = data.data;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },

      // 删除套餐条目
      deleteItem(id,key){
        console.log(key);
        Server.post(Path.deletePackageUse,{
          'packageuseid':id
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.pagenum = 1;
            this.packageUseList();
            // if(this.selected==='0'){
            //   this.notInfo.splice(key,1);
            //   if(this.notInfo.length==0){
            //     this.infoStatus = true;
            //   }
            // }else if(this.selected==='1'){
            //   this.alreadyInfo.splice(key,1);
            //   if(this.alreadyInfo.length==0){
            //     this.infoStatus = true;
            //   }
            // }else{
            //   this.expiredInfo.splice(key,1);
            //   if(this.expiredInfo.length==0){
            //     this.infoStatus = true;
            //   }
            // }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedMeal', this.selected);
        this.pagenum = 1;
        // 再次请求列表数据
        this.packageUseList();
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .SetMeal{
    @extend .main-child;
    .bottomButton{
      position:fixed;
      bottom:0;
      left:0;
      right:0;
      height:98px;
      line-height: 98px;
      text-align: center;
      background:rgba(255,255,255,1);
      box-shadow:0px -2px 6px 0px rgba(0,0,0,0.3);
      font-size:30px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(102,102,102,1);
    }
  }
</style>



