<template>
  <div >
    <div class="MyColItem" v-for="item in data" @click="toTeacher(item.teacherid)">
      <div class="imgShow"  >
        <img v-lazy="item.imageurl ||defaultTeacher"  height="100%" width="100%" alt="">
      </div>
      <div class="infoContent">
        <div class="teacher">
          {{item.teachername}}
        </div>
        <div class="infoInfo text-ellipsis">
          {{item.profile}}
        </div>
      </div>
      <div class="clickButton" @click.stop="cancelTeacherCollect(item.teacherid)">
        取消收藏
      </div>
    </div>
  </div>
</template>
<script>
  import defaultTeacher from '@/assets/images/defaultTeacher.png';      //图片加载页面失败显示
  export default{
    name:'MyColItem',
    data(){
      return {
        studentid:'',  //学生ID
        defaultTeacher
      }
    },
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
    },
    methods:{
      // 前往老师详情页
      toTeacher(teacherId){
        this.$router.push({path:'/TeacherInfo',query:{teacherId:teacherId}});
      },
      // 取消收藏老师
      cancelTeacherCollect(teacherID){
        this.$emit('cancelTeacherCollect',teacherID);
      }
    }
  }
</script>
<style lang="scss" scoped>
  .MyColItem{
    height:148px;
    box-sizing: border-box;
    padding:24px 40px 0px 60px;
    background:rgba(255,255,255,1);
    border-bottom:1px solid rgba(217,217,217,1);
    .imgShow{
      width:100px;
      height:100px;
      border-radius:50%;
      margin-right:32px;
      overflow: hidden;
      float:left;
    }
    .infoContent{
      float:left;
      width: calc(100% - 300px);
      .teacher{
        height:56px;
        font-size:26px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
      }
      .infoInfo{
        font-size:20px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:26px;
        overflow : hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
      }
    }
    .clickButton{
      height:50px;
      padding:0px 15px;
      line-height: 50px;
      float:right;
      text-align: center;
      border:1px solid rgba(255,164,0,1);
      border-radius:10px;
      font-size:20px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(51,51,51,1);
      margin-top:25px;
    }
  }
</style>



