<template>
  <div class="MiddleToast">
    <fadeIn>
      <div class="changeInfo">
        <slot></slot>
      </div>
    </fadeIn>
  </div>
</template>
<script>
  import fadeIn from '@/components/FadeIn';  //过渡组件
  export default{
    name:"MiddleToast",
    components:{
      fadeIn
    }
  }
</script>
<style lang="scss" scoped>
  .MiddleToast{
    /*添加后信息显示*/
    .changeInfo{
      position:absolute;
      left:0px;
      right:0px;
      bottom:0px;
      top:0px;
      background:rgba(0,0,0,.2);
      z-index:999;
      display:flex;
      justify-content: center;
      align-items: center;
    }
  }
</style>



