<template>
  <div>
    <div class='EndInfoList'>
      <!--标题-->
      <div class="titleItem">
        <div class="littleBlock">
          <div class="grayBlock"></div>
        </div>
        <div class="littleInfo">
          共{{title}}个单元，{{subSub}}个课时
        </div>
      </div>
      <!--列表-->
      <div class="listDiv">
        <ul class="outUl">
          <li  v-for="(item,key) in data" :key="key">
            <div class="titleLi">
              <div class="blueBlock">
                <div class="bigShow"></div>
              </div>
              <div class="liTitle">
                {{item.unitname}}
              </div>
            </div>
            <ul class="inUl">
              <li v-for="(info,k) in item.period" :key="k">
                <div class="subItem">
                  <div class="leftIcon"></div>
                  <div class="middleTitle">
                    {{info.periodname}}
                  </div>
                  <div class="rightDiv">
                    {{info.starttime}}
                  </div>
                </div>
              </li>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'EndInfoList',
    data(){
      return {
        title:0,
        subSub:0
      }
    },
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    watch:{
      data:function(info){
        this.subSub = 0;
        this.title =  info.length;
        for(var item in info){
          this.subSub+=info[item].period.length;
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  .EndInfoList{
    background:rgba(255,255,255,1);
    .titleItem{
      height:90px;
      box-sizing: border-box;
      padding-left:36px;
      border-bottom:1px solid rgba(217,217,217,1);
      font-size:32px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(61,61,61,1);
      .littleBlock{
        height:90px;
        width:10px;
        display:flex;
        float:left;
        align-items: center;
        margin-right:19px;
        .grayBlock{
          width:10px;
          height:10px;
          background:rgba(217,217,217,1);
          border-radius:50%;
        }
      }
      .littleInfo{
        height:90px;
        line-height: 90px;
        font-size:32px;
        font-family:PingFang-SC-Bold;
        font-weight:bold;
        color:rgba(51,51,51,1);
      }
    }
    /*列表*/
    .listDiv{
      padding:32px 49px 102px 40px;
      background:rgba(255,255,255,1);
      .outUl{
        padding: 0;
        position: relative;
        box-sizing:border-box;
        .titleLi{
          height:67px;
          font-size:30px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(57,173,239,1);
          .blueBlock{
            width:22px;
            height:28px;
            display:flex;
            margin-top:10px;
            float:left;
            align-items: center;
            margin-right:27px;
            .bigShow{
              width:22px;
              height:22px;
              background:rgba(217,217,217,1);
              border-radius:50%;
            }
          }
          .liTitle{
            float:left;
            height:67px;
            font-size:30px;
            font-family:PingFang-SC-Bold;
            font-weight:bold;
            color:rgba(77,77,77,1);
          }
        }
        .inUl{
          clear:both;
          margin-left:50px;
          .subItem{
            height:27px;
            font-size:26px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            margin-bottom:23px;
            color:rgba(102,102,102,1);
            .leftIcon{
              width:6px;
              height:6px;
              background:rgba(153,153,153,1);
              margin-top:9.5px;
              float:left;
              margin-right:13px;
            }
            .middleTitle{
              float:left;
              height:27px;
              line-height:27px;
              font-size:26px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(102,102,102,1);
            }
            .rightDiv{
              float:right;
              height:27px;
              line-height: 27px;
              text-align: right;
            }
          }
        }
      }
      .outUl::before{
        content: '';
        position: absolute;
        top: 13px;
        bottom: 30px;
        width: 2px;
        background: rgba(217,217,217,1);
        left: 10px;
      }

    }
  }
</style>



