//获取语言
function getlanguage() {
  // JoinRoomCall.getLanguage(success);

  function success(msg) {
    // zh-CN  zh-HK en-US hi-IN
    if (msg.indexOf('zh') != -1) {
      return 'zh-cn';
    } else {
      return 'en-us';
    }
  }
}

let navLang = (navigator.language || navigator.browserLanguage).toLowerCase();
let lang = navLang.indexOf('zh') != -1 ? 'zh-cn' : 'en-us';

http.interceptors.request.use(function (config) {
  // Do something before request is sent
  var starttime = Date.parse(new Date());
  if(config.method == 'get'){
    config.url  = encodeURI(config.url);
  }
  if(config.method == 'post'){

  }

  if(config.url=='index/Login/getPublicKey' ||config.url=='student/Homepage/register'  ||config.url=='appstudent/Homepage/randomCode' || config.url=='appstudent/Homepage/sendMobileMsg'){

  }else{
    if(
      localStorage.getItem('appstudentinfo') == null ||
      localStorage.getItem('appstudentinfo') == "" ||
      localStorage.getItem('appstudentkey') == null ||
      localStorage.getItem('appstudentkey') == ""
    ){
      console.log(1111111111111111111111111111);
      localStorage.removeItem('appstudentinfo');
      localStorage.removeItem('appstudentkey');
      localStorage.removeItem('isRegistrat');
      router.push('/Login');
    }else{
      var token = JSON.parse(window.localStorage.getItem("appstudentinfo")).token;
      var data = qs.parse(config.data);
      var SignStr = getStr(
          data,
          localStorage.getItem("appstudentkey"),
          starttime,
          token
      )
    }
  }

  config.headers.common['lang'] = lang;
  config.headers.common['sign'] = SignStr;
  config.headers.common['token'] = token;
  config.headers.common['starttime'] = starttime;
  return config;
}, function (error) {
  // Do something with request error
  return Promise.reject(error);
});
var count = 0
// Add a response interceptor
http.interceptors.response.use(function (response) {
  // Do something with response data
  if(response.data.code===-40666){
      count = count + 1
      if (count == 1) {
        Toast(response.data.info)
      }
      window.localStorage.removeItem('appstudentinfo');
      window.localStorage.removeItem('appstudentkey');
      router.push('/Login');
    }else{
      return response.data;
    }
}, function (error) {
  // Do something with response error
  if(error.response){
    console.log(error.response.status);
    if(error.response.status === 400 ){
      this.$Message.error('数据错误！');
    }
    else if(error.response.status === 401 ){
      console.log(33333333333333333333333333333333333);
      // define window.app=vm in main.js
      window.app.$router.push({name: 'Login'});
      Toast({
        message: '网络连接失败，请检查网络',
        iconClass: 'icon icon-error',
        duration: '1800'
      });
    }
    else if(error.response.status === 403 ){
      this.$Message.error('您没有操作权限哦！');
    }
    else if(error.response.status === 404 ){
      this.$Message.error('对象不存在！');
    }
    else if(error.response.status === 500 ){
      this.$Message.error('服务器发生了一些错误哦！');
    }
  }
  return Promise.reject(error);
});

export default http;



