// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import '@/assets/common/common.css';   //全局属性
import '@/assets/common/reset.css';     //初始化属性
import Vue from 'vue'
import App from './App'
import store from '@/store';
import router from './router'
import 'lib-flexible'     //移动端自适应
// import Fastclick from 'fastclick';
import { JSEncrypt } from 'jsencrypt'; //登录加密
import Utils from './assets/js/utils';
import defaultCover from './assets/images/defaultClass.png';      //图片加载页面失败显示
import errorLogo from './assets/images/errorLogo.png';      //图片加载页面失败显示
// 图片懒加载
import VueLazyLoad from 'vue-lazyload'
Vue.use(VueLazyLoad,{
  error:defaultCover,
  loading:defaultCover,
  attempt: 3
});

// 表单验证
import VeeValidate,{ Validator } from 'vee-validate';
// import VeeValidate from 'vee-validate';
import zh_CN from 'vee-validate/dist/locale/zh_CN'
import VueI18n from 'vue-i18n';
Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'zh_CN',
})
Vue.use(VeeValidate, {
  i18n,
  i18nRootKey: 'validation',
  dictionary: {
    zh_CN
  }
});

const dictionary = {
  zh_CN: {
    messages:{
      required: (value) => '输入内容不能为空'
    }
  }
};

// Override and merge the dictionaries
Validator.localize(dictionary);


Validator.extend('phone', {
  getMessage: (field, [args]) => `请输入正确的手机号码`,
  validate: (value, [args]) =>{
    const reg = /^[0-9]{6,15}$/;
    return reg.test(value)
  }
});



Vue.prototype._imageError = function (e) { //列表缺省图
  e.target.src = errorLogo;
};
// 记录用户的uid
Vue.prototype.$uid = (JSON.parse(window.localStorage.getItem('appstudentinfo')) || {}).uid;
// 隐藏报错信息
Vue.config.productionTip = false;

// 300ms延迟点击
// if ('addEventListener' in document) {
//   document.addEventListener('DOMContentLoaded', function () {
//     Fastclick.attach(document.body);
//   }, false);
// }


// 是否已经登陆
const checkLogined = ()=>{
// 判断是否已经登陆
  if(
    localStorage.getItem('appstudentinfo') == null ||
    localStorage.getItem('appstudentinfo') == "" ||
    localStorage.getItem('appstudentkey') == null ||
    localStorage.getItem('appstudentkey') == ""
  ) {
    localStorage.removeItem('appstudentinfo');    //清楚用户缓存信息
    localStorage.removeItem('appstudentkey');     //清楚用户key值
    localStorage.removeItem('code');     //清楚用户key值
    // router.push('/Login');
    return false;
  }else{
    return true;
  }
};

// 微信授权验证
router.beforeEach((to, from, next) => {
  if (checkLogined()) {
    let ua = navigator.userAgent.toLowerCase();
    let code = localStorage.getItem('code');
    if (ua.match(/MicroMessenger/i) == 'micromessenger') {
      // 如果前往的是login  register  直接进行页面跳转 不做限制  ！/Author 避免死循环
      if (!code && (to.path == '/' || to.path == '/Home')) {
        // 微信需要授权的页面
        // let obj = {
        //   path:to.name,
        //   query:to.query
        // };
        // let objInfo = JSON.stringify(obj);
        // localStorage.setItem('beforeLoginUrl', objInfo);
        // 跳转授权页面
        next({path: '/Author'});
        return false;
      }
    }
  }

    if (checkLogined()) {
      if(to.path=='/Home'){
        next('/');
        return;
      }
      next();
    } else {
      if(to.path=='/Login'  || to.path=='/Register'){
        next()
      }else{
        next({path: 'Login'})
      }
    }

})





Vue.prototype.$loca = window.localStorage;        //全局local
Vue.prototype.$timeOut = null;        //全局定时器
Vue.prototype.$Utils = Utils;
Vue.prototype.$uid = (JSON.parse(window.localStorage.getItem('appstudentinfo')) || {}).uid;       //用户ID

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



