<template>
  <!--我的录播课详情-->
  <div>
    <div class="MyEndInfo">
      <!--返回导航-->
      <headerBack title="柠檬教育"></headerBack>
      <verticalScroll>
        <!--头部卡片-->
        <div v-if="selected=='2'" style="height:36px;background:rgba(255,255,255,1);"></div>
        <endInfoCard  v-if="selected=='1'" :data="contentInfo.courser"></endInfoCard>
        <nowInfoCard v-if="selected=='2'"  :data="contentInfo.courser"></nowInfoCard>
        <div v-if="selected=='2'" style="height:15px;background:rgba(255,255,255,1);"></div>
        <!--底部列表数据-->
        <endInfoList v-if="selected=='1'" :data="contentInfo.unit"></endInfoList>
        <lessonTime v-if="selected=='2'" :data="contentInfo.unit"></lessonTime>
      </verticalScroll>
    </div>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import verticalScroll from '@/components/VerticalScroll';   //better-scrollz
  import endInfoCard from '../components/EndInfoCard';      // 头部Card
  import lessonTime from '@/containers/Home/components/LessonTime';      //  底部录播课列表
  import endInfoList from '../components/EndInfoList';      //  底部直播课列表
  import nowInfoCard from '../components/NowInfoCard';      //  头部Card
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyEndInfo',
    components:{
      headerBack,
      endInfoCard,
      endInfoList,
      nowInfoCard,
      lessonTime,
      verticalScroll
    },
    data(){
      return {
        courseid:'',      //课程ID
        selected:'',
        contentInfo:{},     //全部数据
        nowInfo:[
          ]
      }
    },
    mounted(){

    },
    created(){
      // 记录课程ID
      this.courseid = this.$route.query.courseid;       //课程ID
      this.schedulingid = this.$route.query.schedulingid;   //班级ID
      this.selected = this.$route.query.selected;       //课程类别
      // 请求数据
      this.getClassSchedule();
    },
    methods:{
      getClassSchedule(){
        console.log(this.selected);
        console.log(this.schedulingid);
        console.log(this.courseid);
        Server.post(Path.getClassSchedule,{
          'coursetype':this.selected,'schedulingid':this.schedulingid,'courseid':this.courseid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            this.contentInfo = data;
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    }

  }
</script>
<style lang="scss" scoped>
  .MyEndInfo{
    background:rgba(255,255,255,1);
    box-sizing: border-box;
  }
</style>



