<template>
  <div class="CollectionButton">
    <div class="collection"  v-if="!is_collect"  @click="selectButton('collection')">
      <div class="imgBg"></div>
      <div class="imgTitle" style="color:orange">
        收藏
      </div>
    </div>
    <div class="collection" v-if="is_collect">
      <div class="imgShow"></div>
      <div class="imgTitle">
        已收藏
      </div>
    </div>
    <div class="price">
      ￥{{price}}
    </div>
    <div v-if="applyStatus===1" class="signUp" @click="selectButton('signUp')">
      立即报名
    </div>
    <div v-if="applyStatus===0" class="signUp" >
      不可报名
    </div>
  </div>
</template>
<script>
  export default{
    name:'CollectionButton',
    props:{
      price:{   //课程价格
        type:String,
        default(){
          return '22';
        }
      },
      applyStatus:{     //0不可报名，1可报名
        type:Number,
        default(){
          return 1;
        }
      },
      is_collect:{
        type:Number,
        default(){
          return 0;
        }
      }
    },
    methods:{
      selectButton(value){
        this.$emit('selectButton',value);//select事件触发后，自动触发showCityName事件
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .CollectionButton{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 110px;
    .collection{
      width:20%;
      height:110px;
      float:left;
      background:rgba(255,255,255,1);
      .imgShow{
        height:42px;
        width:42px;
        margin:19px auto 0px;
        @include bg-image("~@/assets/images/icon_starBlue");
      }
      .imgBg{
        height:42px;
        width:42px;
        margin:19px auto 0px;
        @include bg-image("~@/assets/images/icon_starBorder");
      }
      .imgTitle{
        height:50px;
        line-height: 50px;
        text-align: center;
        font-size:26px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(47,172,250,1);
      }
    }
    .price{
      width:40%;
      height:110px;
      line-height:110px;
      text-align: center;
      float:left;
      background:rgba(255,176,24,1);
      font-size:32px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(255,255,255,1);
    }
    .signUp{
      width:40%;
      height:110px;
      line-height:110px;
      text-align: center;
      float:left;
      background:rgba(47,172,250,1);
      font-size:32px;
      font-family:PingFang-SC-Medium;
      font-weight:500;
      color:rgba(255,255,255,1);
    }
  }
</style>



