<template>
  <!--精彩瞬间列表-->
  <div class="MarvellousList">
    <div class="item" v-for="item in data"  @click="toPage()">
      <div class="contentOut">
        <div class="imgShow"></div>
        <div class="contentInfo">
          <div class="title">
            {{item.title}}
          </div>
          <div class="subTitle">
            {{item.subTitle}}
          </div>
          <div class="teacher">
            {{item.teacher}}
            <div style="float:right">
              {{item.date}}
            </div>
          </div>
        </div>
        <div class="icon">
          <span></span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'MarvellousList',
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    methods:{
      toPage(){
        this.$router.push({path:'/MarveInfo'});
      }
    }
  }
</script>
<style lang="scss" scoped>
  .MarvellousList{
    .item{
      box-sizing:border-box;
      height:160px;
      background:rgba(255,255,255,1);
      padding:25px 40px 25px 30px;
      border-bottom:1px solid ghostwhite;
      margin-top:5px;
      .contentOut{
        height:110px;
        .imgShow{
          width:147px;
          height:110px;
          float:left;
          background:red;
          margin-right:20px;
        }
        .contentInfo{
          width:480px;
          float:left;
          .title{
            height:48px;
            font-size:30px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(51,51,51,1);
          }
          .subTitle{
            height:37px;
            font-size:20px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(51,51,51,1);
            line-height:24px;
          }
          .teacher{
            font-size:20px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(51,51,51,1);
            line-height:24px;
          }
        }
        .icon{
          float:right;
          width:25px;
          height:147px;
          line-height:147px;
          text-align: right;
          span{
            display:inline-block;
            width:21px;
            height:34px;
            background:red;
          }
        }
      }
    }
  }
</style>



