<template>
  <div id="LessonClass">
    <div id="headerBack">
      <div class="Headbar-back" @click="closeModal">
      </div>
      <div class="Headbar-title">课程筛选</div>
      <!--<span class="confirmArea" @click="handleArea()" v-if="confirmFlag">确定</span>-->
    </div>
	<div class="classContent">
		<div class="classLv" v-if="twoInfo.length>0">
			<div class="title">
				<p class="classSymbol"></p>
				<p class="classP">{{fatherName}}</p>
			</div>
			<div style="margin-right:-92px;">
        <ul class="classUl">
          <li :class="{'classLi':twoIndex!==key,'classLiNow':twoIndex===key}" v-for="(item,key) in twoInfo" :key="key"  @click="selectTwo(item.category_id,key,item.child,item.categoryname)">{{item.categoryname}}</li>
        </ul>
      </div>
		</div>
		<div class="classLv" v-if="threeInfo.length>0">
			<div class="title">
				<p class="classSymbol"></p>
				<p class="classP">{{twoName}}</p>
			</div>
      <div style="margin-right:-92px;">
        <ul class="classUl">
          <li class="classLi"v-for="(item,key) in threeInfo" :key="key" @click="selectThree(item.category_id)">{{item.categoryname}}</li>
        </ul>
      </div>
		</div>
	</div>
    <notInfo v-if="infoStatus">
      抱歉，没有指定分类数据
    </notInfo>
</div>
</template>

<script>
  import classBack from '@/components/assets/nav_icon_back@2x.png';
  import notInfo from '@/components/NotInfo';     //返回组件
  import Server from '@/service/request';
  import Path from '@/service/Path';
    export default {
	    name: "LessonClass",
	    components: {
        notInfo
	    },
      props:{
        category_id:{
          type:Number,
          default(){
            return 0;
          }
        },
        categoryname:{
          type:String,
          default(){
            return '';
          }
        }
      },
	    data() {
	      return {
          categoryID:'',   //类别ID
          twoInfo:[],       //二级信息
          fatherName:'',   //一级的类名
          twoIndex:'',    // 二级选中id
          threeInfo:[],   //三级数据
          twoName:'',   //三级头

          infoStatus:true,  //没有数据不显示
	        SearchText: '',
	        SearchResult: [],
	        ResultInfo: '',
	        organid: this.$oid,
	        pagenum: 1,
	        limit: 10,
	        searchHistoy: [],
	        isShowNoResult: false,
	        classBack,
	      	title:['作文','诗词','阅读','句子','词语','课程']
	      }
    	},
    	methods:{
	      // 选择二级并请求数据
        selectTwo(category_id,value,arr,name){
          if(arr.length==0){
            // 关闭模态框
            this.closeModal();
            // 传入查询ID
            this.$emit('changeID',category_id);
          }else{
            // 记录三级头
            this.twoName = name;
            // 记录查询ID
            this.categoryID = category_id;
            this.twoIndex = value;
            // 请求三级数据
            this.getTopCategoryChild();
          }
        },
        // 选择三级分类
        selectThree(category_id){
          // 关闭模态框
          this.closeModal();
          // 传入查询ID
          this.$emit('changeID',category_id);
        },
	      // 关闭页面
        closeModal(){
          // this.infoStatus = false;
          this.twoIndex = '';
          this.threeInfo = [];
    			 this.$emit('closeModal');
    		},
    		// 获取分类数据
        getTopCategoryChild(){
          this.infoStatus = false;
          Server.post(Path.getTopCategoryChild, {
            'categoryid':this.categoryID
          }, (res) => {
            if (res.code === 0) {
              if(this.twoInfo.length==0){
                if(res.data.length==0){
                  this.infoStatus = true;
                }
                // 二级数据
                this.twoInfo = res.data;
              }else{
                // 三级数据
                this.threeInfo = res.data;
              }

            }
          });
        }
    	},
      watch:{
        category_id:function(value){
          // 记录查询的ID
          this.categoryID = value;
          // 清空二级信息
          this.twoInfo = [];
          // 查询分类
          this.getTopCategoryChild();
        },
        categoryname:function(value){
          // 记录父名
          this.fatherName = value;
        }
      }
    }
</script>

<style scoped lang="scss">
  @import "~@/assets/common/variable";
  @import "~@/assets/common/mixin";
  #LessonClass{
  	@extend .fullPosition;
    #headerBack {
      z-index: 3;
      position: relative;
      height: 92px;
      line-height: 92px;
      font-size: $font-size-36;
      text-align: center;
      background-color: $color-white;
      color: $color-black;
      @extend .boxShadow;
      .Headbar-back {
        position: absolute;
        left: 17px;
        top: 20px;
        display: inline-block;
        width: 48px;
        height: 48px;
        @include bg-image('../assets/nav_icon_back');
        background-repeat: no-repeat;
        background-size: cover;
      }
    }
  }
.heade{
  height: 96px;
 	border-bottom:1px solid rgba(217,217,217,1);
 	position: relative;
}
.classBack{	
	width:36px;
	height:27px;
	position: absolute;
	top: 41px;
	left:36px;
	z-index: 99;
}
.headTitle{
	display: inline-block;
	font-size:34px;
	height:34px;
	margin-top: 39px;
	margin-left: 107px;
	line-height: 1;
  color:rgba(61,61,61,1);
}
.classContent{
	height: 100%;
	background: #FFFFFF;
	padding: 44px 66px 0 36px;
}
.classLv{
	padding-bottom: 56px;
}
.title{
	height: 30px;
	line-height: 30px;
	position: relative;
}
.classSymbol{
	display: inline-block;
	line-height: 1;
	width:8px;
	height:8px;
	float: left;
	margin-top: 12px;
	background:rgba(77,77,77,1);
	border-radius:50%;
}
.classP{
	display: inline-block;
	line-height: 1;
	color:rgba(77,77,77,1);
	font-size: 32px;
	margin-left: 22px;
}
.classUl{
	margin-left: 22px;
} 

.classLi{
	float:left;
	width:148px;
  overflow: hidden;
	background:rgba(111,209,171,1);
	opacity:0.65;
	border-radius:38px;
	color:rgba(255,255,255,1);
	font-size:30px;
	line-height: 76px;
	text-align:center;
  margin-right:92px;
	margin-top: 36px;
}
.classLiNow{
  float:left;
  width:148px;
  background:white;
  opacity:0.65;
  border-radius:38px;
  color:orange;
  border:1px solid orange;
  font-size:30px;
  line-height: 76px;
  text-align:center;
  margin-right:92px;
  box-sizing: border-box;
  margin-top: 36px;
}
</style>



