<template>
  <div >
    <transition :name="animateType">
      <slot></slot>
    </transition>
  </div>
</template>
<script >
  export default{
    props:{
      animateType:{
        type:String,
        default(){
          return 'fade'
        }
      }
    }
  };
</script>
<style lang="scss">
  //fade(过渡)
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s;
  }
  .fade-enter,
  .fade-leave-to {
    opacity: 0;
  }
  //bounce(动画)
  .bounce-enter-active {
    animation: bounce-in 0.5s;
  }
  .bounce-leave-active {
    animation: bounce-in 0.5s reverse;
  }
  @keyframes bounce-in {
    0% {
      transform: scale(0);
    }
    50% {
      transform: scale(1.5);
    }
    100% {
      transform: scale(1);
    }
  }
  //slide(上下滑动)
  .slide-enter-active,
  .slide-leave-active {
    transition: all 0.3s;
  }

  .slide-enter,
  .slide-leave-to {
    transform: translate3d(0, 100%, 0);
  }

  //slidelr(左右滑动)
  .slidelr-enter-active,
  .slidelr-leave-active {
    transition: all 0.3s;
  }

  .slidelr-enter,
  .slidelr-leave-to {
    transform: translate3d(100%, 0, 0);
  }
</style>



