<template>
  <div>
    <div class="videoControl">
      <video :src="src"  controls height="100%"   style="object-fit: fill;" loop autoplay></video>
    </div>

  </div>
</template>
<script>
  export default{
    name:'videoControl',
    props:{
      src:{
        type:String,
        default(){
          return '';
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  .videoControl{
    height:403px;
    text-align: center;
  }

</style>



