<template>
  <div class="MealList">
    <div class="item"  @click="toMealDetail(data.packageid)">
      <div class="imgShow">
        <img v-lazy="data.setimgpath" width="100%" height="100%" alt="">
      </div>
      <div class="infoContent">
        <div class="title text-ellipsis">
          {{data.setmeal}}
        </div>
        <div class="date">
          有效期至:{{formatDateTime(data.effendtime)}}
          <div class="alreadyUse"  v-if="type==='1'">
            <img src="~@/assets/images/alreadyUse.png" width="100%" height="100%" alt="">
          </div>
        </div>
        <div class="useNow"   @click="toPage" v-if="type==='0'">
          立即使用
        </div>
      </div>
      <!--底边-->
      <div class="borderBottom">
      </div>
      <!--快过期-->
      <!--<div class="rightTop"  v-if="type==='0' && (data.effendtime-data.effstarttime)<24*60*60*1000">-->
        <!--快过期-->
      <!--</div>-->
    </div>
  </div>
</template>
<script>
  export default{
    name:'MealList',
    props:{
      data:{
        type:Object,
        default(){
          return {};
        }
      },
      type:{
        type:String,
        default(){
          return '0';
        }
      }
    },
    methods:{
      // 套餐详情页
      toMealDetail(packageid){
        this.$router.push({'path':'MealInfo',query:{'packageid':packageid,from:'order'}})
      },
      // 前往所有课程页
      toPage(){
        this.$router.push('/AllCourses');
      },
      formatDateTime(timeStamp) {
        var date = new Date();
        date.setTime(timeStamp * 1000);
        var y = date.getFullYear();
        var m = date.getMonth() + 1;
        m = m < 10 ? ('0' + m) : m;
        var d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        var h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        var minute = date.getMinutes();
        var second = date.getSeconds();
        minute = minute < 10 ? ('0' + minute) : minute;
        second = second < 10 ? ('0' + second) : second;
        return y + '/' + m + '/' + d;
      },
    }
  }
</script>
<style lang="scss" scoped>
  .MealList{
    .item{
      height:220px;
      box-sizing: border-box;
      padding:30px 24px 0px 24px;
      background:rgba(255,255,255,1);
      overflow:hidden;
      position:relative;
      .rightTop{
        width:160px;
        height:30px;
        line-height: 30px;
        background:rgba(63,156,232,1);
        text-align: center;
        font-size:20px;
        position:absolute;
        top:0px;
        right:-60px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(255,255,255,1);
        transform: rotate(45deg);
        transform-origin:20% 0%;
      }
      /*底边*/
      .borderBottom{
        width: calc(100% - 310px);
        height:0px;
        position:absolute;
        right:0px;
        bottom:0px;
        background:rgba(166,208,244,1);
        border-bottom:1px solid rgba(217,217,217,1);
      }
      .imgShow{
        width:280px;
        height:160px;
        float:left;
        overflow:hidden;
        border-radius:8px;
        margin-right:20px;
      }
      .infoContent{
        width: calc(100% - 300px);
        float:left;
        .title{
          height:32px;
          font-size:32px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(51,51,51,1);
        }
        .date{
          padding-top:19px;
          box-sizing: border-box;
          height:77px;
          font-size:24px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(128,128,128,1);
          position:relative;
          .alreadyUse{
            width:100px;
            height:100px;
            position:absolute;
            backgrand:red;
            right:0px;
            top:0px;
          }
        }
        .useNow{
          width:160px;
          height:50px;
          line-height:50px;
          float:right;
          text-align: center;
          background:rgba(63,156,232,1);
          border-radius:10px;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(255,255,255,1);
        }
      }
    }
  }
</style>



