<template>
  <!--支付反馈-->
  <div class="paymentBack">
    <headerBack title="支付反馈"></headerBack>
    <div class="infoContent">
      <div class="imgShow"></div>
      <div class="title">
        支付成功
      </div>
      <div class="subTitle">
        订单已支付成功，可以开始学习了！
      </div>
        <div class="homeButton" @click="toHome">
          返回首页
        </div>
    </div>
  </div>

</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  export default{
    name:'paymentBack',
    components:{
      headerBack
    },
    methods:{
      toHome(){
        this.$router.push('/');
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";

  .paymentBack{
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    background:rgba(255,255,255,1);
    .infoContent{
      padding:108px 37px 0px 37px ;
      .imgShow{
        width:168px;
        height:168px;
        margin:0 auto;
        margin-bottom:44px;
        @include bg-image("~@/assets/images/icon_paymentSuccess");
      }
      .title{
        text-align: center;
        height:108px;
        width:100%;
        font-size:38px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(57,173,239,1);
      }
      .subTitle{
        height:152px;
        text-align: center;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(77,77,77,1);
      }
      .homeButton{
        width:676px;
        height:108px;
        line-height:108px;
        margin:0px auto;
        text-align: center;
        background:rgba(57,173,239,1);
        border-radius:10px;
        font-size:34px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(254,255,255,1);
      }
    }
  }
</style>



