<template>

    <div class="SelectBackground">
      <headerBack title="更换背景">
        <div class="buttonOk"  @click="changeSigninImage">
          确定
        </div>
      </headerBack>

      <bScroll
        class="wrapper"
        ref="scroll"
      >
        <div class="BackgroundList">
          <div style="margin-right:-31px;">
            <ul>
              <li v-if="bgInfo.length!=0"  :class="{'classLi':keyIndex!==key,'classOk':keyIndex===key}" v-for="(item,key) in bgInfo" :key="key" @click="keyIndex=key">
                <img v-lazy="'http://'+item.imageurl" height="100%" width="100%" alt="">
              </li>
              <!--<li class="upLoad">上传</li>-->
            </ul>
          </div>
        </div>
      </bScroll>
      <notInfo v-if="infoStatus">
        抱歉，没有可选背景图
      </notInfo>
    </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import bScroll from '@/components/Lib/BScroll';           //bScroll
  import notInfo from '@/components/NotInfo';           //没有数据
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'SelectBackground',
    components:{
      headerBack,
      bScroll,
      notInfo
    },
    data(){
      return {
        infoStatus:false,
        bgInfo:[],  //背景图列表
        keyIndex:-2,   //默认选中
      }
    },
    created(){
      // 请求背景图列表
      this.getSigninbgiList();
    },
    methods:{
      // 更换背景
      changeSigninImage(){
        Server.post(Path.changeSigninImage,{
          'signinimageid':this.key!=-2?this.bgInfo[this.keyIndex].id:''
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$router.go(-1);
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 获取背景列表
      getSigninbgiList(){
        Server.post(Path.getSigninbgiList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length!=0){
              this.bgInfo = data.data;
              for(let item of data.data){
                item.status = false;
              }
            }else{
              this.infoStatus = true;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .buttonOk{
    height:92px;
    margin-right:20px;
    line-height:92px;
    float:right;
  }
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .BackgroundList{
    padding:30px 23px 0px 23px;
    .classLi{
      width:214px;
      height:316px;
      text-align: center;
      background:rgba(179,179,179,1);
      margin-right:31px;
      margin-bottom:31px;
      float:left;
      color:white;
      font-size:30px;
    }
    .classOk{
      width:214px;
      height:316px;
      box-sizing: border-box;
      border:10px solid orange;
      text-align: center;
      background:rgba(179,179,179,1);
      margin-right:31px;
      margin-bottom:31px;
      float:left;
      color:white;
      font-size:30px;
    }

    .upLoad{
      width:214px;
      height:316px;
      line-height:316px;
      text-align: center;
      background:rgba(179,179,179,1);
      margin-right:31px;
      margin-bottom:31px;
      float:left;
      color:white;
      font-size:30px;
    }
  }
  .SelectBackground{
    background:rgba(255,255,255,1);
  }
</style>



