<template>
  <!--个人信息编辑-->

  <div class="DataEditing">
    <!--头部导航-->
    <headerBack title="编辑资料"  v-if="from=='register'" typeStatus="tittle" rightTitle="下一步"   @clickMe="toPage('top')"></headerBack>
    <headerBack title="编辑资料"  v-if="from=='mineCard'" typeStatus="tittle" ></headerBack>
    <bScroll
      class="wrapper"
      ref="scroll"
    >
    <div>
      <div class="infoContent">
        <!--图片Item-->
        <div class="imgItem" >
          头像
          <div class="imgOut">
            <img :src="imgUrl"  @error="_imageError" width="100%" height="100%" alt="">
          </div>
        </div>
        <!--昵称Item-->
        <div class="itemItem" @click="toAutograph('nickname',nickname)">
          昵称
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">{{nickname}}</div>
          </div>
        </div>
        <!--性别-->
        <div class="itemItem"  @click="openToast">
          性别
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis" v-if="sex==1">男</div>
            <div class="titleSpan text-ellipsis" v-if="sex==2">女</div>
          </div>
        </div>

        <!--生日-->
        <div class="itemItem" @click="openPicker">
          生日
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">{{birth}}</div>
          </div>
        </div>
        <!--地区-->
        <div class="itemItem"  @click="popupCountry=true;">
          地区
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">
              <!--<span style="padding-right:3px;">{{countryName}}</span>-->
              <!--<span style="padding-right:3px;">{{provinceName}}</span>-->
              <!--<span style="padding-right:3px;">{{cityName}}</span>-->
              <span style="padding-right:3px;">{{getCity(country)}}</span>
              <span style="padding-right:3px;">{{getCity(province)}}</span>
              <span style="padding-right:3px;">{{getCity(city)}}</span>
            </div>
          </div>
        </div>
        <!--地址管理-->
        <router-link to="/AddressSelect">
          <div class="itemItem"   v-if="from=='mineCard'">
            地址管理
            <div class="rightTitle ">
              <div class="rightIcon">
                <span class="iconSpan">
                </span>
              </div>
              <div class="titleSpan text-ellipsis" v-if="addressNow">
                {{addressNow.pname}} {{addressNow.cname}}  {{addressNow.aname}}
              </div>
            </div>
          </div>
        </router-link>
        <!--手机号-->
        <div class="itemItem" @click="toPhone"  v-if="from=='mineCard'">
          手机号
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">
              +<span>{{prphone}}</span>
              {{mobile}}
            </div>
          </div>
        </div>

        <!--修改密码-->
        <div class="itemItem" v-if="from=='mineCard'" @click="changePass">
          修改密码
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
          </div>
        </div>
        <!--个性签名-->
        <div class="itemItem"   @click="toAutograph('autograph',profile)">
          个性签名
          <div class="rightTitle ">
            <div class="rightIcon">
              <span class="iconSpan"></span>
            </div>
            <div class="titleSpan text-ellipsis">{{profile}}</div>
          </div>
        </div>
      </div>
      <!--<div class="saveButton" v-if="from=='mineCard'"  @click="toPage">-->
        <!--保存-->
      <!--</div>-->
      <div style="height:40px;"></div>
    </div>
    </bScroll>
    <middleToast v-if="toastStatus==true">
      <div class="toastDiv">
        <div class="sex">
          性别
        </div>

        <div class="radio">
          <div class="sexName">
            男
          </div>
          <div class="sexMan"></div>
          <div @click="changeSex(1)"  :class="{'buttonNone':sex!=1,'buttonOk':sex==1}" ></div>
        </div>
        <div class="radio" style="border:none;">
          <div class="sexName">
            女
          </div>
          <div class="sexWoman"></div>
          <div @click="changeSex(2)"  :class="{'buttonNone':sex!=2,'buttonOk':sex==2}"></div>
        </div>
      </div>
    </middleToast>
    <!--国家选择-->
    <mt-popup v-model="popupCountry" position="right" style="width:100%;height:100%;">
      <country :popupVisible="popupCountry"  @closeModal="closeCountry"  @selectCountry="selectCountry"></country>
    </mt-popup>
    <!--省选择-->
    <mt-popup v-model="popupProvince" position="right" style="width:100%;height:100%;">
      <province  :popupVisible="popupProvince" @provinceOk="provinceOk"  :countryID="country"  @selectProvince="selectProvince"  @closeModal="closeProvince"></province>
    </mt-popup>
    <!--城市选择-->
    <mt-popup v-model="popupCity" position="right" style="width:100%;height:100%;">
      <city  :popupVisible="popupCity"  :provinceID="province"  @selectCity="selectCity"  @closeModal="closeCity"></city>
    </mt-popup>

    <!--个人简介 和昵称修改-->
    <mt-popup v-model="autographStatus" position="right" style="width:100%;height:100%;">
      <autograph @closeModal="closeAuto"  @saveValue="saveValue" :maxLength="maxLength" :value="autoValue"></autograph>
    </mt-popup>
    <!--生日-->
    <mt-datetime-picker class="mydatepicker" ref="picker" type="date"
                        v-model="value"
                        :year-format="`{value}年 `"
                        :month-format="`{value}月 `"
                        :date-format="`{value} 日`"
                        :startDate="startDate"
                        :endDate="endDate"
                        @confirm="confirmDate">
    </mt-datetime-picker>
  </div>

</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import middleToast from '@/components/MiddleToast';     //头部返回导航
  import country from '@/components/Country';     //国家三级联动
  import province from '@/components/Province';     //国家三级联动
  import city from '@/components/City';     //国家三级联动
  import autograph from '../Autograph';     //昵称和个人简介
  import bScroll from '@/components/Lib/BScroll';           //bScroll
  import {Popup,Toast,DatetimePicker} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import CityJSON from '@/assets/city/city.json';
  export default{
    name:'DataEditing',
    components:{
      bScroll,
      headerBack,
      middleToast,
      country,
      autograph,
      province,
      city,
      'mt-datetime-picker': DatetimePicker,
      'mt-popup':Popup
    },
    data(){
      return {
        autographStatus:false,  //昵称模态框
        maxLength:'',           //最多可以写几个字
        autoValue:"",            //输入的值
        from:"",        //页面判断
        studentid:'',       //学生ID
        popupCountry:false,    //选择国家
        popupProvince:false,   //选择省份
        popupCity:false,    //选择城市
        toastStatus:false,  //性别选择弹框默认关闭
        imgUrl:"",        //图片地址
        addressNow:{},   //当前的地址

        country:'',       //国家的ID
        province:'',      //省份的ID
        city:'',          //城市ID
        value:'',
        startDate: new Date('1960-01-01'),
        endDate: new Date(),
        sex:1,
        profile:'',   //个性签名
        nickname:'',      //昵称
        prphone:"86",
        mobile:'',
        birth: "",
      }
    },
    mounted(){

    },
    created(){
      this.from = this.$route.query.from;
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 请求个人信息
      this.getStudentInfo();
      // 查询收货地址
      this.getAddressList();
    },
    methods:{
      // 查找收货地址
      getAddressList(){
        Server.post(Path.getAddressList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 如果有地址就显示地址
            if(data.length>0){
                // 遍历所有的数据找到默认的地址
                for(let item of data){
                  if(item.isdefault==1){
                    this.addressNow = item;
                  }
                }

            }else{
              // 改变显示的样式
              this.noneAddress = true;
            }
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      // 获取个人信息
      getStudentInfo(){
        Server.post(Path.getStudentInfo,{
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
              this.country=data.country;   //国家的ID
              this.province= data.province;    //省份的ID
              this.city= data.city;        //城市ID
              this.sex=data.sex;
              this.profile= data.profile; //个性签名
              this.nickname= data.nickname;     //昵称
              this.imgUrl =  data.imageurl;     //昵称
              this.prphone=data.prphone;
              this.mobile=data.mobile;
              this.birth=data.birth;
          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });
      },
      /**
       * *   日期选择开始
       */
      // 打开日期选择
      openPicker() {
        this.$refs.picker.open();
      },
      confirmDate(val) {
        this.birth = this.formatDate(val);
        this.toPage('');
      },
      formatDate(date) {
        let y = date.getFullYear();
        let m = date.getMonth() + 1;
        m = m < 10 ? "0" + m : m;
        let d = date.getDate();
        d = d < 10 ? "0" + d : d;
        return y + "-" + m + "-" + d;
      },


      /**
       * *   城市联动开始
       */
      //城市转换
      getCity: function(val) {
        if(val == 0 || val == null){
          return
        }else{
          CityJSON.map((el,index) => {
            if(el.id == val){
              val =el.name
            }
          })
          return val;
        }
      },
      // 关闭国家的选择器
      closeCountry(){
        this.popupCountry = false;
      },
      // 关闭省份的选择
      closeProvince(){
        this.popupProvince = false;
      },
      // 关闭城市的选择器
      closeCity(){
        this.popupCity = false;
      },
      // 选择的国家ID
      selectCountry(value){
        // 记录选择的国家ID
        this.country = value;
        // 打开省份选择
        this.popupProvince = true;
      },
      // 选择的省份ID
      selectProvince(value){
        // 记录选择的省份ID
        this.province = value;
        // 打开城市选择
        this.popupCity = true;
      },
      // 选择城市
      selectCity(value){
        // 记录选择的城市
        this.city = value;
        this.popupCity = false;
        this.popupProvince = false;
        this.popupCountry = false;
        this.toPage('');
      },
      provinceOk(value){
        this.province = value;
        this.city = 0;
        this.popupProvince = false;
        this.popupCountry = false;
        this.toPage('');
      },

      /**
       * *   性别选择开始
       */
      // 性别修改
      openToast(){
        this.toastStatus = true;
      },
      // 改变性别
      changeSex(sex){
        if(sex!=this.sex){
          this.sex = sex;
        }
        this.toastStatus = false;
        this.toPage('');
      },
      /**
       * *   昵称 个性签名开始
       */
      // 个性签名 昵称修改
      toAutograph(type,value){
        if(type=='nickname'){
          this.maxLength = 10;
        }else{
          this.maxLength = 100;
        }
        this.autoValue = value;
        this.autographStatus = true;
      },
      // 关闭个性签
      closeAuto(){
        this.autographStatus = false;
      },
      // 保存数据
      saveValue(value){
        if(this.maxLength==10){
          this.nickname = value;
        }else{
          this.profile = value;
        }
        this.autographStatus = false;
        this.toPage('');
      },
      /**
       * *   修改密码
       */
      // 下一步
      toPage(value){
        Server.post(Path.updateStudentInfo,{
          'imageurl':'url',
          'nickname':this.nickname,
          'sex':this.sex,
          'country':this.country,
          'province':this.province,
          'city':this.city,
          'birth': this.birth,
          'profile':this.profile,
          'studentid':this.studentid
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            if(this.from=='mineCard'){
              Toast({
                message:'修改成功',
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }else{
              if(value=='top'){
                this.$router.push('/CourseScreening');
              }
            }

          }else{
            Toast({
              message:res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        });

      },


      // 修改手机号
      toPhone(){
        this.$router.push({path:'/ChangePhone',query:{'prphone':this.prphone,'mobile':this.mobile}});
      },
      // 修改密码
      changePass(){
        this.$router.push({path:'/ChangePass',query:{'prphone':this.prphone,'mobile':this.mobile}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0px;
    background:rgba(255,255,255,1);
  }
  .saveButton{
    width:300px;
    height:88px;
    line-height:88px;
    text-align: center;
    background:rgba(57,173,239,1);
    border-radius:8px;
    font-size:32px;
    font-family:PingFang-SC-Medium;
    font-weight:500;
    color:rgba(255,255,255,1);
    margin:72px auto 0px;
  }
  .DataEditing{
    background:rgba(255,255,255,1);
    /*自定义模态弹框*/
    .toastDiv{
      width:548px;
      box-sizing: border-box;
      background:rgba(255,255,255,1);
      border-radius:12px;
      padding:47px 64px 60px 62px;
      .sex{
        height:61px;
        text-align: center;
        font-size:40px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
      }
      .radio{
        height:120px;
        box-sizing: border-box;
        border-bottom:1px solid rgba(217,217,217,1);
        padding-left:5px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        .sexName{
          height:119px;
          width:90px;
          float:left;
          line-height:119px;
          margin-right:32px;
        }
        .sexMan{
          height:34px;
          width:34px;
          margin-top:43px;
          float:left;
          @include bg-image("../assets/icon_man");
        }
        .sexWoman{
          height:34px;
          width:34px;
          margin-top:43px;
          float:left;
          @include bg-image("../assets/icon_woman");
        }
        .buttonNone{
          height:34px;
          width:34px;
          margin-top:43px;
          float:right;
          @include bg-image("../assets/icon_buttonNone");
        }
        .buttonOk{
          height:34px;
          width:34px;
          margin-top:43px;
          float:right;
          @include bg-image("../assets/icon_buttonOk");
        }

      }
    }
    .infoContent{
      padding:0px 41px 0px 39px;
      /*字体显示Item*/
      .itemItem{
        height:118px;
        line-height:118px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(61,61,61,1);
        border-bottom:1px solid rgba(217,217,217,1);
        /*右侧信息展示*/
        .rightTitle{
          height:118px;
          line-height: 118px;
          width:380px;
          float:right;
          text-align: right;
          .titleSpan{
            width:350px;
            float:right;
            height:118px;
            line-height: 118px;
            font-size:30px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(128,128,128,1);
            span{
              font-size:30px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(128,128,128,1);
            }
          }
          .rightIcon{
            width:24px;
            height:118px;
            line-height: 118px;
            float:right;
          }
          .iconSpan{
            font-size:0px;
            display:inline-block;
            width:12px;
            height:24px;
            @include bg-image("~@/assets/images/icon_right");
            background-size:100% 100%;
            margin-left:2px;
          }
        }
      }
      /*图片显示Item*/
      .imgItem{
        height:170px;
        line-height:170px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(61,61,61,1);
        border-bottom:1px solid rgba(217,217,217,1);
        /*图片外层*/
        .imgOut{
          width:120px;
          height:120px;
          line-height:120px;
          float:right;
          overflow:hidden;
          margin-top:25px;
          border-radius:50%;
        }
      }
    }
  }
</style>



