<template>
  <!--套餐列表-->
  <div class="MealList">
    <headerBack title="套餐列表"></headerBack>
    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore">
        <ul>
          <li class="FullItem" v-for="(info,key) in dataInfo"   :key="key"  @click="toMealInfo(info.packageid)">
            <div class="itemInfo" :style="{'border-bottom':key==dataInfo.length-1?'none':'1px solid rgba(217,217,217,1)'}">
              <div class="imgShow">
                <img v-lazy="info.setimgpath" width="100%" height="100%" alt="">
              </div>
              <div class='infoContent'>
                <div class="title">
                  {{info.setmeal}}
                </div>
                <div class="teacher" :class="{'hidden':true}">
                </div>
                <div class="priceOut">
                  <div class="priceRight">
                    ￥{{info.setprice}} 元
                  </div>
                </div>
              </div>
            </div>
          </li>
        </ul>
      </mt-loadmore>
    </div>
    <notInfo v-if="infoStatus">
      抱歉，没有套餐数据
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import notInfo from '@/components/NotInfo';     //返回组件
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import { Loadmore } from 'mint-ui';
  export default{
    name:"MealList",
    components:{
      headerBack,
      'mt-loadmore':Loadmore,
      notInfo
    },
    data(){
      return {
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
        pagenum:1,  //默认加载第一页
        dataInfo:[]     //数组集合
      }
    },
    created() {
      // 获取轮播图
      this.getPackageList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.getPackageList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.getPackageList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 前往套餐详情页
      toMealInfo(id){
        this.$router.push({'path':'MealInfo',query:{'packageid':id}})
      },
      getPackageList(){
        // 获取列表数据
        Server.post(Path.getPackageList, {
        'pagenum':this.pagenum
        }, (res) => {
          if (res.code === 0) {
            if(res.data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              //页码不为一，追加
              this.dataInfo = this.dataInfo.concat(res.data.data);
            } else {
              //否则从第一页开始，覆盖列表数据
              this.dataInfo = res.data.data;
            }
          }
        });
      }
    }

  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MealList{
    .infoShow{
      padding:30px 20px;
      text-align: center;
      height:200px;
      line-height:200px;
      font-size:28px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(102,102,102,1);
      background:rgba(255,255,255,1);
    }
    .FullItem{
      height:216px;
      padding:28px 24px 28px 24px;
      background:rgba(255,255,255,1);
      .itemInfo{
        height:216px;
        box-sizing: border-box;
        /*border-bottom:1px solid rgba(217,217,217,1);*/
        .imgShow{
          width:280px;
          height:160px;
          border-radius:8px;
          float:left;
          overflow:hidden;
          margin-right:34px;
        }
        .infoContent{
          float:left;
          width:388px;
          height:160px;
          .title{
            height:60px;
            font-size:30px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            color:rgba(51,51,51,1);
          }
          .teacher{
            height:67px;
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(102,102,102,1);
          }
          .hidden{
            visibility: hidden
          }
          .priceOut{
            height:28px;
            .priceLeft{
              margin-top:3px;
              float:left;
              height:22px;
              line-height:22px;
              font-size:28px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(235,31,21,1);
            }
            .priceRight{
              margin-top:3px;
              float:right;
              height:22px;
              margin-right:25px;
              line-height:22px;
              font-size:28px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(235,31,21,1);
            }
            .rightBlock{
              width:62px;
              height:28px;
              text-align: center;
              line-height:28px;
              float:right;
              background:rgba(255,152,106,1);
              border-radius:4px;
              font-size:20px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(255,255,255,1);
              .video{
                font-size:16px;
                font-family:PingFang-SC-Regular;
                font-weight:400;
                color:rgba(255,255,255,1);
              }
            }
          }
        }
      }
    }
  }
</style>



