<template>
  <div>
    <div class="radio" v-for="(item,key) in data" :key="key"  @click="selectRadio(key,item.surplus)">
      <div :class="{'buttonNone':key!=selectIndex,'buttonOk':key==selectIndex}"></div>
      <div class="lableInfo text-ellipsis">{{item.setmeal}}</div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'radio',
    data(){
      return {
        selectIndex:-1, //选中的ID
      }
    },
    props:{
      data:{  //数据
        type:Array ,
        default(){
          return [];
        }
      },

    },
    methods:{
      selectRadio(key,num){
        console.log(num);
        this.selectIndex = key;
        this.$emit('selectRadio',key,num);
      }
    }
  }
</script>
<style scoped lang="scss">
  @import "~@/assets/common/mixin";
  .radio{
    padding-left:60px;
    height:80px;
    line-height:80px;
    box-sizing: border-box;
    font-size:28px;
    font-family:PingFang-SC-Regular;
    font-weight:400;
    color:rgba(51,51,51,1);
    .buttonNone{
      height:26px;
      width:26px;
      margin-top:27px;
      float:left;
      @include bg-image("~@/containers/Main/assets/icon_buttonNone");
      margin-right:15px;
    }
    .buttonOk{
      height:26px;
      width:26px;
      margin-top:27px;
      float:left;
      margin-right:15px;
      @include bg-image("~@/containers/Main/assets/icon_buttonOk");
    }
    .lableInfo{
      height:80px;
      width:610px;
      float:left;
      line-height:80px;
    }
  }
</style>



