<template>
  <!--课程详情-->
  <div class="courseDetails">
    <!--头部导航-->
    <headerBack title="课程详情"></headerBack>
    <verticalScroll >
      <!--顶部课程宣传画-->
      <courseImg  :imgUrl="listInfo.imageurl"></courseImg>
      <!--课程宣传语-->
      <div class="coursePublic">
        <div class="publicTitle">
          {{listInfo.coursename}}
        </div>
        <div class="publicInfo" >
          {{listInfo.subhead}}
        </div>
      </div>
      <!--课程选择-->
      <div class="courseSelect text-ellipsis" v-if="classtypes==2"  @click="openModal">
        <div class="leftTitle">
          课程选择
        </div>
        <div class="middleTittle  text-ellipsis">
          {{date}} , 1V{{defaultVs}} , {{className}}
        </div>
        <div class="rightIcon" >
          <img src="~@/assets/images/icon_right.png" width="100%" alt="">
        </div>
      </div>
      <!--课程简介-->
      <circleBlock title="课程简介"></circleBlock>
      <div class="courseIntro"  v-html="listInfo.generalize">
        <div class="leftGray"></div>
      </div>

      <!--老师简介-->
      <circleBlock title="老师简介"></circleBlock>
      <div class="teacherCard">
        <div class="teacherImg">
          <router-link :to="{path:'TeacherInfo',query:{'teacherId':listInfo.teacherid}}">
            <div class="imgShow">
              <img :src="listInfo.teacher.imageurl || defaultTeacher" width="100%" height="100%" alt="">
            </div>
          </router-link>
          <div class="infoInfo">
            <div class="teacherName">
              {{listInfo.teacher.teachername}}
            </div>
            <div class="teacherInfo">
              {{getCity(listInfo.teacher.country)}} {{getCity(listInfo.teacher.province)}} {{getCity(listInfo.teacher.city)}}
            </div>
          </div>
        </div>
        <div class="bottomInfo" >
          <div class="leftGray" ></div>
          <template>
            {{listInfo.teacher.profile}}
          </template>
          <template v-if="!listInfo.teacher.profile">
            该老师没有更多的介绍信息
          </template>
        </div>
      </div>
      <!--课时介绍-->
      <lessonTime :data="listInfo.unitlist"></lessonTime>

    </verticalScroll>

    <!--<mt-popup v-model="popupVisible"  position="bottom" style="width: 100%;height:100%;">-->
      <!--<payment @restartSend="restartSend" :giftdescribe="giftdescribe" :buttonStatuss="buttonStatus" :allInfo="allInfo" :defaultVs="defaultVs" :dateInfo="dateInfo" :classtypes="classtypes" :dateIndex="dateIndex" :dateTime="date"  :imgUrl="listInfo.imageurl" :price="listInfo.totalprice" @closeModal="closePayment" :formData="formData"></payment>-->
    <!--</mt-popup>-->

    <middleToast v-if="toastStatus==true" >
      <div class="selectDate">
        <div class="offIcon" @click="closeModal">
          <img src="~@/assets/images/icon_add.png" width="100%" alt="">
        </div>
        <ul>
          <li class="liInfo"  v-for="(item,key) in dateInfo"  @click="selectDate(item.starttime,item.fullpeople,item.scheduid,item.gradename)">
            {{item.starttime}}, 1v{{item.fullpeople}}, {{item.gradename}}
          </li>
        </ul>
      </div>
    </middleToast>

    <!--底部确认按钮-->
    <collectionButton v-if="from!='order'" :price="listInfo.totalprice" :is_collect="is_collect"  :applyStatus="listInfo.applyStatus" @selectButton="selectButton"></collectionButton>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //headerBack组件
  import courseImg from '@/components/CourseImg';     //顶部宣传画
  import circleBlock from '@/components/CircleBlock';     //整行的课程信息
  import payment from '../components/Payment';     //底部modal
  import lessonTime from '../components/LessonTime';     //课时介绍
  import collectionButton from '../components/CollectionButton';     //底部收藏
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import middleToast from '@/components/MiddleToast';     //自定义模态弹框
  import defaultTeacher from '@/assets/images/defaultTeacher.png';      //图片加载页面失败显示
  import CityJSON from '@/assets/city/city.json';
  import {Popup,Toast} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default {
    name:"CourseDetails",
    components:{
      headerBack,
      courseImg,
      payment,
      middleToast,
      circleBlock,
      'mt-popup':Popup,
      lessonTime,
      verticalScroll,
      collectionButton
    },
    data(){
      return{
        defaultTeacher,
        toastStatus:false,  //默认不显示弹框
        popupVisible:false,
        studentid:'',   //学生ID

        dateInfo:[],  //时间信息数组
        userPakeInfo:[],  //优惠信息
        giftdescribe:[],  //教材信息

        date:'',      //直播课需要的时间
        defaultVs:0 ,  //默认是一对0教育
        className:'',  //班级名称

        buttonStatus:0,  //默认可使用
        classId:0,    //默认的班级id
        classIndex:0,  //默认的班级索引
        curriculumid:'',   //课程ID
        price:'',         //课程价格
        teacherImg:'',   //老师图片

        from:'',   //是否是订单过来的数据
        teacherid:'',  //老师详情过来的id
        listInfo:{
          teacher:{}
        },
        formData:{
          name:'',
          mobile: true
        },
        classtypes:2,   //课程类别  1录播  2直播
        courseid:'',      //课程ID
        is_collect:0,   //课程是否已经收藏   0未收藏 1已经收藏
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      this.courseid = this.$route.query.courseid;       //课程ID
      this.classtypes = this.$route.query.classtypes;     //课程类型直播  录播
      this.teacherid = this.$route.query.teacherid;     //老师详情的老师ID
      this.from = this.$route.query.from;               // ''课程列表 'order'订单课程  'teacher'老师课程
      // 如果是直播课直接请求
      if(this.classtypes==1){
        this.getCurriculumInfo();
      }else{
        // 获取时间的数组
        // this.getCurriculumDateList();
        // 获取整理好的时间数组
        this.chooseAllList();
      }
    },
    methods:{
      /**
       * *   城市联动开始
       */
      //城市转换
      getCity: function(val) {
        if(val == 0 || val == null){
          return
        }else{
          CityJSON.map((el,index) => {
            if(el.id == val){
              val =el.name
            }
          })
          return val;
        }
      },
      // 再次请求
      restartSend(date,vs){
        this.date = date;
        this.defaultVs = vs;
        this.getCurriculumInfo();
      },
      // 请求课程详情信息
      getCurriculumInfo(){
          Server.post(Path.getCurriculumInfo, {
            'date':this.date,
            'fullpeople':this.defaultVs,
            'courseid':this.courseid,
            'classtypes':this.classtypes,
          }, (res) => {
            if (res.code === 0) {
              // 判断录播课还是直播课
              if(this.classtypes==1){
                // 录播课直接绑定数据
                this.listInfo = res.data;
              }else{
                if(res.data.classinfo.length!=0){
                  if(this.from=='teacher'){
                    for(let i in res.data.classinfo){
                      if(res.data.classinfo[i].teacherid==this.teacherid){
                        // 默认显示选中课程的数据
                        this.listInfo = res.data.classinfo[i];
                        // 课程的索引
                        this.classIndex = i;
                      }
                    }
                  }else{
                    let now =false;
                    for(let i in res.data.classinfo){
                      if(res.data.classinfo[i].scheduid==this.classId){
                        now = true;
                        // 默认显示选中课程的数据
                        this.listInfo = res.data.classinfo[i];
                        // 课程的索引
                        this.classIndex = i;
                        // return后下面不执行  错误
                        // return;
                      }
                    }
                    if(!now){
                      this.listInfo = res.data.classinfo[0];
                    }
                  }
                }


                // 记录第一个数据课程信息的可用性
                this.buttonStatus = this.listInfo.applystatus;
                // 直播课记录优惠的数据
                this.giftdescribe = res.data.giftdescribe;
              }
              this.$set(this.listInfo.teacher,"imageurl", this.listInfo.teacher.imageurl);
              // 老师图片的更换
              // this.teacherImg = this.listInfo.teacher.imageurl;

              // 记录课程ID
              this.curriculumid = this.listInfo.curriculumid;
              // 记录课程价格
              this.totalprice = this.listInfo.totalprice;
              // 记录是否已经收藏
              this.is_collect = res.data.is_collect;
            }
          });
      },
      // 获取整理好的时间数组
      chooseAllList(){
        Server.post(Path.chooseAllList, {
          'courseid':this.courseid,
        }, (res) => {
          if (res.code === 0) {
            // 绑定数据
            this.dateInfo = res.data;
            if(res.data.length>0){
              if(this.from=='teacher'){
                for(let item of res.data){
                  if(item.teacherid==this.teacherid){
                    this.date = item.starttime;
                    this.defaultVs = item.fullpeople;
                    this.className = item.gradename;
                  }
                }
              }else{
                //默认显示的时间
                this.date = res.data[0].starttime;
                // 默认显示的老师几对几教学
                this.defaultVs = res.data[0].fullpeople;
                // 显示默认的班级
                this.className = res.data[0].gradename;
              }
              // 请求课程信息数据
              this.getCurriculumInfo();
            }
          }
        });
      },
      // 获取时间的数组
      getCurriculumDateList(){
        Server.post(Path.getCurriculumDateList, {
          'courseid':this.courseid,
        }, (res) => {
          if (res.code === 0) {
            // 绑定数据
            this.dateInfo = res.data;
            if(res.data.length>0){
              if(this.from=='teacher'){
                for(let item of res.data){
                  if(item.teacherid==this.teacherid){
                    this.data = item.data;
                    this.defaultVs = item.class[0];
                  }
                }
              }else{
                //默认显示的时间
                this.date = res.data[0].date;
                // 默认显示的老师几对几教学
                this.defaultVs = res.data[0].class[0];
              }
              // 请求课程信息数据
              this.getCurriculumInfo();
            }
          }
        });
      },
      // 底部收藏 tab点击
      selectButton(value){
        if(value=='signUp'){
          this.$router.push({path:'CourseSelect',query:{'date':this.date,'fullpeople':this.defaultVs,'courseid':this.courseid,'classIndex':this.classIndex,'classtypes':this.classtypes,key:''}});
        }else{
          // this.popupVisible = true;
          // // 请求优惠信息
          // this.getUserPackage();
          // 收藏课程
          this.classCollect();
        }
      },
      // 收藏课程
      classCollect(){
        Server.post(Path.classCollect, {
          'courseid':this.courseid,
          'studentid':this.studentid
        }, (res) => {
          if (res.code === 0) {
            this.is_collect = 1;
            Toast('收藏成功');
          }else{
            Toast(res.info);
          }
        });
      },
      // 关闭底部课程选择页
      closePayment(){
        this.popupVisible = false;
      },
      // 打开时间模态框
      openModal(){
        this.toastStatus = true;
      },
      // 选择时间和班级几对几
      selectDate(date,vs,scheduid,className){
        // 显示的时间
        this.date = date;
        // 老师教育几对几
        this.defaultVs = vs;
        // 显示的班级名称
        this.className = className;
        // 班级id的显示
        this.classId = scheduid;
        // 关闭模态框
        this.toastStatus = false;
        // 再次请求数据
        this.getCurriculumInfo();
      },
      // 关闭选择时间模态框
      closeModal(){
        this.toastStatus = false;
      },
      // 请求优惠券
      getUserPackage(){
        Server.post(Path.getUserPackage, {
          'curriculumid':this.curriculumid,
          'amount':this.price
        }, (res) => {
          if (res.code === 0) {
            this.userPakeInfo = res.data;
          }
        });
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  .courseDetails{
    position:absolute;
    right:0;
    top:0;
    bottom:0;
    left:0;
    .selectDate{
      width:500px;
      height:350px;
      overflow: scroll;
      padding-left:30px;
      padding-top:80px;
      border-radius:12px;
      background:rgba(255,255,255,1);
      position:relative;
      .liInfo{
        height:50px;
        line-height:50px;
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(128,128,128,1);
      }
      /*关闭按钮*/
      .offIcon{
        position:absolute;
        top:21px;
        right:25px;
        width:19px;
        height:19px;
        transform: rotate(45deg);
      }
    }
    @extend .main-child;
    /*课程宣传*/
    .coursePublic{
      padding:29px 40px 14px 40px;
      background:rgba(255,255,255,1);
      margin-bottom:20px;
      /*margin-bottom:20px;*/
      .publicTitle{
        height:60px;
        font-size:32px;
        font-family:PingFang-SC-Bold;
        font-weight:bold;
        color:rgba(51,51,51,1);
      }
      .publicInfo{
        font-size:24px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(128,128,128,1);
      }
    }
    /*课程选择*/
    .courseSelect{
      box-sizing: border-box;
      height:100px;
      line-height: 100px;
      padding:0px 30px 0px 39px;
      margin-bottom:20px;
      background:rgba(255,255,255,1);
      .leftTitle{
        height:100px;
        line-height: 100px;
        font-size:24px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        float:left;
        margin-right:21px;
        color:rgba(128,128,128,1);
      }
      .middleTittle{
        height:100px;
        line-height: 100px;
        font-size:30px;
        float:left;
        width:500px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(77,77,77,1);
      }
      .rightIcon{
        width:12px;
        height:22px;
        line-height:22px;
        margin-top:39px;
        float:right;
      }
    }
    /*课程简介*/
    .courseIntro{
      padding:38px 65px 75px 64px;
      background:rgba(255,255,255,1);
      margin-bottom:20px;
      font-size:24px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(128,128,128,1);
      position:relative;
      .leftGray{
        position:absolute;
        top:45px;
        left:37px;
        width:8px;
        height:8px;
        background:rgba(153,153,153,1);
        border-radius:50%;
      }
    }
    /*老师简介*/
    .teacherCard{
      padding:39px 0px 76px 0px;
      background:rgba(255,255,255,1);
      margin-bottom:20px;
      .teacherImg{
        padding-left:36px;
        height:180px;
        .imgShow{
          width:140px;
          height:140px;
          float:left;
          overflow: hidden;
          margin-right:42px;
          border-radius:50%;
        }
        .infoInfo{
          width: calc(100% - 220px);
          float:left;
          height:140px;
          padding-top:25px;
          .teacherName{
            height:67px;
            font-size:32px;
            font-family:PingFang-SC-Medium;
            font-weight:500;
            color:rgba(51,51,51,1);
          }
          .teacherInfo{
            font-size:28px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(128,128,128,1);
          }
        }
      }
      .bottomInfo{
        padding:0px 65px;
        font-size:24px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(128,128,128,1);
        position:relative;
        .leftGray{
          position:absolute;
          top:6px;
          left:37px;
          width:8px;
          height:8px;
          background:rgba(153,153,153,1);
          border-radius:50%;
        }
      }
    }

  }
</style>



