<template>
  <div id="Home">
    <!--头部返回导航-->
    <headerBack :title="title"></headerBack>
    <!--头部搜索-->
    <logoSearch></logoSearch>
    <!--滚动组件-->
    <bScroll
    class="wrapper"
    ref="scroll">
      <!--轮播图-->
      <banner></banner>
      <!--年级分类-->
      <studentClass ></studentClass>
      <!--课程推荐-->
      <showCourse ></showCourse>
      <!--多级分类   基础素质国学经典-->
      <div  v-for="(item,key) in listInfo" :key="key"  v-if="item.courselist.length!=0">
        <itemTitle  :title="item.categoryname" :category_id="item.category_id"></itemTitle>
        <fullItem :data="item.courselist"></fullItem>
      </div>
      <!--学习套餐-->
      <MealItem></MealItem>
      <!--名师推荐-->
      <famousTeacher></famousTeacher>
      <!--联系我们-->
      <contactUs></contactUs>
    </bScroll>
  </div>
</template>
<script>
  import bScroll from '@/components/Lib/BScroll';           //bScroll
import headerBack from '@/components/HeaderBack';     //头部导航
import studentClass from './components/StudentClass'; //年级分类
import showCourse from './components/ShowCourse';   //课程推荐
import fullItem from './components/FullItem';     //整行的课程信息
import itemTitle from './components/ItemTitle';   //查看全部
import logoSearch from './components/LogoSearch';   //查看全部
import MealItem from './components/MealItem';   //套餐
import banner from './components/Banner';   //轮播图
import famousTeacher from './components/FamousTeacher';   //名师推荐
import contactUs from './components/ContactUs';   //联系我们
import Server from '@/service/request';
import Path from '@/service/Path';
export default{
  name:'Home',
  components:{
    bScroll,
    headerBack,
    studentClass,
    showCourse,
    fullItem,
    itemTitle,
    banner,
    contactUs,
    famousTeacher,
    logoSearch,
    MealItem
  },
  data(){
    return{
      listInfo:[],  //分类信息  国学  阅读
      selected:1,
      // 名师推荐
      teacherRouter:'TeacherList',   //名师列表路由
      famousTeacher:'名师推荐',
      title:'柠檬教育',
      itemTitle:'基础素质',
    }
  },
  created() {
    // 获取分类信息
    this.getTopCategoryCourser();
  },
  methods:{
    // 获取分类信息
    getTopCategoryCourser(){
      // 获取列表数据
      Server.post(Path.getTopCategoryCourser, {
      }, (res) => {
        if (res.code === 0) {
          // 绑定数据
          this.listInfo = res.data;
        }
      });
    },
    // 获取轮播图
    getSlideList(){

    },

  },

}
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  .wrapper {
    overflow: hidden;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  #Home {
    @extend .main-child;
  }
</style>



