<template>
  <!--我的点评-->
  <div class="ReviewList">
    <div class="item" v-for="item in data" @click="toPage(item.id)">
      <div class="periodname">
        {{item.periodname}}
      </div>
      <div class="icon">
        <span></span>
      </div>
      <div class="bottomStar">
        <span>
          {{item.gradename}}
        </span>
        <div class="starIcon">
          评分：
          <span v-for="v in 5" :class="{'spanStar':v<=item.score,'spanNoe':v>item.score}"></span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'ReviewList',
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    methods:{
      toPage(id){
        this.$router.push({'path':'ReviewInfo',query:{id:id,from:'list'}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .ReviewList{
    margin-top:5px;
    .item{
      box-sizing:border-box;
      height:141px;
      padding:30px 40px;
      background:rgba(255,255,255,1);
      border-bottom:1px solid ghostwhite;
      /*上层*/
      .periodname{
        line-height: 20px;
        font-size:20px;
        font-family:MicrosoftYaHei-Bold;
        font-weight:bold;
        color:rgba(51,51,51,1);
      }
      /*中间层*/
      .icon{
        height:40px;
        line-height:40px;
        text-align:right;
        span{
          display:inline-block;
          height:34px;
          width:21px;
          @include bg-image("~@/assets/images/icon_right");
        }
      }
      /*底层*/
      .bottomStar{
        height:20px;
        line-height:20px;
        span{
          font-size:18px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(51,51,51,1);
          line-height:24px;
        }
        .starIcon{
          float:right;
          padding-right:110px;
          .spanStar{
            display:inline-block;
            width:21px;
            height:19px;
            @include bg-image("~@/assets/images/icon_starBg");
            margin-right:5px;
          }
          .spanNoe{
            display:inline-block;
            width:21px;
            height:19px;
            @include bg-image("~@/assets/images/icon_starBorder");
            margin-right:5px;
          }
        }
      }
    }
  }
</style>



