<template>
  <!--订单的显示-->
  <div class="ShowOrder">
    <!--头部导航-->
    <headerBack title="订单详情"></headerBack>
    <!--顶部课程宣传画-->
    <courseImg :imgUrl="orderInfo.setimgpath"></courseImg>
    <!--信息的显示-->
    <div class="orderInfo">
      <ul>
        <li>订单编号:{{orderInfo.ordernum}}</li>
        <li>下单时间:{{timestampToTime(orderInfo.ordertime)}}</li>
        <li>套餐课时:{{orderInfo.setmeal}}</li>
        <li>套餐描述:{{orderInfo.content}}</li>
      </ul>
    </div>
    <!--底部确认订单-->
    <bottomButton :buttonL="'￥'+orderInfo.amount"   buttonR="提交" leftColor="rgba(235,28,28,1)" @selectedTab="selectedTab"  backgroundL="rgba(255,255,255,1)" backgroundR="rgba(57,173,239,1)"></bottomButton>
    <!--支付Modal-->
    <paymentModal v-if="modalStatus" @priceStatus="priceStatus"  :balances="orderInfo.amount"  :price="orderInfo.usablemoney"></paymentModal>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import bottomButton from '@/components/BottomButton';     //底部确认订单
  import {Toast} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import courseImg from '@/components/CourseImg';     //顶部宣传画
  import paymentModal from '@/components/PaymentModal';   //支付Modal
  let that = {};
  export default{
    name:"ShowOrder",
    components:{
      headerBack,
      bottomButton,
      courseImg,
      paymentModal
    },
    data(){
      return {
        orderNum:'',  //订单号
        orderInfo:{}, //订单详情
        modalStatus:false,  //默认付钱modal关闭
        paytype:'',     //支付方式   1余额支付  2微信支付
        weiInfo:{},     //返回的微信数据
        studentid:'',   //学生ID
        code:'',
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      that = this;
      // 获取页面传值订单号
      this.orderNum = this.$route.query.orderNum;
      // 获取订单详情
      this.showOrderDetail();
    },
    methods:{
      // 时间转换
      timestampToTime(timestamp) {
        var date = new Date(timestamp * 1000);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
        var Y = date.getFullYear() + '-';
        var M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
        var D = date.getDate() + ' ';
        var h = date.getHours() + ':';
        var m = date.getMinutes() + ':';
        var s = date.getSeconds();
        return Y+M+D+h+m+s;
      },
      // 获取订单详情
      showOrderDetail(){
        Server.post(Path.showOrderDetailPackage,{
          'ordernum':this.orderNum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 订单信息
            this.orderInfo = data;
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 套餐下单
      selectedTab(value){
        if(value=='right'){
          // 立即支付  开启Modal
          this.modalStatus = true;
        }
      },
      // 是否提交支付
      priceStatus(value,balance){
        let code = localStorage.getItem('code');
        this.paytype = balance;
        // 关闭modal
        this.modalStatus = false;
        if(value=='yes'){
          Server.post(Path.gotoPay,{
            'ordernum':this.orderNum,
            'paytype':this.paytype,
            'studentid':this.studentid,
            'code':code
          },(res)=>{
            let { code, data } = res;
            if(res.code===0){
              if(this.paytype==1){
                this.$router.push('/PaymentBack');
              }else{
                this.weiInfo = res.data;
                this.pay();
              }
            }else{
              Toast({
                message: res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }
          })
        }else{
          console.log('取消');
        }
      },
      // 微信支付
      pay(){
        if (typeof WeixinJSBridge == "undefined"){
          if( document.addEventListener ){
            document.addEventListener('WeixinJSBridgeReady',that.onBridgeReady,false);
          }else if (document.attachEvent){
            document.attachEvent('WeixinJSBridgeReady', that.onBridgeReady);
            document.attachEvent('onWeixinJSBridgeReady', that.onBridgeReady);
          }
        }else{
          that.onBridgeReady();
        }
      },
      onBridgeReady(){
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
            "appId":that.weiInfo.appId,     //公众号名称，由商户传入
            "timeStamp":that.weiInfo.timeStamp,         //时间戳，自1970年以来的秒数
            "nonceStr":that.weiInfo.nonceStr, //随机串
            "package":that.weiInfo.package,
            "signType": that.weiInfo.signType,         //微信签名方式:
            "paySign": that.weiInfo.paySign   //微信签名
          },
          function(res){
            if(res.err_msg == "get_brand_wcpay_request:ok"){
              that.$router.push('/PaymentBack');
            }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
              Toast('取消支付');
            }else{
              Toast('支付失败');
            }
          });
      }
    },
  }
</script>
<style scoped lang="scss">
  .ShowOrder{
    position:absolute;



    top:0;
    right:0;
    left:0;
    bottom:0;
    background:rgba(255,255,255,1);
    /*信息显示*/
    .orderInfo{
      margin-top:20px;
      li{
        height:88px;
        padding-left:40px;
        line-height:88px;
        font-size:30px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(61,61,61,1);
        border-bottom:1px solid rgba(217,217,217,1);
      }
    }
  }
</style>



