<template>
  <!--账户明细-->
  <div class="AccountDetails">
    <headerBack title="账单明细"></headerBack>
    <verticalScroll>
      <ul>
        <li v-for="(item,key) in contentInfo" :key="key">
          <div class="innerDiv">
            <div class="leftOut">
              <div class="title" v-if='item.paystatus==1'>
                {{item.coursename}}
              </div>
              <div class="title" v-if='item.paystatus==2'>
                微信充值
              </div>
              <div class="title" v-if='item.paystatus==3'>
                套餐购买
              </div>
              <div class="time">
                {{item.paytime}}
              </div>
            </div>
            <div class="rightOut">
              <template v-if="item.paystatus==1">
                - {{item.paynum}}
              </template>
              <template v-else-if="item.paystatus==2">
                + {{item.paynum}}
              </template>
              <template v-else>
                - {{item.paynum}}
              </template>
            </div>
          </div>
        </li>
      </ul>
    </verticalScroll>
    <notInfo v-if="infoStatus">
      抱歉，没有账单记录
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import notInfo from '@/components/NotInfo';           //没有数据
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:"AccountDetails",
    components:{
      headerBack,
      verticalScroll,
      notInfo
    },
    data(){
      return {
        infoStatus:false,    //无数据默认不显示
        studentid:'',     //学生ID
        limit:10,
        pagenum:1,
        studentid:'',
        contentInfo:[],    //所有的账单信息
      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      this.getStudentPaylog();
    },
    methods:{
      // 账户余额明细
      getStudentPaylog(){
        Server.post(Path.getStudentPaylog,{
          'studentid':this.studentid,
          'pagenum':this.pagenum,
          'limit':this.limit
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              this.infoStatus = true;
            }else{
              this.contentInfo = data.data;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .AccountDetails{
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    background:rgba(255,255,255,1);
    li{
      height:136px;
      box-sizing: border-box;
      padding:0px 15px;
      background:rgba(255,255,255,1);
      .innerDiv{
        height:134px;
        padding:0px 22px;
        /*border:1px solid red;*/
        border-bottom:1px solid rgba(179,179,179,1);
        .leftOut{
          height:134px;
          width:500px;
          padding-top:32px;
          box-sizing: border-box;
          float:left;
          .title{
            height:53px;
            font-size:34px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(26,26,26,1);
          }
          .time{
            font-size:24px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(128,128,128,1);
          }
        }
        .rightOut{
          height:134px;
          line-height:134px;
          width:150px;
          float:right;
          text-align: center;
          font-size:34px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(63,156,232,1);
        }
      }
    }
  }
</style>



