<template>
  <div class="courseImg">
    <img v-lazy="imgUrl" width="100%" height="100%" alt="">
  </div>
</template>
<script>
  export default{
    name:'CourseImg',
    props:{
      imgUrl:{
        type:String,
        default(){
          return '';
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
.courseImg{
  height:400px;
  background:rgba(255,255,255,1);
}
</style>



