/*
* success : 请求成功执行的函数
* fail：请求失败执行的函数
*  定义全局方法
* import request from './service/request'
* Vue.prototype.$request = request;
* */
import http from './http';

var request = {
  get:function (url,data,success,fail) {
    return http.get(url,{
      params:data
    })
      .then(function (res) {
        success(res);
      })
      .catch(function (error) {
        if(fail){
          fail(error);
        }
        console.log(error);
      })
  },
  post:function (url,data,success,fail) {
    return http.post(url,data)
      .then(function (res) {
        success(res);
      })
      .catch(function (error) {
        if(fail){
          fail(error);
        }
        console.log(error);
      })
  },
  filepost: function (url, data, success, fail) {
    return http.post(url,
      data,
      {
        headers:{
          'Content-Type':'multipart/form-data'
        }
      }
    )
    .then(res => {
      success(res);
    })
    .catch(error => {
      if (fail) {
        fail(error);
      }
      console.log(error);
    })
  }
}
export default  request;



