<template>
  <div class="title">
    <div class="titleBlock"></div>
    <div class="titleWeight">
      {{title}}
    </div>
    <div class="rightAll" @click="toPage">
      全部
      <div class="icon">
        <!--<img src="~@/assets/images/allIcon.png"  alt="">-->
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name:'ItemTitle',
    props:{
      title:{
        type:String,
        default(){
          return '';
        }
      },
      category_id:{
        type:Number,
        default(){
          return 0;
        }
      },
      router:{
        type:String,
        default(){
          return '';
        }
      }
    },
    methods:{
      // 跳转页面
      toPage(){
        if(this.router){
          this.$router.push(this.router);
        }else{
          this.$router.push({path:'AllCourses',query:{category_id:this.category_id}});
        }

      }
    }
  }
</script>
<style lang="scss"  scoped>
  @import "~@/assets/common/mixin";
  .title{
    box-sizing: border-box;
    height:90px;
    padding:0px 22px;
    background:rgba(255,255,255,1);
    border-bottom:1px solid rgba(217,217,217,1);
    margin-top:20px;
    clear:both;
    .titleBlock{
      width:6px;
      height:32px;
      float:left;
      margin-top:29px;
      background:rgba(57,173,239,1);
      margin-right:23px;
    }
    .titleWeight{
      height:90px;
      line-height:90px;
      text-align:center;
      float:left;
      margin-right:50px;
      font-size:32px;
      font-family:PingFang-SC-Bold;
      font-weight:bold!important;
      color:rgba(51,51,51,1);
    }
    .rightAll{
      height:90px;
      line-height:90px;
      float:right;
      font-size:26px;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      color:rgba(51,51,51,1);
      .icon{
        margin-top:31px;
        width:24px;
        height:24px;
        background:red;
        /*overflow: hidden;*/
        float:right;
        margin-left:10px;
        @include bg-image("~@/assets/images/allIcon");
      }
    }
  }
</style>



