<template>
  <!--修改手机号-->
  <div class="changePhone">
    <headerBack title="修改手机号"></headerBack>
    <div class="infoContent">
      <div class="nowPhone">
        当前手机号：+{{prphone}} {{oldMobile}}
      </div>
      <div class="newPhone">
        <div class="showPhone">
          <span @click="toNumber">+{{prphone}}</span> <input class="phoneInput" v-validate="'required|phone'"  name="phone" v-model="mobile" type="text" placeholder="请输入新的手机号码">
        </div>
      </div>
      <div class="errorShow" v-show="errors.has('phone')">
        {{ errors.first('phone') }}
      </div>
      <div class="codeOut">
        <input class="codeInput"  v-validate="'required'"  maxlength="8"  name="code" v-model="code" type="text" placeholder="请输入短信验证码">
        <div class="rightTitle" @click="getCode">
          {{codeObj.codeText}}
        </div>
        <div class="lineWidth"></div>
      </div>
      <div class="errorShow" v-show="errors.has('code')">
        {{ errors.first('code') }}
      </div>

      <div class="bottomButton"  @click="changePhone">
        确定
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="right" style="width:100%;height:100%;">
      <number :popupVisible="popupVisible"  @closeModal="closeModal"></number>
    </mt-popup>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast,Popup} from 'mint-ui';
  import number from '@/components/Number';     //选择区号
  export default{
    name:'changePhone',
    components:{
      headerBack,
      number,
      'mt-popup':Popup
    },
    data(){
      return {
        prphone:'',  //默认区号
        popupVisible:false,    //选择区号
        mobile:'',      //手机号
        oldMobile:'',   //老手机号
        code:'',      //手机验证码
        codeObj:{
          codeText:'获取验证码' , //发送验证码
          timer:null,
          count:''
        },
      }
    },
    created(){
      this.prphone = this.$route.query.prphone;
      this.oldMobile = this.$route.query.mobile;
    },
    methods:{
      // 关闭选择区号
      closeModal(info){
        this.popupVisible = false;
        // 给区号赋值
        this.prphone = info.code;
      },
      // 获取短信验证码
      getCode(){
        if(this.mobile.length!=11){
          //请输入正确手机号
          Toast({
            message:'请输入正确手机号',
            iconClass: 'icon icon-error',
            duration: '1800'
          });
          return false;
        }
        if(this.mobile==this.oldMobile){
          //请输入正确手机号
          Toast({
            message:'请输入新的手机号',
            iconClass: 'icon icon-error',
            duration: '1800'
          });
          return false;
        }
        // 定义读秒
        const count = 60;
        if(!this.codeObj.timer){
          Server.post(Path.sendUpdateMobileMsg,{
            'mobile':this.mobile,
            'prphone':'86'
          },(res)=>{
            let { code, data } = res;
            if(code===0){
            }else{
              Toast({
                message:res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }
          });
          this.codeObj.count = count;
          this.codeObj.codeText = this.codeObj.count+'s';
          this.codeObj.timer = setInterval(() => {
            if (this.codeObj.count > 0 && this.codeObj.count <= count) {
              this.codeObj.count--;
              this.codeObj.codeText = this.codeObj.count+'s';
            } else {
              clearInterval(this.codeObj.timer);
              this.codeObj.timer = null;
              // 重发验证码
              this.codeObj.codeText = '重新发送';
            }
          }, 1000)
        }
      },
      // 选择区号
      toNumber(){
        this.popupVisible = true;
      },
      // 修改手机号
      changePhone(){
        this.$validator.validate().then(result => {
          if(!result){
            return;
          }else{
            Server.post(Path.updateMobile,{
              'oldmobile':this.oldMobile,
              'prphone':this.prphone,
              'newmobile':this.mobile,
              'code':this.code
            },(res)=>{
              console.log(res);
              let { code, data } = res;
              if(code===0){
                Toast('手机号修改成功');
                this.$router.go(-1);
              }else{
                Toast({
                  message:res.info,
                  iconClass: 'icon icon-error',
                  duration: '1800'
                });
              }
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .errorShow{
    height:50px;
    line-height:50px;
    padding-left:25px;
    color:red;
  }
    .changePhone{
      position:absolute;
      left:0;
      right:0;
      top:0;
      bottom:0;
      background:rgba(255,255,255,1);
      .infoContent{
        padding:0px 37px;
        .nowPhone{
          height:146px;
          line-height:146px;
          padding-left:14px;
          font-size:32px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(77,77,77,1);
          border-bottom:1px solid rgba(217,217,217,1);
        }
        .newPhone{
          height:117px;
          padding-left:14px;
          box-sizing: border-box;
          padding-top:62px;
          border-bottom:1px solid rgba(217,217,217,1);
          .showPhone{
            height:54px;
            line-height:54px;
            span{
              font-size:32px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              color:rgba(77,77,77,1);
            }
            .phoneInput{
              font-size:32px;
              font-family:PingFang-SC-Regular;
              font-weight:400;
              padding-left:15px;
              color:rgba(179,179,179,1);
            }
          }
        }
        .codeOut{
          height:117px;
          padding-left:14px;
          box-sizing: border-box;
          padding-top:62px;
          border-bottom:1px solid rgba(217,217,217,1);
          .codeInput{
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(179,179,179,1);
          }
          .rightTitle{
            padding:0 10px;
            height:54px;
            line-height: 54px;
            text-align: right;
            float:right;
            font-size:30px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(16,165,228,1);
          }
          .lineWidth{
            float:right;
            width:0px;
            height:42px;
            margin-top:6px;
            margin-right:21px;
            border-right:1px solid rgba(217,217,217,1);
          }
        }
        .bottomButton{
          width:676px;
          height:108px;
          line-height:108px;
          text-align: center;
          background:rgba(57,173,239,1);
          border-radius:10px;
          font-size:34px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(254,255,255,1);
          margin:68px auto 0px;
        }
      }
    }
</style>



