
<template>
  <div class="banner" id="Banner">
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide v-for="i in Math.ceil(data.length/3)" :key="i">
        <div class="StudentClass">
          <div class="classItem"  v-for="item in data.slice((i-1)*3,3+(i-1)*3)" @click="toPage(item.courseid,item.classtypes)">
            <div class="flexOut">
              <div class="imgShow">
                <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
              </div>
              <div class="titleInfo">
                {{item.coursename}}
              </div>
            </div>
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
</template>

<script>
  import {swiper, swiperSlide} from 'vue-awesome-swiper';
  import 'swiper/dist/css/swiper.css'
  export default {
    name:'StudentClass',

    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      }
    },
    components: {
      swiper,
      swiperSlide
    },
    data() {
      return {
        swiperOption: {
          slidesPerView: 1,     //一次显示1个
          autoplayDisableOnInteraction: false,
          initialSlide: 0,
          loop: false,
          autoplay: 3000,
          type: 'bullets',
          notNextTick: true,
          direction: 'horizontal',
          pagination: '.swiper-pagination',
          // slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 30,
          onSlideChangeEnd: swiper => {
            this.page = swiper.realIndex + 1;
            this.index = swiper.realIndex;
          }
        },
        listImg: [],
      }
    },
    created() {
      this.getBanners();
    },
    methods: {
      toPage(courseid,classtypes){
        this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes}});
      },
      getBanners() {
        // Server.post(Path.getBanners, {

        // }, (res) => {
        //   if (res.code === 0) {
        //     this.listImg = res.data;
        //   }
        // });
      },
    },
    //定义这个sweiper对象
    computed: {
      swiper() {
        return this.$refs.mySwiper.swiper;
      },
    }
  }
</script>

<style scoped lang="scss">
  .swiper-container {
    width: 100%;
    height: 238px;
    overflow: hidden;
    margin-bottom:20px;
    .swiper-wrapper {
      width: 100%;
      height: 238px;
      .swiper-slide {
        width: 100%;
        height: 100%;
        .StudentClass{
          font-size:26px;
          color:rgba(128,128,128,1);
          box-sizing: border-box;
          padding:20px 37px 0px 36px;
          height:238px;
          background:rgba(255,255,255,1);
          margin-bottom:20px;
          .classItem{
            height:215px;
            width:200px;
            width:33.3%;
            float:left;
            .flexOut{
              width:200px;
              margin:0px auto;
              .imgShow{
                width:200px;
                height:114px;
                border-radius:8px;
                margin-bottom:10px;
              }
              .titleInfo{
                font-size:26px;
                font-family:MicrosoftYaHei;
                font-weight:400;
                text-align: center;
                color:rgba(128,128,128,1);
                line-height:34px;
              }
            }

          }
        }
      }
    }
  }

</style>




