<template>
  <!--历史签到-->
  <div>
    <div class="HistorySignIn">
      <headerBack title="历史签到"></headerBack>
      <div class="wrapper" ref="scroll">
        <mt-loadmore :top-method="loadTop"
                     :bottom-method="loadBottom"
                     :bottom-all-loaded="allLoaded"
                     ref="loadmore"  >
          <div class="SignInList">
            <div class="title">
              我的知识清单
            </div>
            <div class="subTitle">
              <div class="leftTitle">
                签到时间
              </div>
              <div class="rightTitle">
                内容
              </div>
            </div>

            <ul  v-if="dataInfo">
              <li class="liOdd"  v-for="(item,key) in dataInfo" :key = key>
                <div class="leftInfo">
                  {{item.signdate}}
                </div>
                <div class="rightInfo">
                  {{item.content}}
                </div>
              </li>
            </ul>
          </div>
        </mt-loadmore>
      </div>
    </div>
    <notInfo v-if="infoStatus">
      抱歉，没有历史数据
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import notInfo from '@/components/NotInfo';     //返回组件
  import { Loadmore } from 'mint-ui';
  export default{
    name:'HistorySignIn',
    components:{
      headerBack,
      notInfo,
      'mt-loadmore':Loadmore
    },
    data(){
      return {
        dataInfo:[],   //数组集合
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      // 请求历史签到
      this.mySigninList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.mySigninList();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.mySigninList();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 请求历史数据
      mySigninList(){
        Server.post(Path.mySigninList, {
          'limit':this.limit,
          'pagenum':this.pagenum
        }, (res) => {
          if (res.code === 0) {
            if(res.data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.limit;
            if (this.pagenum != 1 ) {
              //页码不为一，追加
              this.dataInfo = this.dataInfo.concat(res.data.data);
            } else {
              //否则从第一页开始，覆盖列表数据
              this.dataInfo = res.data.data;
            }
          }
        });
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .HistorySignIn{

  }
  .SignInList{
    padding:59px 68px 12px 68px;
    box-sizing: border-box;
    background:rgba(250,250,250,1);
    .title{
      height:59px;
      font-size:30px;
      text-align: center;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(61,61,61,1);
    }
    .subTitle{
      padding-left:35px;
      height:50px;
      box-sizing: border-box;
      line-height:50px;
      font-size:24px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(61,61,61,1);
      background:rgba(235,235,235,1);
      .leftTitle{
        height:50px;
        width:159px;
        line-height: 50px;
        float:left;
      }
      .rightTitle{
        height:50px;
        line-height: 50px;
        float:left;
        text-align: center;
        width:410px;
      }
    }
    .liOdd{
      width:613px;
      height:100px;
      background:red;
      padding-left:35px;
      box-sizing: border-box;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(61,61,61,1);
      line-height:24px;
      background:rgba(247,247,247,1);
      font-size:20px;
      .leftInfo{
        width:161px;
        height:100px;
        line-height: 100px;
        float:left;
      }
      .rightInfo{
        width:415px;
        height:100px;
        display: flex;
        align-items: center;
        float:left;
      }
    }
    .liEven{

    }
  }
</style>



