<template>
  <div >
    <div :class="{'MyCollCourse':type=='coll','MyOrderCourse':type=='order'}"  @click="toPage">
      <div class="imgShow">
        <img v-lazy="data.imageurl" width="100%" height="100%" alt="">
      </div>
      <div class="infoContent">
        <div class="title">
          {{data.coursename}}
        </div>
        <div class="subTitle">
          <!--{{data.subhead}}-->
        </div>
        <div class="teacher">
          <!--{{data.teachername}}-->
        </div>
        <div class="price">
          <template v-if="data.classtypes==1 ||(data.classtypes==2 && data.price==data.maxprice) ">
            ￥{{data.price}}/元
          </template>
          <template v-else>
            ￥{{data.price}}-{{data.maxprice}}/元
          </template>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'MyCollCourse',
    props:{
      data:{
        type:Object,
        default(){
          return {};
        }
      },
      type:{
        type:String,
        default(){
          return 'coll'
        }
      }
    },
    methods:{
      toPage(){
        console.log(this.data);
        this.$router.push({path:'/CourseDetails',query:{'courseid':this.data.curriculumid,'classtypes':this.data.classtypes}});
      }
    }
  }
</script>
<style lang="scss" scoped>
  .MyCollCourse {
    height: 200px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 20px 36px 18px 25px;
    border-bottom: 1px solid rgba(217, 217, 217, 1);
  }
  .MyOrderCourse{
    height: 200px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 20px 36px 18px 25px;
  }
    .imgShow{
      width:280px;
      height:160px;
      border-radius:8px;
      float:left;
      margin-right:21px;
    }
    .infoContent{
      height:160px;
      width: calc(100% - 362px);
      float:left;
      /*background:red;*/
      .title{
        height:49px;
        font-size:30px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(77,77,77,1);
      }
      .subTitle{
        height:43px;
        font-size:26px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(77,77,77,1);
      }
      .teacher{
        height:46px;
        font-size:26px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(107,107,107,1);
      }
      .price{
        font-size:26px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        text-align: right;
        color:rgba(231,19,19,1);
      }
    }
</style>



