<template>
  <div class="logoSearch" >
    <div class="leftLogo"></div>
    <div class="inputOut" @click="toPage">
      <div class="searchIcon"></div>
      <div class="titleInfo" >
        请输入关键字
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name:'logoSearch',
    methods:{
      toPage(){
        this.$router.push('/SearchCourse');
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .logoSearch{
    height:94px;
    box-sizing: border-box;
    padding:15px 28px 15px 24px;
    background:rgba(255,255,255,1);
    .leftLogo{
      height:64px;
      width:181px;
      margin-top:5px;
      float:left;margin-right:37px;
      @include bg-image("../../../Main/assets/icon_logo");
    }
    .inputOut{
      width:480px;
      margin-top:5px;
      height:64px;
      float:left;
      box-sizing: border-box;
      background:rgba(242,242,242,1);
      border-radius:31px;
      padding-left:18px;
      .searchIcon{
        height:30px;
        width:30px;
        float:left;
        margin-top:17px;
        @include bg-image("~@/assets/images/icon_search");
        margin-right:23px;
      }
      .titleInfo{
        height:64px;
        line-height: 64px;
        float:left;
        font-size:24px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(166,166,166,1);
      }
    }
  }
</style>



