<template>
  <!--课堂反馈-->
  <div class="WonderfulMoment">
    <!--头部导航-->
    <headerBack title="课堂反馈"></headerBack>
    <!--<mt-navbar v-model="selected" >-->
      <!--<mt-tab-item id="1" value="1">我的点评</mt-tab-item>-->
      <!--<mt-tab-item id="2">精彩瞬间</mt-tab-item>-->
    <!--</mt-navbar>-->
    <!--<verticalScroll scrollType="Tab"  :data="vScrollData">-->
      <!--<mt-tab-container :swipeable="true" v-model="selected">-->
        <!--<mt-tab-container-item id="1">-->
          <!--<div v-infinite-scroll=""-->
               <!--infinite-scroll-disabled="loading"-->
               <!--infinite-scroll-distance="10"-->
               <!--class="content"-->
          <!--&gt;-->
            <!--&lt;!&ndash;我的点评组件&ndash;&gt;-->
            <!--<reviewList :data="vScrollData.listInfo"></reviewList>-->
          <!--</div>-->
        <!--</mt-tab-container-item>-->
        <!--<mt-tab-container-item id="2">-->
          <!--<div v-infinite-scroll=""-->
               <!--infinite-scroll-disabled="loadingReceive"-->
               <!--infinite-scroll-distance="10"-->
               <!--class="content"-->
          <!--&gt;-->
            <!--&lt;!&ndash;精彩瞬间组件&ndash;&gt;-->
            <!--<marvellousList :data="vScrollData.marvellousInfo"></marvellousList>-->
          <!--</div>-->
        <!--</mt-tab-container-item>-->
      <!--</mt-tab-container>-->
    <!--</verticalScroll>-->
    <verticalScroll >
      <!--我的点评组件-->
      <reviewList :data="listInfo"></reviewList>
    </verticalScroll>

    <notInfo v-if="infoStatus">
      抱歉，没有反馈信息
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import reviewList from '../components/ReviewList';      //我的点评组件
  import marvellousList from '../components/MarvellousList';      //行列表
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import notInfo from '@/components/NotInfo';           //没有数据
  import { Navbar, TabItem ,TabContainer, TabContainerItem} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'WonderfulMoment',
    components:{
      headerBack,
      reviewList,
      marvellousList,
      verticalScroll,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      notInfo
    },
    data(){
      return{
        infoStatus:false,
        listInfo:[],    //用户列表
        selected:'1',
        studentid:'',     //学生ID

      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 点评列表
      this.myCommentList();
    },
    methods:{
      // 请求点评列表
      myCommentList(){
        Server.post(Path.myCommentList,{
          'studentid':this.studentid,
          'pagenum':1,
          'limit':10
        },(res)=>{
          console.log(res);
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0){
              this.infoStatus = true;
            }else{
              this.listInfo = data.data;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>

</style>



