<template>
  <div >
    <div class="MyWorkNone"  v-for="(item,key) in data" :key="key">
      <div class="leftLine"></div>
      <div class="rightLine"></div>
      <div class="title">
        <!--未完成-->
        <div class="blueTitle">
          <span></span>
          共{{item.subjectcount}}题
          <div class="littleBlock">
            <span></span>
          </div>
        </div>
      </div>
      <div class="infoContent">
        <div class="infoTitle">
          {{item.gradename}}
        </div>
        <div class="writeWork">
          {{item.coursename}}
          <div class="write">
            <span v-if="type===0" @click="toPage(item.lessonid,item.classid,'')">
              写作业
            </span>
            <span v-if="type===1" >
              <div v-if="item.sendstatus===0"  @click="sendHomeworkMessage(item.homeworkid)">
                提醒
              </div>
               <template v-if="item.sendstatus===1">
                已提醒
              </template>
            </span>
            <span v-if="type===2" @click="toPage(item.lessonid,item.classid,'show')">
              查看
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyWorkNone',
    props:{
      data:{
        type:Array,
        default(){
          return [];
        }
      },
      type:{
        type:Number,
        default(){
          return 0;
        }
      }
    },
    methods:{
      toPage(lessonid,classid,from){
        this.$router.push({path:'/MyWorkInfo',query:{lessonid:lessonid,classid:classid,from:from}});
      },
      // 提醒老师批阅
      sendHomeworkMessage(homeworkid){
        Server.post(Path.sendHomeworkMessage,{
          'homeworkid':homeworkid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.$emit('aginSend')
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .MyWorkNone{
    position:relative;
    margin:30px auto 0px;
    width:662px;
    height:242px;
    background:rgba(255,255,255,1);
    border-radius:12px;
    box-shadow:0px 2px 6px 0px rgba(79,79,79,0.1);
    .leftLine{
      width:2px;
      height:242px;
      background:rgba(255,255,255,1);
      border:1px solid rgba(245,154,139,1);
      opacity:0.6;
      float:left;
      margin-left:20px;
    }
    .title{
      height:62px;
      line-height:62px;
      text-align: right;
      box-sizing: border-box;
      position:relative;
      padding-right:58px;
      border-bottom:1px solid rgba(217,217,217,1);
      font-size:24px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(255,164,0,1);
      .blueTitle{
        left:-16px;
        top:6px;
        position:absolute;
        text-align: left;
        width:137px;
        height:50px;
        line-height:50px;
        background:rgba(88,178,235,1);
        border-radius:0px 25px 25px 0px;
        font-size:26px;
        font-family:PingFang-SC-Medium;
        font-weight:normal;
        color:rgba(255,255,255,1);
        span{
          display:inline-block;
          width:8px;
          height:8px;
          background:rgba(255,255,255,1);
          border-radius:50%;
          margin-left:12px;
        }
        .littleBlock{
          width: 0px;
          height: 0px;
          position:absolute;
          left:0px;
          bottom:-14px;
          border-color:   rgba(63,145,197,1) rgba(63,145,197,1) transparent transparent ;
          border-width: 8px 8px 8px 8px;
          border-style: solid;
        }
      }
    }
    .infoContent{
      height:179px;
      box-sizing:border-box;
      /*padding:0px 20px 0px 96px;*/
      .infoTitle{
        height:82px;
        line-height:82px;
        padding-left:97px;
        box-sizing: border-box;
        border-bottom:1px solid rgba(217,217,217,1);
        font-size:30px;
        font-family:PingFang-SC-Regular;
        font-weight:normal;
        color:rgba(77,77,77,1);
      }
      .writeWork{
        height:96px;
        line-height:96px;
        padding-left:97px;
        padding-right:56px;
        font-size:26px;
        font-family:PingFang-SC-Regular;
        font-weight:normal;
        color:rgba(128,128,128,1);
        .write{
          height:96px;
          line-height:96px;
          float:right;
          span{
            display:inline-block;
            width:102px;
            height:42px;
            line-height:42px;
            text-align: center;
            background:rgba(255,164,0,1);
            border:1px solid rgba(255,164,0,1);
            border-radius:20px;
            font-size:24px;
            font-family:MicrosoftYaHei;
            font-weight:400;
            color:rgba(254,254,254,1);
          }
        }
      }
    }
  }
</style>



