<template>
  <div id="FootNav">
    <ul class="footbar">
      <router-link class="icon tab_home" tag="li" :to="{path:'/'}" exact active-class="tab_home_active">首页</router-link> <!-- 首页 -->
      <router-link class="icon tab_lesson" tag="li" :to="{path:'/AllCourses'}" exact active-class="tab_lesson_active" >课程</router-link> <!-- 课程 -->
      <router-link class="icon tab_mine" tag="li" :to="{path:'/Mine'}" exact  active-class="tab_mine_active">我的</router-link> <!-- 我的 -->
    </ul>
  </div>
</template>
<script>
  export default {
    name: "FootNav",
    components: {
    }
  };
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable.scss";
  .footbar {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    height: 98px;
    background: $color-white;
    -webkit-box-shadow: 4px 0 6px rgba(182, 182, 182, 0.23);
    -moz-box-shadow: 4px 0 6px rgba(182, 182, 182, 0.23);
    box-shadow: 4px 0 6px rgba(182, 182, 182, 0.23);
    display: flex;
    justify-content: space-around;
    .icon {
      display: inline-block;
      flex: 1;
      padding: 60px 0 0 0;
      font-size: $font-size-20;
      text-align: center;
    }
    @each $iconNormal in tab_home, tab_lesson, tab_lessonlist, tab_mine {
      .#{$iconNormal} {
        background: url("./assets/images/#{$iconNormal}.png");
        background-size: 48px 48px;
        background-repeat: no-repeat;
        background-position: center 10px;
      }
    }
    @each $iconActive in tab_home_active, tab_lesson_active, tab_lessonlist_active,
    tab_mine_active
    {
      .#{$iconActive} {
        background: url("./assets/images/#{$iconActive}.png");
        background-size: 48px 48px;
        background-repeat: no-repeat;
        background-position: center 10px;
        color: $color-green;
      }
    }
    @media (-webkit-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
      @each $iconNormal in tab_home, tab_lesson, tab_lessonlist, tab_mine {
        .#{$iconNormal} {
          background: url("./assets/images/#{$iconNormal}@2x.png");
          background-size: 48px 48px;
          background-repeat: no-repeat;
          background-position: center 10px;
        }
      }
      @each $iconActive in tab_home_active, tab_lesson_active,
      tab_lessonlist_active, tab_mine_active
      {
        .#{$iconActive} {
          background: url("./assets/images/#{$iconActive}@2x.png");
          background-size: 48px 48px;
          background-repeat: no-repeat;
          background-position: center 10px;
          color: $color-green;
        }
      }
    }
  }
</style>



