<template>
  <!--添加地址-->
  <div  class="addAddress">
    <!--头部导航-->
    <headerBack title="地址添加"></headerBack>
    <div class="item">
      收货人 <input class="inputClass" placeholder="请填写收货人名称"   v-validate="'required'" name="nickname" maxlength="25" v-model="linkman" type="text">
    </div>
    <div class="errorShow" v-show="errors.has('nickname')">
      {{ errors.first('nickname') }}
    </div>
    <div class="item">
      手机号码 <input class="inputClass" placeholder="请输入手机号" v-validate="'required|phone'"  name="phone"  minlength="6"  maxlength="11"   v-model="mobile" >
    </div>
    <div class="errorShow" v-show="errors.has('phone')">
      {{ errors.first('phone') }}
    </div>
    <div class="item">
      邮编 <input class="inputClass"  data-vv-as="邮编"   v-validate="{ required: true, regex: /^\d{6}$/ }" name="zipcode" placeholder="请输入邮编" v-model="zipcode" type="text">
    </div>
    <div class="errorShow" v-show="errors.has('zipcode')">
      {{ errors.first('zipcode') }}
    </div>
    <div class="item"  >
      地区 <span @click="openProvince"  >
              <span v-if="!province">省市区</span>
              <span style="padding-right:3px;">{{getCity(province)}}</span>
              <span style="padding-right:3px;">{{getCity(city)}}</span>
              <span style="padding-right:3px;">{{getCity(area)}}</span>
          </span>
    </div>
    <div class="itemAddress">
      详细地址 <span>省街道、楼牌号等</span>
      <div>
        <input v-model="address" class="inputAddress"   v-validate="'required'" maxlength="50"  name="address"  type="text" placeholder="请输入详细地址">
      </div>
    </div>
    <div class="errorShow" v-show="errors.has('address')">
      {{ errors.first('address') }}
    </div>
    <div class="item">
      是否设为默认地址
      <div  style="float:right;margin-top:13px;margin-right:15px;">
        <mt-switch v-model="value"></mt-switch>
      </div>
    </div>
    <div class="saveButton"  @click="addOrUpdateAddress">
      保 存
    </div>

    <!--省选择-->
    <mt-popup v-model="popupProvince" position="right" style="width:100%;height:100%;">
      <province :popupVisible="popupProvince"   :countryID="country"  @selectProvince="selectProvince"  @closeModal="closeProvince"></province>
    </mt-popup>
    <!--城市选择-->
    <mt-popup v-model="popupCity" position="right" style="width:100%;height:100%;">
      <city :popupVisible="popupCity"    from="address" :provinceID="province"  @selectCity="selectCity"  @closeModal="closeCity"></city>
    </mt-popup>
    <!--区域选择-->
    <mt-popup v-model="popupArea" position="right" style="width:100%;height:100%;">
      <areas :popupVisible="popupArea"    :areaID="city"  @selectArea="selectArea"  @closeModal="closeArea"></areas>
    </mt-popup>

  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import { Switch ,Popup,Toast} from 'mint-ui';
  import province from '@/components/Province';     //国家三级联动
  import city from '@/components/Citys';     //国家三级联动
  import areas from '@/components/Area';     //国家三级联动
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import CityJSON from '@/assets/city/city.json';
  export default{
    name:'addAddress',
    components:{
      headerBack,
      'mt-switch':Switch,
      'mt-popup':Popup,
      province,
      city,
      areas
    },
    data(){
      return {
        mobile:'',   //手机号
        linkman:'',   //联系人
        address:'',   //详细地址
        country:'',       //国家的ID
        province:'110000',      //省份的ID
        city:'110100',          //城市ID
        area:'110101',          //区域ID
        popupProvince:false,   //选择省份
        popupCity:false,    //选择城市
        popupArea:false,    //选择区域
        value:false,  //单选框
        zipcode:'',   //邮编
        nowCity:'省市区',
        addressID:'',   //列表ID
        addressInfo:{},  //修改的信息
      }
    },
    created(){
      this.addressInfo = this.$route.query.item;
      if(this.addressInfo){
        this.addressID = this.addressInfo.id;
        this.city = this.addressInfo.cityid;
        this.province = this.addressInfo.pid;
        this.area = this.addressInfo.areaid;
        this.linkman = this.addressInfo.linkman;
        this.mobile = this.addressInfo.mobile;
        this.zipcode = this.addressInfo.zipcode;
        this.address = this.addressInfo.address;
        this.value = this.addressInfo.isdefault==0?false:true;
      }
    },
    methods:{
      // 保存地址信息
      addOrUpdateAddress(){
        this.$validator.validate().then(result => {
          if (!result) {
            return;
          }else{
            Server.post(Path.addOrUpdateAddress,{
              'pid':this.province,
              'cityid':this.city,
              'areaid':this.area,
              'address':this.address,
              'linkman':this.linkman,
              'mobile':this.mobile,
              'zipcode':this.zipcode,
              'isdefault':this.value?1:0,
              'id':this.addressID
            },(res)=>{
              let { code, data } = res;
              if(code===0){
                this.$router.go(-1);
              }else{
                Toast({
                  message:res.info,
                  iconClass: 'icon icon-error',
                  duration: '1800'
                });
              }
            });
          }
        });

      },
      //城市转换
      getCity: function(val) {
        if(val == 0 || val == null){
          return
        }else{
          CityJSON.map((el,index) => {
            if(el.id == val){
              val =el.name
            }
          })
          return val;
        }
      },
      // 打开省选择
      openProvince(){
        this.popupProvince = true;
        this.country = 7;
      },
      // 关闭省份的选择
      closeProvince(){
        this.popupProvince = false;
      },
      // 选择的省份ID
      selectProvince(value){
        // 记录选择的省份ID
        this.province = value;
        // 打开城市选择
        this.popupCity = true;
      },
      // 选择的城市ID
      selectArea(value){
        this.area = value;
        this.popupProvince = false;
        this.popupCity = false;
        this.popupArea = false;
      },
      // 关闭城市的选择器
      closeCity(){
        this.popupCity = false;
      },
      // 关闭区域选择
      closeArea(){
        this.popupArea = false;
      },
      // 选择城市
      selectCity(value){
        // 记录选择的城市
        this.city = value;
        // 打开区域的选择
        this.popupArea = true;
      },
    }
  }
</script>
<style scoped lang="scss">
  .errorShow{
    padding-left:40px;
    height:40px;
    line-height:40px;
    font-size:24px;
    color:red;
  }
  span{
    font-size:30px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(153,153,153,1);
  }
  .addAddress{
    position:absolute;
    top:0;
    bottom:0;
    right:0;
    left:0;
    background:rgba(255,255,255,1);
    .item{
      width: calc(100% - 50px);
      margin:0px auto;
      height:101px;
      padding-left:12px;
      line-height:101px;
      border-bottom:1px solid rgba(191,191,191,1);
      font-size:30px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(77,77,77,1);
      clear:both;
    }
    .itemAddress{
      width: calc(100% - 50px);
      margin:0px auto;
      height:201px;
      padding-left:12px;
      line-height:101px;
      border-bottom:1px solid rgba(191,191,191,1);
      font-size:30px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(77,77,77,1);
    }
    .inputClass{
      height:101px;
      line-height: 101px;
      width: calc(100% - 350px);
    }
    .inputAddress{
      height:101px;
      line-height: 101px;
      width: calc(100% - 100px);
    }
    .saveButton{
      width:400px;
      height:70px;
      line-height:70px;
      text-align: center;
      position:fixed;
      bottom:30px;
      left:calc(50% - 200px);
      background:rgba(63,156,232,1);
      border-radius:20px;
      font-size:30px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      color:rgba(255,255,255,1);
    }
  }
</style>



