<template>
  <!--个性签名-->
  <div class="Autograph">
    <!--头部导航-->
    <div id="headerBack">
      <div class="Headbar-back" @click="closeModal">
      </div>
      <div v-if="maxLength==10" class="Headbar-title">昵称修改</div>
      <div v-if="maxLength==100" class="Headbar-title">个性签名</div>
    </div>
    <div class="infContent">
      <!--input-->
      <div class="inputOut" v-if="maxLength==10">
        <input type="text" class="input" v-model="nowValue"  :maxlength="maxLength">
        <div class="rightNumber">
          <span >{{nowValue.length}}/{{maxLength}}</span>
        </div>
      </div>
      <div v-if="maxLength==100" class="textOut">
        <textarea  class="textBlock"    v-model="nowValue"></textarea>
        <div class="rightNumber" style="text-align: right;">
          <span >{{nowValue.length}}/{{maxLength}}</span>
        </div>
      </div>
      <!--info详情-->
      <div v-if="maxLength==10" class="info">
        好名字可以让别人更容易记住你
      </div>
      <!--save-->
      <div class="saveButton"  @click="goBack">
        保存
      </div>
    </div>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  export default{
    name:'Autograph',
    components:{
      headerBack
    },
    data(){
      return {
        nowValue:''
      }
    },
    watch:{
      value:function(value){
        this.nowValue = value;
      },
      nowValue:function(value){
        if(value.length>100){
          this.nowValue = this.nowValue.slice(0,100);
          return;
        }
      }
    },
    props:{
      // 最多输入字数
      maxLength:'',
      // value值
      value:{
        type:String,
        default(){
          return '';
        }
      }
    },
    mounted(){
    },
    methods:{
      changeText(){
        if(this.nowValue.length>15){
          return;
        }
      },
      // 关闭模态框
      closeModal(){
        this.$emit('closeModal');
      },
      goBack(){
        this.$emit('saveValue',this.nowValue);
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  @import "~@/assets/common/mixin";
  .textOut{
    width:670px;
    margin:0px auto;
  }
  .textBlock{
    box-sizing: border-box;
    padding:10px 7px;
    margin:0px auto;
    width:670px;
    font-size:20px;
    font-family:MicrosoftYaHei;
    font-weight:400;
    color:rgba(51,51,51,1);
    min-height:200px;
    line-height:34px;
    border:1px solid rgba(217,217,217,1);
  }
  #headerBack {
    z-index: 3;
    position: relative;
    height: 92px;
    line-height: 92px;
    font-size: $font-size-36;
    text-align: center;
    background-color: $color-white;
    color: $color-black;
    position:relative;
    .confirmArea {
      font-size: 30px;
      color: #878787;
      position: absolute;
      top:0px;
      right: 31px;
    }
    @extend .boxShadow;
    .Headbar-back {
      position: absolute;
      left: 17px;
      top: 20px;
      display: inline-block;
      width: 48px;
      height: 48px;
      @include bg-image('~@/components/assets/nav_icon_back');
      background-repeat: no-repeat;
      background-size: cover;
    }
  }
  .Autograph{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:rgba(255,255,255,1);
    .infContent{
      padding:47px 36px 0px 36px;

      .inputOut{
        height:90px;
        width: calc(100%);
        box-sizing: border-box;
        border-bottom:1px solid rgba(57,173,239,1);
        padding-left:5px;
        font-size:34px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(61,61,61,1);
        .input{
          height:89px;
          height:89px;
          width: calc(100% - 180px);
        }
        .rightNumber{
          width:90px;
          height:41px;
          float:right;
          text-align: right;
          margin-top:48px;
          font-size:28px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(179,179,179,1);
        }
      }
      .info{
        margin-top:24px;
        font-size:28px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(179,179,179,1);
      }
      .saveButton{
        width:676px;
        height:88px;
        line-height:88px;
        text-align: center;
        background:rgba(57,173,239,1);
        border-radius:8px;
        font-size:32px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        margin:72px auto 0px;
        color:rgba(255,255,255,1);
      }

    }
  }
</style>



