<template>
  <!--我的订单-->
  <div class="MyOrder">
    <headerBack title="我的订单"></headerBack>
    <mt-navbar :value="selected" v-model="selected" >
      <mt-tab-item id="0" value="1">课程订单</mt-tab-item>
      <mt-tab-item id="1">套餐订单</mt-tab-item>
    </mt-navbar>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="0">
            <div infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content myCellSwipe">
              <div v-for="(item,key) in courseInfo" :key="key">
                <mt-cell-swipe class="orderItem"
                               :right="item.orderstatus=='0'?[
                      {content: '取消订单',style: {background: 'rgb(255, 121, 0)', color: 'white'},handler:() => cancelOrderCourse(item.ordernum,key)}
                      ]:[]"  >
                  <div  @click="toSignUp(item.ordernum,item.orderstatus,item.curriculumid,item.coursetype)">
                    <div class="titleBar">
                      <div class="leftOrder text-ellipsis">
                        <span class="circleBlock"></span>
                        <span class="titleNumber">
                          订单编号：{{item.ordernum}}
                        </span>
                      </div>
                      <div class="rightTitle">
                        <span style="color:#FFD700;" v-if="item.orderstatus == '0'">
                          去支付
                        </span>
                        <template v-else-if="item.orderstatus == '10'">
                          已取消
                        </template>
                        <template v-else-if="item.orderstatus == '20'">
                          已支付
                        </template>
                        <template v-else>
                          申请退款
                        </template>
                      </div>
                    </div>
                    <div class="infoCar">
                      <div class="imgShow">
                        <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
                      </div>
                      <div class="infoContent">
                        <div class="title text-ellipsis">
                          {{item.coursename}}
                        </div>
                        <div class="subTitle text-ellipsis">
                          {{item.classname}}
                        </div>
                        <div class="teacher text-ellipsis">
                          {{item.teachername}}
                        </div>
                        <div class="price text-ellipsis">
                          <span class="allPrice">总价:</span>
                          <span class="priceNumber">￥{{item.amount}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </mt-cell-swipe>
              </div>


            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10"  class="content myCellSwipe" >
              <div v-for="(item,key) in mealInfo" :key="key">
                <mt-cell-swipe class="orderItem"
                               :right="item.orderstatus=='0'?[
                      {content: '取消订单',style: {background: 'rgb(255, 121, 0)', color: 'white'},handler:() => cancelOrder(item.ordernum,key)}
                      ]:[]"   >
                  <div @click="toMealDetail(item.ordernum,item.orderstatus,item.packageid)">
                    <div class="titleBar">
                      <div class="leftOrder text-ellipsis">
                        <span class="circleBlock"></span>
                        <span class="titleNumber">
                          订单编号：{{item.ordernum}}
                        </span>
                      </div>
                      <div class="rightTitle">
                        <span style="color:#FFD700;" v-if="item.orderstatus == '0'">
                          去支付
                        </span>
                        <template v-else-if="item.orderstatus == '10'">
                          已取消
                        </template>
                        <template v-else-if="item.orderstatus == '20'">
                          已支付
                        </template>
                        <!--<template v-else>-->
                        <!--申请退款-->
                        <!--</template>-->
                      </div>
                    </div>
                    <div class="infoCar">
                      <div class="imgShow">
                        <img v-lazy="item.setimgpath" width="100%" height="100%" alt="">
                      </div>
                      <div class="infoContent">
                        <div class="title">
                          {{item.setmeal}}
                        </div>
                        <div class="subTitle">
                          <!--{{item.content}}-->
                        </div>
                        <div class="teacher text-ellipsis">
                          {{item.content}}
                        </div>
                        <div class="price text-ellipsis">
                          <span class="allPrice">总价:</span>
                          <span class="priceNumber">￥{{item.amount}}</span>
                        </div>
                      </div>
                    </div>
                  </div>
                </mt-cell-swipe>
              </div>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <notInfo v-if="infoStatus">
      抱歉，没有订单
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import { Loadmore,Navbar, Popup,TabItem ,TabContainer, TabContainerItem,Toast,CellSwipe} from 'mint-ui';
  import myCollCourse from '../components/MyCollCourse';      // course item
  import Server from '@/service/request';
  import notInfo from '@/components/NotInfo';           //没有数据
  import Path from '@/service/Path';
  export default{
    name:'MyOrder',
    components:{
      headerBack,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-cell-swipe':CellSwipe,
      'mt-popup':Popup,
      myCollCourse,
      'mt-loadmore':Loadmore,
      notInfo
    },
    data(){
      return {
        selected:'0',   //默认显示课程订单
        mealInfo:[],      //套餐订单数据
        courseInfo:[],      //课程订单数据

        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      if(window.localStorage.getItem('selectedOrder') && window.localStorage.getItem('selectedOrder')==1){
        this.selected = window.localStorage.getItem('selectedOrder');
        return;
      }
      // 查询课程订单列表
      this.getMyOrderList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          if(this.selected=='0'){
            this.getMyOrderList();
          }else{
            this.getPackageOrderList();
          }
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            if(this.selected=='0'){
              this.getMyOrderList();
            }else{
              this.getPackageOrderList();
            }
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 订单如果成功前往套餐详情页
      toMealDetail(ordernum,type,packageid){
        if(type=='0'){
          // this.$router.push({'path':'MealInfo',query:{'packageid':id}});
          this.$router.push({path:"ShowOrder",query:{orderNum:ordernum}})
        }else if(type=='20'){
          this.$router.push({'path':'MealInfo',query:{'packageid':packageid,from:'order'}})
        }
      },
      // 课程订单
      toSignUp(ordernum,type,courseid,classtypes){
        if(type=='0'){
          this.$router.push({path:'SignUp',query:{ordernum:ordernum}});
        }else if(type=='20'){
          this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':JSON.stringify(classtypes),from:'order'}});
        }

      },
      // 取消套餐订单
      cancelOrder(orderNum,key){
        Server.post(Path.cancelOrder,{
          'ordernum':orderNum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 删除本地数据条目
            this.mealInfo.splice(key,1);
            if(this.mealInfo.length==0){
              this.infoStatus = true;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 取消课程订单
      cancelOrderCourse(orderNum,key){
        Server.post(Path.cancelOrderCourse,{
          'ordernum':orderNum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            // 删除本地数据条目
            this.pagenum =1;
            this.getMyOrderList();
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 查询套餐订单列表
      getPackageOrderList(){
        Server.post(Path.getPackageOrderList,{
        'pagenum':this.pagenum
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;

            if (this.pagenum != 1 ) {
              this.mealInfo = this.mealInfo.concat(data.data);
            } else {
              this.mealInfo = data.data;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 查询课程订单列表
      getMyOrderList(){
        Server.post(Path.getMyOrderList,{
          'pagenum':this.pagenum,
          'limit':this.limit,
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;

            if (this.pagenum != 1 ) {
                this.courseInfo = this.courseInfo.concat(data.data);
            } else {
                this.courseInfo = data.data;
            }
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        this.pagenum = 1;
        window.localStorage.setItem('selectedOrder', this.selected);
        if(value==='0'){
          // 查询课程订单
          this.getMyOrderList();
        }else{
          //查询套餐订单
          this.getPackageOrderList();
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyOrder{

    .orderItem{
      box-sizing:border-box;
      height:295px;
      background:rgba(255,255,255,1);
      border-radius:12px;
      /*padding:0px 38px 30px 37px;*/
      .titleBar{
        height:74px;
        box-sizing:border-box;
        line-height:74px;
        padding:0px 37px;
        border-top:1px solid rgba(217,217,217,1);
        border-bottom:1px solid rgba(217,217,217,1);
        .circleBlock{
          vertical-align: middle;
          width:10px;
          height:10px;
          display:inline-block;
          background:rgba(77,77,77,1);
          border-radius:50%;
        }
        .titleNumber{
          vertical-align: middle;
          font-size:30px;
          font-family:PingFang-SC-Medium;
          font-weight:normal;
          color:rgba(61,61,61,1);
        }
        .leftOrder{
          float:left;
          width: calc(100% - 180px);
        }
        .rightTitle{
          height:74px;
          width:100px;
          line-height:74px;
          float:right;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(153,153,153,1);
        }
      }
      .infoCar{
        height:220px;
        padding:30px 37px;
        .imgShow{
          width:280px;
          height:160px;
          float:left;
          overflow:hidden;
          border-radius:8px;
          margin-right:33px;
        }
        .infoContent{
          height:160px;
          width: calc(100% - 387px);
          float:left;
          padding-top:8px;
          font-size:24px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(102,102,102,1);
          .title{
            height:39px;
            font-size:32px;
            font-family:Adobe Heiti Std R;
            font-weight:normal;
            color:rgba(51,51,51,1);
          }
          .subTitle{
            height:34px;
          }
          .teacher{
            height:50px;
          }
          .price{
            height:25px;
            line-height:25px;
            float:right;
            .allPrice{
              margin-right:10px;
            }
            .priceNumber{
              font-size:24px;
              font-family:MicrosoftYaHei;
              font-weight:400;
              color:rgba(233,29,29,1);
            }
          }
        }
      }
    }
  }
</style>



