<template>
  <div class="Mine">
    <!--头部返回导航-->
    <headerBack title="个人中心"></headerBack>
      <verticalScroll >
        <mineCard></mineCard>
        <!--<router-link :to="{path:item.router}" v-for="(item,key) in dataInfo" :key="key">-->
          <div class="itemLine" v-for="(item,key) in dataInfo"  @click="toPage(item.router)">
            <div class="iconShow bgUrl">
              <!--<img src="~@/assets/images/teacher.png" width="100%" alt="">-->
            </div>
            <div class="title">
              {{item.title}}
            </div>
            <div class="rightIcon">
              <div>
              </div>
            </div>
          </div>
        <!--</router-link>-->
      </verticalScroll>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部导航
  import verticalScroll from '@/components/VerticalScroll';   //better-scroll
  import mineCard from './components/MineCard';      //信息卡片
  export default{
    name:'Mine',
    components:{
      headerBack,
      verticalScroll,
      mineCard
    },
    created(){
    },
    data(){
      return{
        dataInfo:[
          {
            title:"我的余额",
            router:'/MyBalance'
          },
          {
            title:"我的课表",
            router:'/MyTimetable'
          },
          {
            title:"我的课程",
            router:'/MyCourse'
          },
          {
            title:"我的订单",
            router:'/MyOrder'
          },
          {
            title:"我的作业",
            router:'/MyHomework'
          },{
            title:"我的收藏",
            router:'MyCollection'
          },
          {
            title:"我的消息",
            router:'/MyNews'
          },

          {
            title:"课堂反馈",
            router:'/WonderfulMoment'
          },
          {
            title:"APP学习",
            router:'/AppDownload'
          },
          {
            title:"我的套餐",
            router:'/SetMeal'
          },
          // {
          //   title:"用户设置",
          //   router:'/SetMeal'
          // }

        ]
      }
    },
    methods:{
      toPage(item){
        this.$router.push(item);
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/variable";
  @import "~@/assets/common/mixin";
  .Mine{
      @extend .main-child;
    .itemLine:nth-of-type(2) .bgUrl{
      @include bg-image("./assets/icon_balance");
    }
    .itemLine:nth-of-type(3) .bgUrl{
      @include bg-image("./assets/icon_timeTable");
    }
    .itemLine:nth-of-type(4) .bgUrl{
      @include bg-image("./assets/icon_course");
    }
    .itemLine:nth-of-type(5) .bgUrl{
      @include bg-image("./assets/icon_order");
    }
    .itemLine:nth-of-type(6) .bgUrl{
      @include bg-image("./assets/icon_task");
    }
    .itemLine:nth-of-type(7) .bgUrl{
      @include bg-image("./assets/icon_course");
    }
    .itemLine:nth-of-type(8) .bgUrl{
      @include bg-image("./assets/icon_news");
    }
    .itemLine:nth-of-type(9) .bgUrl{
      @include bg-image("./assets/icon_feedback");
    }
    .itemLine:nth-of-type(10) .bgUrl{
      @include bg-image("./assets/icon_app");
    }
    .itemLine:nth-of-type(11) .bgUrl{
      @include bg-image("./assets/icon_collect");
    }
    .itemLine:nth-of-type(12) .bgUrl{
      @include bg-image("./assets/icon_setUp");
    }
    .itemLine{
      box-sizing:border-box;
      height:88px;
      padding:16.5px 37px 16.5px 47px;
      background:rgba(255,255,255,1);
      border-bottom:1px solid rgba(217,217,217,1);
      .iconShow{
        height:55px;
        width:55px;
        float:left;
        overflow: hidden;
        margin-right:25px;
        border-radius:50%;
        overflow:hidden;
      }
      .bgUrl{
      }
      .title{
        height:55px;
        line-height: 55px;
        float:left;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:normal;
        color:rgba(77,77,77,1);
      }
      .rightIcon{
        height:55px;
        display:flex;
        align-items: center;
        float:right;
        div{
          width:14px;
          height:28px;
          margin-top:13px;
          @include bg-image("~@/assets/images/icon_right");
        }
      }
    }
  }
</style>



