<template>
  <!--我的收藏-->
  <div class="MyCollection">
    <headerBack title="我的收藏"></headerBack>
    <mt-navbar :value="selected" v-model="selected" >
      <mt-tab-item id="0" value="1">课程</mt-tab-item>
      <mt-tab-item id="1">老师</mt-tab-item>
    </mt-navbar>
    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="0">
            <div  infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content myCellSwipe">
              <!--课程列表-->
              <mt-cell-swipe  v-for="(item,key) in courseInfo"   :key="key"
                              :right="[
                      {content: '取消收藏',style: {background: 'rgb(255, 121, 0)', color: 'white'},handler:() => cancelClassCollect(item.curriculumid)},
                      ]" >
                <myCollCourse   :data="item"  ></myCollCourse>
              </mt-cell-swipe>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div  infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <!--老师列表-->
              <myCollItem :data="teacherInfo"  @cancelTeacherCollect="cancelTeacherCollect"></myCollItem>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <notInfo v-if="infoStatus">
      抱歉，没有收藏信息
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import myCollItem from '../components/MyCollItem';      // teacher item
  import myCollCourse from '../components/MyCollCourse';      // course item
  import {Loadmore, Navbar, TabItem ,TabContainer, TabContainerItem,CellSwipe} from 'mint-ui';
  import notInfo from '@/components/NotInfo';           //没有数据
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyCollection',
    components:{
      headerBack,
      myCollItem,
      myCollCourse,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-cell-swipe':CellSwipe,
      'mt-loadmore':Loadmore,
      notInfo
    },

    data(){
      return{
        studentid:'',     //学生ID
        selected:'0',
        teacherInfo:[],  //老师信息
        courseInfo:[],   //课程信息

        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    created(){
      if(window.localStorage.getItem('selectedCollect') && window.localStorage.getItem('selectedCollect')!=='0'){
        this.selected = window.localStorage.getItem('selectedCollect');
        return;
      }
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      // 默认搜索我的课程
      this.classCollectList();
    },
    methods:{
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          if(this.selected=='0'){
            this.classCollectList();
          }else{
            this.teacherCollectList();
          }
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            if(this.selected=='0'){
              this.classCollectList();
            }else{
              this.teacherCollectList();
            }
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 我的老师列表
      teacherCollectList(){
        Server.post(Path.teacherCollectList,{
          'studentid':this.studentid,
          'pagenum':this.pagenum,
          'limit':10
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;

            if (this.pagenum != 1 ) {
              this.teacherInfo = this.teacherInfo.concat(data.data);
            } else {
              this.teacherInfo = data.data;
            }

          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 我的课程列表
      classCollectList(){
        Server.post(Path.classCollectList,{
          'studentid':this.studentid,
          'pagenum':this.pagenum,
          'limit':10
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;

            if (this.pagenum != 1 ) {
              this.courseInfo = this.courseInfo.concat(data.data);
            } else {
              this.courseInfo = data.data;
            }

          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 取消收藏老师
      cancelTeacherCollect(teacherID){
        Server.post(Path.cancelTeacherCollect,{
          'teacherid':teacherID,
          'studentid':this.studentid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.pagenum = 1;
            this.teacherInfo = [];
            this.teacherCollectList();
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 取消收藏课程
      cancelClassCollect(courseid){
        Server.post(Path.cancelClassCollect,{
          'courseid':courseid,
          'studentid':this.studentid
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            this.pagenum = 1;
            this.courseInfo = [];
            this.classCollectList();
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      }
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedCollect', this.selected);
        this.pagenum = 1;
        if(value==0){
          // 搜索我的课程
          this.classCollectList();
        }else{
          // 搜索我的老师
          this.teacherCollectList();
        }
      }
    },
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyCollection{

  }
</style>



