<template>
  <!--修改手机号-->
  <div class="changePass">
    <headerBack title="修改密码"></headerBack>
    <div class="infoContent">
      <div class="nowPhone">
        当前手机号：+{{prphone}} {{mobile}}
      </div>
      <div class="codeOut">
        <input class="codeInput" v-model="code" v-validate="'required'"  maxlength="8"  name="code" type="text" placeholder="请输入短信验证码">
        <div class="rightTitle" @click="getCode">
          {{codeObj.codeText}}
        </div>
        <div class="lineWidth"></div>
      </div>
      <div class="errorShow" v-show="errors.has('code')">
        {{ errors.first('code') }}
      </div>
      <div class="newPhone">
        <div class="showPhone">
          <input class="phoneInput" v-model="newPass"  data-vv-as="密码"  v-validate="'required|min:6'" maxlength="16" type="text"  name="newPass" placeholder="请输入新密码">
        </div>
      </div>
      <div class="errorShow" v-show="errors.has('newPass')">
        {{ errors.first('newPass') }}
      </div>
      <div class="newPhone">
        <div class="showPhone">
          <input class="phoneInput"  data-vv-as="新密码"  v-validate="{'required': 'true', 'is': newPass}" name="repeatPass" v-model="repeatPass" type="text" placeholder="请再次输入密码">
        </div>
      </div>
      <div class="errorShow" v-show="errors.has('repeatPass')">
        {{ errors.first('repeatPass') }}
      </div>
      <div class="bottomButton"  @click="changePass">
        确定
      </div>
    </div>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Toast,Popup} from 'mint-ui';
  import number from '@/components/Number';     //选择区号
  export default{
    name:'changePhone',
    components:{
      headerBack,
      number,
      'mt-popup':Popup
    },
    data(){
      return {
        prphone:86,  //默认区号
        mobile:'',      //手机号
        code:'',      //手机验证码
        newPass:'',  //新密码
        repeatPass:'',   //重复密码
        codeObj:{
          codeText:'获取验证码' , //发送验证码
          timer:null,
          count:''
        },
      }
    },
    created(){
      this.prephone = this.$route.query.prephone;
      this.mobile = this.$route.query.mobile;
    },
    methods:{
      // 获取短信验证码
      getCode(){
        if(this.mobile.length!=11){
          //请输入正确手机号
          Toast({
            message:'请输入正确手机号',
            iconClass: 'icon icon-error',
            duration: '1800'
          });
          return false;
        }
        // 定义读秒
        const count = 60;
        if(!this.codeObj.timer){
          Server.post(Path.sendUpdateMobileMsg,{
            'mobile':this.mobile,
            'prphone':this.prphone
          },(res)=>{
            console.log(res);
            let { code, data } = res;
            if(code===0){
            }else{
              Toast({
                message:res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
            }
          });
          this.codeObj.count = count;
          this.codeObj.codeText = this.codeObj.count+'s';
          this.codeObj.timer = setInterval(() => {
            if (this.codeObj.count > 0 && this.codeObj.count <= count) {
              this.codeObj.count--;
              this.codeObj.codeText = this.codeObj.count+'s';
            } else {
              clearInterval(this.codeObj.timer);
              this.codeObj.timer = null;
              // 重发验证码
              this.codeObj.codeText = '重新发送';
            }
          }, 1000)
        }
      },
      // 修改密码
      changePass(){
        this.$validator.validate().then(result => {
          if(!result){
            return;
          }else{
            Server.post(Path.updateUserPass,{
              'mobile':this.mobile,
              'code':this.code,
              'newpass':this.newPass
            },(res)=>{
              console.log(res);
              let { code, data } = res;
              if(code===0){
                Toast('密码修改成功');
                this.$router.go(-1);
              }else{
                Toast({
                  message:res.info,
                  iconClass: 'icon icon-error',
                  duration: '1800'
                });
              }
            });
          }
        });



      }
    }
  }
</script>
<style lang="scss" scoped>
  .errorShow{
    height:50px;
    line-height:50px;
    padding-left:25px;
    color:red;
  }
  .changePass{
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    background:rgba(255,255,255,1);
    .infoContent{
      padding:0px 37px;
      .nowPhone{
        height:146px;
        line-height:146px;
        padding-left:14px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(77,77,77,1);
        border-bottom:1px solid rgba(217,217,217,1);
      }
      .newPhone{
        height:117px;
        padding-left:14px;
        box-sizing: border-box;
        padding-top:62px;
        border-bottom:1px solid rgba(217,217,217,1);
        .showPhone{
          height:54px;
          line-height:54px;
          span{
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(77,77,77,1);
          }
          .phoneInput{
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(179,179,179,1);
          }
        }
      }
      .codeOut{
        height:117px;
        padding-left:14px;
        box-sizing: border-box;
        padding-top:62px;
        border-bottom:1px solid rgba(217,217,217,1);
        .codeInput{
          font-size:32px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(179,179,179,1);
        }
        .rightTitle{
          padding:0px 10px;
          height:54px;
          line-height: 54px;
          text-align: right;
          float:right;
          font-size:30px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(16,165,228,1);
        }
        .lineWidth{
          float:right;
          width:0px;
          height:42px;
          margin-top:6px;
          margin-right:21px;
          border-right:1px solid rgba(217,217,217,1);
        }
      }
      .bottomButton{
        width:676px;
        height:108px;
        line-height:108px;
        text-align: center;
        background:rgba(57,173,239,1);
        border-radius:10px;
        font-size:34px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(254,255,255,1);
        margin:68px auto 0px;
      }
    }
  }
</style>



