<template>
  <!--登陆-->
  <div class="Login">
    <!--头部导航-->
    <headerBack title="登录" typeStatus="tittle" rightTitle="注册"  @clickMe="register"></headerBack>
    <!--顶部Logo-->
    <loginIcon></loginIcon>
    <div class="inputCard">
      <div class="phone">
        <div class="phoneIcon">
        </div>
        <div class="input">
          <input type="text" placeholder="手机号码"  v-validate="'required|phone'"  data-vv-as="手机号"  name="phone"  v-model.trim="mobile">
        </div>
        <span class="code"  @click="toNumber">+{{prphone}}&nbsp;<i>></i></span>
      </div>
      <div class="errorShow">
        <span v-show="errors.has('phone')" class="help is-danger">{{ errors.first('phone') }}</span>
      </div>
      <div class="phone">
        <div class="passIcon">
        </div>
        <div class="input">
          <input type="password" placeholder="登录密码"  data-vv-as="密码"  v-validate="'required|min:6'" maxlength="16" name="pass" v-model.trim="pwd">
        </div>
      </div>
      <div class="errorShow">
        <span v-show="errors.has('pass')" class="help is-danger" >{{ errors.first('pass') }}</span>
      </div>
      <!--登陆按钮-->
      <div style="padding-top:13px;padding-bottom:55px;">
        <div class="loginButton" @click="login">
          登录
        </div>
      </div>



      <router-link to="Register">
        <div class="register" @click.passive="register">
          还没有帐号，立即注册
        </div>
      </router-link>



    </div>

    <mt-popup v-model="popupVisible" position="right" style="width:100%;height:100%;">
      <number  :popupVisible="popupVisible"  @closeModal="closeModal"></number>
    </mt-popup>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import loginIcon from '@/components/LoginIcon';     //头部返回导航
  import number from '@/components/Number';     //手机区号选择
  import { Toast,Popup } from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';


  export default{
    name:'Login',
    components:{
      headerBack,
      loginIcon,
      number,
      'mt-popup':Popup
    },
    data(){
      return {
        popupVisible:false,    //选择区号
        mobile:'',  //手机号码
        pwd:'',     //密码
        publickey:'',     //公钥
        prphone:86     //手机区号
      }
    },
    created(){
      localStorage.removeItem('code');     //清楚用户key值
      // 获取公钥
      this.getKey();
    },
    methods:{
      // 选择区号
      toNumber(){
        this.popupVisible = true;
      },
      // 关闭选择区号
      closeModal(info){
        this.popupVisible = false;
        // 给区号赋值
        this.prphone = info.code;
      },
      // 注册
      register(){
        this.$router.push('Register');
      },
      // 登陆
      login(){
        this.$validator.validate().then(result => {
          if (!result) {
            return;
          }else{
            var key = this.randomString(10);
            //type : 1 老师 2机构 3学生 4官方管理员登录
            let jsons = {
              username: this.mobile,
              password: this.pwd,
              source: 'microsite',
              type: 3,
              key: key,
              organid:'1',
              prephone: this.prphone
            };
            var postData = this.wrap_encrypt(JSON.stringify(jsons));
            Server.post(Path.login, {
              data: postData
            }, (res) => {
              console.log(res);
              let {
                code,
                data
              } = res;
              if (code === 0) {
                window.localStorage.setItem('appstudentinfo', JSON.stringify(data));
                window.localStorage.setItem('appstudentkey', key);
                this.$router.push('/');
              } else {
                  Toast({
                    message: res.info,
                    iconClass: 'icon icon-success',
                    duration: '1800'
                  });
              }
            })
          }
        });

      },
      // 生成随机字符串
      randomString(len, charSet) {
        charSet = charSet || "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
        var randomString = "";
        for (var i = 0; i < len; i++) {
          var randomPoz = Math.floor(Math.random() * charSet.length);
          randomString += charSet.substring(randomPoz, randomPoz + 1);
        }
        return randomString;
      },
      //公钥加密
      wrap_encrypt(content) {
        var encryptobj = new JSEncrypt();
        encryptobj.setPublicKey(this.publickey);
        var result = "";
        var step = 117;
        var strlen = content.length;
        var count = Math.ceil(strlen / step);
        for (var i = 0; i < count; i++) {
          let chunk = content.substring(step * i, step * (1 + i));
          result += encryptobj.encrypt(chunk);
        }
        return result;
      },
      //获取公钥
      getKey(){
        Server.post(Path.getPublicKey,{
        },res=>{
          let {code,data}=res;
          if(code===0){
            this.publickey = res.key;
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .Login{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:rgba(255,255,255,1);
    .inputCard{
      padding:0px 65px 0px 66px;
      .errorShow{
        height:90px;
        line-height:90px;
        padding-left:65px;
        color:red;
      }
      .phone{
        height:58px;
        box-sizing: border-box;
        padding-left:10px;
        border-bottom:1px solid rgba(217,217,217,1);
        position:relative;

        .code{
          position: absolute;
          right: 7%;
          top: 50%;
          transform: translateY(-50%);
          color:#C4CCC9;
          font-size: 30px;
          i{
            font-family: '宋体';
            font-weight: bold;
          }
        }
        .phoneIcon{
          height:32px;
          width:32px;
          margin-top:13px;
          float:left;
          line-height:58px;
          margin-right:28px;
          @include bg-image("../assets/icon_phone");
        }
        .passIcon{
          height:32px;
          width:32px;
          margin-top:13px;
          float:left;
          line-height:58px;
          margin-right:28px;
          @include bg-image("../assets/icon_pass");
        }
        .input{
          height:56px;
          width:540px;
          float:left;
          box-sizing: border-box;
          line-height: 56px;
          input{
            width:540px;
            height:56px;
            /*border:1px solid red;*/
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(179,179,179,1);
          }
        }
      }
      /*登陆按钮*/
      .loginButton{
        width:581px;
        height:92px;
        line-height:92px;
        text-align: center;
        margin:0px auto;
        background:rgba(16,165,228,1);
        opacity:0.6;
        border-radius:46px;
        font-size:32px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(255,255,255,1);
      }
      /*注册详情*/
      .register{
        text-align: center;
        font-size:30px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(16,165,228,1);
      }
    }
  }
</style>



