<template>
  <!--滚动组件-->
  <div :class="{'VerticalScroll':scrollType=='Home','VerticalScrolls':scrollType=='Tab','VerticalScrollss':scrollType=='allCourse','bottomScroll':scrollType=='bottomTab'}" ref="verticalWrapper">
    <div class="vertical-group" ref="verticalGroup">
      <slot></slot>
    </div>
  </div>
</template>

<script>
  import BScroll from "better-scroll";

  export default {
    name: "VerticalScroll",
    props: {
      scrollType:{     //false 首页
        type: String,
        default() {
          return 'Home';
        }
      },
      probeType: {
        type: String,
        default() {
          return 'Home';
        }
      },
      /**
       * 点击列表派发click事件
       * @type {Boolean}
       */
      click: {
        type: Boolean,
        default: true
      },
      /**
       * data参数只是用于监听数据是否发生变化
       * @type {Object}
       */
      data: {
        // type: ANY,
        // default: null
      },
      dataList: {
        type: Array,
        default() {
          return []
        }
      },
      // 是否监听滚动位置
      listenScroll: {
        type: Boolean,
        default: false
      },
      //是否开启上拉加载
      pullup: {
        type: Boolean,
        default: true
      },
      //滚动前触发事件
      beforeScroll: {
        type: Boolean,
        default: false
      },
      pullDownRefresh: {
        type: Boolean,
        default: false
      },

    },
    mounted() {
      setTimeout(() => {
        // this.setWrapperHeight();
        this.initScroll();
      }, 20);
    },
    watch: {
      //路由跳转后，重新计算高度
      $route() {
        this.$nextTick(() => {
//        this.setWrapperHeight();
          this.initScroll();
        });
      },
      //当data发生变化的时候，重新计算列表的高度
      data() {
        setTimeout(() => {
          this.refresh();
        }, 100);
      },
      dataList() {
        setTimeout(() => {
          this.refresh();
        }, 100);
      }
    },
    methods: {
      scrollTo(x, y) {
        this.vslider.scrollTo(x, y, 500, "easing");
      },
      refresh() {
        this.vslider && this.vslider.refresh();
      },
      //设置容器高度
      setWrapperHeight() {
        this.children = this.$refs.verticalGroup.children;
        let height = 0;
        let sliderHeight = 0;
        let margin = 0;
        for (let i = 0; i < this.children.length; i++) {
          let child = this.children[i];
          this.addClass(child, "vertical-item");
          margin +=
            Number(this.getStyle(child, "marginTop").slice(0, -2)) +
            Number(this.getStyle(child, "marginBottom").slice(0, -2));
          sliderHeight += child.offsetHeight;
        }
        height = sliderHeight + margin / 2;
        if (this.loop) {
          height += 2 * sliderHeight;
        }
        this.$refs.verticalGroup.style.height = height + "px";
      },
      initScroll() {
        this.vslider = new BScroll(this.$refs.verticalWrapper, {
          scrollY: true, //纵向
          scrollX: false, //横向
          click: true, //是否可点击
          bounce: true, //回弹动画
          probeType: this.probeType
        });

        //派发监听滚动位置
        if (this.listenScroll) {
          this.vslider.on("scroll", pos => {
            // 向父组件传值
            this.$emit("scroll", pos);
          });
        }
        //派发上拉加载
        if (this.pullup) {
          this.vslider.on('scrollEnd', () => {
            // 滚动到底部
            if (this.vslider.y <= (this.vslider.maxScrollY + 50)) {
              this.$emit('scrollToEnd')
            }
          })
        }
        //下拉刷新
        if (this.pullDownRefresh) {
          this.vslider.on("touchEnd", pos => {
            // 下拉动作
            // console.log(666,pos)
            // console.log(2,pos)
            if (pos.y > 50) {

              this.$emit("scrollToEnd");
            }
            if (pos.y <= (this.vslider.maxScrollY + 50)) {
              this.$emit("pulldown");
            }
          });
        }
        //滚动前派发事件
        if (this.beforeScroll) {
          this.scroll.on("beforeScrollStart", () => {
            this.$emit("beforeScroll");
          });
        }
      },
      addClass(ele, cls) {
        ele.className = ele.className === "" ? cls : ele.className + " " + cls;
      },
      getStyle(obj, attr) {
        if (obj.currentStyle) {
          return obj.currentStyle[attr];
        } else {
          return document.defaultView.getComputedStyle(obj, null)[attr];
        }
      }
    }
  };
</script>

<style scoped lang="scss">
  .VerticalScroll {
    position: absolute;
    top: 92px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
  }
  .VerticalScrolls {
    position: absolute;
    top: 192px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
  }

  .VerticalScrollss{
    position: absolute;
    top: 350px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
  }
</style>



