<template>
  <!--搜索课程-->
  <div class="SearchCourse">
    <!--头部导航-->
    <headerBack title="课程搜索"></headerBack>
    <div class="titleBar">
      <div class="searchInput">
        <div class="searchIcon"></div>
        <div class="titleInfo" >
          <input type="text"  placeholder="请输入关键字" v-model="value">
        </div>
      </div>
      <div class="searchTitle" @click="searchContent">
        搜索
      </div>
    </div>
    <!--<div class="infoContent">-->
      <!--<div class="searchInfo">-->
        <!--搜索历史：-->
        <!--<div class="rightTitle">-->
          <!--清空历史-->
        <!--</div>-->
      <!--</div>-->

      <!--<div style="margin-right:-39px">-->
        <!--<ul>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
          <!--<li>作文</li>-->
        <!--</ul>-->
      <!--</div>-->
    <!--</div>-->
    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <div class="MyCollCourse"  v-for="(item,key) in dataInfo" :key="key" @click="toPage(item.courseid,item.classtypes)">
          <div class="imgShow">
            <img v-lazy="item.imageurl" width="100%" height="100%" alt="">
          </div>
          <div class="infoContent">
            <div class="title text-ellipsis">
              {{item.coursename}}
            </div>
            <div class="subTitle text-ellipsis">
              {{item.subhead}}
            </div>
            <div class="teacher">
              <!--{{data.teachername}}-->
            </div>
            <div class="price">
              <template v-if="item.classtypes==1 ||(item.classtypes==2 && item.price==item.maxprice) ">
                ￥{{item.price}}/元
              </template>
              <template v-else>
                ￥{{item.price}}-{{item.maxprice}}/元
              </template>
            </div>
          </div>
        </div>
      </mt-loadmore>
    </div>
    <notInfo v-if="infoStatus">
      抱歉，没有指定分类数据
    </notInfo>
  </div>

</template>
<script>
  import headerBack from '@/components/HeaderBack';     //返回组件
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import notInfo from '@/components/NotInfo';     //返回组件
  import { Loadmore } from 'mint-ui';
  export default{
    name:'SearchCourse',
    components:{
      headerBack,
      notInfo,
      'mt-loadmore':Loadmore
    },
    data(){
      return{
        value:'',   //搜索数据
        dataInfo:[],   //数组集合
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉
      }
    },
    methods:{
      // 搜索课程
      searchContent(){
        this.infoStatus = false;
        this.dataInfo = [];
        this.searchCourseOrTeacher();
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.searchCourseOrTeacher();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this. searchCourseOrTeacher();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 前往课程详情页
      toPage(courseid,classtypes){
        this.$router.push({path:'/CourseDetails',query:{'courseid':courseid,'classtypes':classtypes}});
      },
      // 请求三级信息
      searchCourseOrTeacher(){
        Server.post(Path.searchCourseOrTeacher, {
          'search':this.value,
          'searchtype':1,
          'pagenum':this.pagenum
        }, (res) => {
          if (res.code === 0) {
            if(res.data.arr.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              //页码不为一，追加
              this.dataInfo = this.dataInfo.concat(res.data.arr);
            } else {
              //否则从第一页开始，覆盖列表数据
              this.dataInfo = res.data.arr;
            }
          }
        });
      }
    },
  }
</script>
<style scoped lang="scss">
  @import "~@/assets/common/mixin";
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 188px;
    left: 0;
    right: 0;
    bottom: 0;
  }

  .MyCollCourse {
    height: 200px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 20px 36px 18px 25px;
    border-bottom: 1px solid rgba(217, 217, 217, 1);
  }
  .MyOrderCourse{
    height: 200px;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 1);
    border-radius: 10px;
    padding: 20px 36px 18px 25px;
  }
  .imgShow{
    width:280px;
    height:160px;
    border-radius:8px;
    float:left;
    margin-right:21px;
  }
  .infoContent{
    height:160px;
    width:388px;
    float:left;
    /*background:red;*/
    .title{
      height:49px;
      font-size:30px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(77,77,77,1);
    }
    .subTitle{
      height:43px;
      font-size:26px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(77,77,77,1);
    }
    .teacher{
      height:46px;
      font-size:26px;
      font-family:Adobe Heiti Std R;
      font-weight:normal;
      color:rgba(107,107,107,1);
    }
    .price{
      font-size:26px;
      font-family:MicrosoftYaHei;
      font-weight:400;
      text-align: right;
      color:rgba(231,19,19,1);
    }
  }

  .SearchCourse{
    position:absolute;
    left:0;
    right:0;
    bottom:0;
    top:0;
    background:rgba(255,255,255,1);
    .titleBar{
      height:96px;
      background:rgba(255,255,255,1);
      box-sizing: border-box;
      padding:18px 38px 0px 30px;
      border-bottom:1px solid rgba(217,217,217,1);
      .searchInput{
        width:550px;
        height:64px;
        background:rgba(242,242,242,1);
        float:left;
        box-sizing: border-box;
        border-radius:31px;
        .searchIcon{
          height:30px;
          width:30px;
          margin-top:17px;
          margin-left:18px;
          float:left;
          @include bg-image("~@/assets/images/icon_search");
          margin-right:23px;
        }
        .titleInfo{
          height:64px;
          line-height: 64px;
          float:left;
          font-size:24px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(166,166,166,1);
        }

      }
      .searchTitle{
        height:64px;
        line-height: 64px;
        float:right;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(24,141,237,1);
      }
    }

    /*.infoContent{*/
      /*padding:50px 37px 0px 37px;*/
      /*box-sizing: border-box;*/
      /*.searchInfo{*/
        /*height:75px;*/
        /*font-size:24px;*/
        /*font-family:PingFang-SC-Regular;*/
        /*font-weight:400;*/
        /*color:rgba(166,166,166,1);*/
        /*.rightTitle{*/
          /*font-size:24px;*/
          /*font-family:PingFang-SC-Regular;*/
          /*font-weight:400;*/
          /*color:rgba(24,141,237,1);*/
          /*float:right;*/
        /*}*/
      /*}*/
      /*li{*/

        /*width:140px;*/
        /*height:72px;*/
        /*display:flex;*/
        /*justify-content: center;*/
        /*align-items: center;*/
        /*box-sizing: border-box;*/
        /*float:left;*/
        /*border:2px solid rgba(209,209,209,1);*/
        /*border-radius:36px;*/
        /*margin-right:39px;*/
        /*margin-bottom:39px;*/
        /*font-size:28px;*/
        /*font-family:PingFang-SC-Regular;*/
        /*font-weight:400;*/
        /*color:rgba(77,77,77,1);*/
      /*}*/
    /*}*/

  }
</style>



