export default {
  getItem(param) {
    return localStorage.getItem(param);
  },

  setItem(key, value) {
    window.localStorage.setItem(key, value);
  },
  parseQuery(query) {
    let sHref = query;
    let args = sHref.split('?');
    if (args[0] == sHref) {
      return "";
    }
    let arr = args[1].split('&');
    let obj = {};
    for (let i = 0; i < arr.length; i++) {
      let arg = arr[i].split('=');
      obj[arg[0]] = arg[1];
    }
    return obj;
  },
  jsonParse(jsonStr) {
    return JSON.parse(jsonStr);
  },

  isArray(obj) {
    return Object.prototype.toString.call(obj) == "[object Array]";
  },

  isJson(obj) {
    return (
      typeof obj == "object" && Object.prototype.toString.call(obj).toLowerCase() == "[object object]" && !obj.length
    );
  },

  isLogin() {
    if(!this.getItem('appstudentinfo') || !this.getItem('appstudentkey')) {
      return false;
    } else {
      return true;
    }
  },

  getNowTime(d2) {
    let date = new Date(),
      year = date.getFullYear(),
      month = date.getMonth() + 1,
      day = date.getDate(),
      hour = date.getHours(),
      minute = date.getMinutes();
    let dateStr = `${year}-${month}-${day} ${hour}:${minute}`;
    /**
     * 如果存在参数，返回当前时间是否大于目标时间，否则返回当前时间字符串
     */
    if (d2) {
      return (
        new Date(dateStr.replace(/-/g, "/")) >= new Date(d2.replace(/-/g, "/"))
      );
    } else {
      return dateStr;
    }
  },

  getUserInfo() {
    return this.jsonParse(this.getItem('appstudentinfo'));
  },

  /**
   * 检测本地是否存在当前票据
   * @param Obj  当前票据对象
   * @returns {boolean} true存在  false不存在
   */
  checkLocalHas(Obj) {
    let data = [], i = 0;
    if (this.getItem('iOSRechargeOrders')) {
      data = this.getItem('iOSRechargeOrders');
    }

    let len = data.length;
    let receiptData = Obj;
    if (data.indexOf(JSON.stringify(receiptData)) == -1) {
      return false;
    } else {
      return true;
    }
  },

  /**
   * 获取当前url中的参数值
   * @param name  想获取的参数值
   */
  GetQueryString (name) {
    let url = new RegExp('(^|&)' + name + '=([^&]*)(&|$)')
    let newUrl = window.location.search.substr(1).match(url)
    if (newUrl != null) {
      return unescape(newUrl[2])
    } else {
      return false
    }
  },

  /**
   * 删除本地票据
   * @param Obj  支付成功后的票据对象
   */
  removeRechargeDataItem(Obj) {
    let data = [];
    if (this.getItem('iOSRechargeOrders')) {
      data = JSON.parse(this.getItem('iOSRechargeOrders'));
    }

    if (!(data && data.length && data.length > 0) || data.findIndex(item => JSON.stringify(item) == JSON.stringify(Obj)) == -1) {
      return;
    }
    //删除满足条件的数据
    data.splice(data.findIndex(item => JSON.stringify(item) == JSON.stringify(Obj)), 1);
    //重新存储票据数据
    this.setItem('iOSRechargeOrders', JSON.stringify(data));
  },

  /**
   * 存储一条新的票据到本地
   * @param ordernum    订单号
   * @param sendString  新的票据字符串
   */
  setRechargeData(ordernum, sendString) {
    let data = [], newData = [];
    //查询本地是否存在未完成票据
    if (this.getItem('iOSRechargeOrders')) {
      data = JSON.parse(this.getItem('iOSRechargeOrders'));
    }

    let receiptData = {
      ordernum: ordernum,
      sendString: sendString,
    };
    if (this.checkLocalHas(receiptData)) {
      return;
    } else {
      if (data.length > 0) {
        data.forEach(el => {
          newData.unshift(el);
        })
        newData.push(receiptData);
      } else {
        newData.push(receiptData);
      }
      this.setItem('iOSRechargeOrders', JSON.stringify(newData));
    }
  },
}



