<template>
  <!--我的课程-->
  <div class="MyCourse">
    <headerBack title="我的课程"  typeStatus="tittle" rightTitle="调班" @clickMe="clickMe"></headerBack>
    <div >
      <mt-navbar :value="selected" v-model="selected" >
        <mt-tab-item id="2" >直播课</mt-tab-item>
        <mt-tab-item id="1">录播课</mt-tab-item>
      </mt-navbar>
    </div>

    <div class="wrapper" ref="scroll">
      <mt-loadmore :top-method="loadTop"
                   :bottom-method="loadBottom"
                   :bottom-all-loaded="allLoaded"
                   ref="loadmore"  >
        <mt-tab-container :swipeable="true" v-model="selected">
          <mt-tab-container-item id="2">
            <div  infinite-scroll-disabled="loading" infinite-scroll-distance="10" class="content">
              <!--未结束-->
              <myCourseNow :data="beginInfo"  @MyEndInfo="MyEndInfo"></myCourseNow>
            </div>
          </mt-tab-container-item>
          <mt-tab-container-item id="1">
            <div  infinite-scroll-disabled="loadingReceive" infinite-scroll-distance="10" class="content">
              <!--已结束-->
              <myCourseEnd :data="endInfo"  @MyEndInfo="MyEndInfo"></myCourseEnd>
            </div>
          </mt-tab-container-item>
        </mt-tab-container>
      </mt-loadmore>
    </div>

    <mt-popup
      v-model="popupVisible"
      position="bottom"  style="width:100%">
      <div class="bottomPopup">
        <div class="offIcon" @click="closeModal">
          <img src="~@/assets/images/icon_add.png" width="100%" alt="">
        </div>
        <!--下拉框-->
        <div class="selectTop">
          班级名称：
          <div class="selectOption">
            <select style="width:100%;height:40px;" v-model="oldIndex" @change="changeOld"  >
              <option v-if="!newStatus" value="0" disabled>请选择</option>
              <option   v-for="(item, index) in listInfo" :key="index" :value="index">{{item.gradename}}</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <!--中间-->
        <div class="subItem">
          <span style="margin-right:20px;">
            剩余课时：
          </span>
          <span v-if="periodnum">
            {{periodnum}}个课时
          </span>
        </div>
        <!--下拉框-->
        <div class="selectTop">
          目标班级：
          <div class="selectOption">
            <select style="width:100%"  v-model="newIndex" @change="changeNew"  >
              <option value="0" disabled >请选择</option>
              <option v-if="newStatus" :value="newIndex"  v-for="(info,key) in listInfo[oldIndex].classlist" :key="key">{{info.gradename}}</option>
              <option v-if="newStatus && listInfo[oldIndex].classlist.length==0" :value="newIndex"    disabled>抱歉，没有可选班级</option>
            </select>
            <div class="rightBottom">
              <img src="~@/assets/images/icon_right.png" width="100%" alt="">
            </div>
          </div>
        </div>
        <!--下拉框-->
        <div class="subItem" v-if="timeStatus">
          <span style="margin-right:20px;">
            老师时间：
          </span>
          <span >
            {{teachername}}：{{starttime}}-{{endtime}}
          </span>
        </div>

        <div class="buttonOut">
          <div style="width:50%;float:left;">
            <div class="leftButton" @click="okModal">确定</div>
          </div>
          <div style="width:50%;float:left;">
            <div class="rightButton"  @click="closeModal">取消</div>
          </div>

        </div>
      </div>
    </mt-popup>

    <middleToast v-if="toastStatus==true" >
      <div class="infoContent">
        <div class="topIcon">
          <img src="~@/assets/images/icon_success.png" height="100%" alt="">
        </div>
        <div class="titleInfo">
          申请提交成功
        </div>
        <div class="infoInfo">
          我们会在12小时内通过系统消息或短信的方式通知您，请注意查收。
        </div>
        <div class="okOut">
          <div class="okShow" @click="closeToast">
            确定
          </div>
        </div>
      </div>
    </middleToast>

    <middleToast v-if="toastType==true" >
      <div class="infoContent">
        <div class="topIcon">
          <img src="~@/assets/images/icon_paymentError.png" height="100%" alt="">
        </div>
        <div class="titleInfo">
          申请调班失败
        </div>
        <div class="infoError">
          原因:
          1.已学习三次课以上不可申请调班；
          2.每个课程有两次调班机会；
          3.若需其他帮助请联系客服：023-6235196
        </div>
        <div class="okOut">
          <div class="okShow" @click="toastType=false;">
            确定
          </div>
        </div>
      </div>
    </middleToast>

    <notInfo v-if="infoStatus">
      抱歉，没有课程
    </notInfo>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import myCourseNow from '../components/MyCourseNow';      // 未结束
  import myCourseEnd from '../components/MyCourseEnd';      // 未结束
  import middleToast from '@/components/MiddleToast';     //自定义模态弹框
  import notInfo from '@/components/NotInfo';           //没有数据
  import { Loadmore,Navbar,Popup,Toast, TabItem ,TabContainer, TabContainerItem} from 'mint-ui';
  import Server from '@/service/request';
  import Path from '@/service/Path';
  export default{
    name:'MyCourse',
    components:{
      headerBack,
      myCourseNow,
      myCourseEnd,
      middleToast,
      notInfo,
      'mt-navbar':Navbar,
      'mt-tab-item':TabItem,
      'mt-tab-container':TabContainer,
      'mt-tab-container-item':TabContainerItem,
      'mt-popup':Popup,
      'mt-loadmore':Loadmore
    },
    data(){
      return{
        studentid:'',         //学生ID
        toastStatus:false,  //模拟弹框
        toastType:false,   //调班失败默认不显示
        popupVisible:false, //默认底部弹框关闭
        beginInfo:[],       //待上课课表
        endInfo:[],        //已结束课程列表
        selected:'2',//默认请求 直播课   1 录播课  2直播课
        listInfo:[
          {
            classlist:[]
          }
        ],   //调课信息
        timeStatus:false,  //时间默认先不显示
        oldIndex:0,   //默认原先班级请选择显示
        newIndex:0,   //默认对象班级请选择显示
        periodnum:'',    //原班级的课时数量
        newStatus:false,  //默认目标班级不可点
        teachername:'',  //老师名称
        starttime:'',   //开始时间
        endtime:'',     //结束时间
        infoStatus:false,  //没有数据不显示
        total: 0,         //数据总条数
        limit: 10,        //规定每页条数
        pagenum:1,        //默认第一页
        allLoaded: false, //是否加载完成，控制组件是否能继续上拉

      }
    },
    created(){
      // 学生ID
      this.studentid = JSON.parse(this.$loca.getItem("appstudentinfo")).uid;
      if(window.localStorage.getItem('selectedCourse') && window.localStorage.getItem('selectedCourse')==1){
        this.selected = window.localStorage.getItem('selectedCourse');
        return;
      }
      // 请求我的课程
      this.getBuyCurriculum();
    },
    methods:{
      // 请求我的课程
      getBuyCurriculum(){
        Server.post(Path.getBuyCurriculum,{
          'studentid':this.studentid,
          'pagenum':this.pagenum,
          'limit':this.limit,
          'coursetype':this.selected
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.data.length==0 && this.pagenum == 1){
              this.infoStatus = true;
              return;
            }
            // 绑定数据
            this.total = res.data.pageinfo.total;
            this.limit = res.data.pageinfo.pagesize;
            if (this.pagenum != 1 ) {
              if(this.selected==='2'){
                this.beginInfo = this.beginInfo.concat(data.data);
              }else{
                this.endInfo = this.endInfo.concat(data.data);
              }
            } else {
              if(this.selected==='2'){
                this.beginInfo = data.data;
              }else{
                this.endInfo = data.data;
              }
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      /**
       * 下拉刷新
       */
      loadTop() {
        setTimeout(() => {
          this.$refs.loadmore.onTopLoaded();  //隐藏loading
          this.pagenum = 1;                      //重置页码到1
          this.allLoaded = false;             //是否加载完成：否
          this.getBuyCurriculum();                //请求列表数据
        }, 500);
      },
      /**
       * 上拉加载
       */
      loadBottom() {
        if(this.pagenum > Math.ceil(this.total / this.limit)) {  //判断是否需要分页，阻止上拉请求
          this.allLoaded = true;                              //阻止上拉方法
          this.$refs.loadmore.onBottomLoaded();               //隐藏loading
        } else {                                              //否则列表追加数据
          setTimeout(() => {
            this.pagenum++;                                      //页码加一
            this.getBuyCurriculum();
            this.$refs.loadmore.onBottomLoaded();             //隐藏loading
          }, 500);
        }
      },
      // 录播课查看详情
      MyEndInfo(schedulingid,courseid){
          this.$router.push({path:'/MyEndInfo',query:{'selected':this.selected,'schedulingid':schedulingid,'courseid':courseid}});
      },
      /**
       * 调班开始
       */
      // 打开模态框
      clickMe(){// 查询可调的班级和目标班级信息
        Server.post(Path.getAllClassList,{
        },(res)=>{
          let { code, data } = res;
          if(code===0){
            if(data.length==0){
              Toast('抱歉，没有可调班级');
            }else{
              // 记录数据
              this.listInfo = data;
              this.popupVisible = true;
            }
          }else{
            // this.$Message.error(res.info);
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
      // 选择调课的班级
      changeOld(){
        this.timeStatus = false;
        // 新班级可点
        this.newStatus = true;
        // 记录有几个课时
        this.periodnum = this.listInfo[this.oldIndex].periodnum;
      },
      // 选择新班级
      changeNew(){
        // 显示老师和时间
        this.timeStatus = true;
        this.teachername = this.listInfo[this.oldIndex].classlist[this.newIndex].teachername;
        this.starttime = this.listInfo[this.oldIndex].classlist[this.newIndex].starttime;
        this.endtime = this.listInfo[this.oldIndex].classlist[this.newIndex].endtime;
      },
      // 关闭模态框
      closeModal(){
        // 将模态框需要的属性重置
        this.timeStatus = false;
        this.newStatus = false;
        this.popupVisible = false;
      },
      // 确认提交
      okModal(){
        if(!this.timeStatus &&  !this.newStatus){
          Toast('请选择调班班级');
        }else if(!this.timeStatus){
          Toast('请选择目标班级');
        }else{
          Server.post(Path.submitApplyClasss,{
            'curriculumid':this.listInfo[this.oldIndex].curriculumid,
            'oldschedulingid':this.listInfo[this.oldIndex].schedulingid,
            'newschedulingid':this.listInfo[this.oldIndex].classlist[this.newIndex].schedulingid
          },res => {
            let {code, data} = res;
            if (code === 0) {
              // 关闭模态框
              this.popupVisible = false;
              // 开启弹框
              this.toastStatus = true;
            }else{
              this.popupVisible = false;
              this.toastType = true;
            }
          })

        }
      },
      // 关闭toast
      closeToast(){
        this.toastStatus = false;
      }
    },
    watch:{
      selected:function(value){
        // 默认不显示无数据
        if(this.infoStatus){
          this.infoStatus = false;
        }
        window.localStorage.setItem('selectedCourse', this.selected);
        this.pagenum = 1;
        // 再次请求列表数据
        this.getBuyCurriculum();
      }
    }
  }
</script>
<style lang="scss" scoped>
  .wrapper {
    overflow: scroll;
    position: absolute;
    top: 190px;
    left: 0;
    right: 0;
    bottom: 0;
  }
  .MyCourse{
    /*添加后信息显示*/
    .infoContent{
      position:relative;
      width:580px;
      background:rgba(255,255,255,1);
      border-radius:10px;
      padding:20px 55px 20px 55px;
      z-index:6;
      .topIcon{
        height:64px;
        text-align:center;
        margin-bottom:20px;
      }
      .titleInfo{
        height:48px;
        text-align:center;
        font-size:30px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(51,51,51,1);
      }
      .infoInfo{
        font-size:26px;
        font-family:Adobe Heiti Std R;
        font-weight:normal;
        color:rgba(51,51,51,1);
        line-height:40px;
        margin-bottom:47px;
      }
      .okOut{
        height:52px;
        display:flex;
        justify-content: center;
        .okShow{
          width:122px;
          height:52px;
          line-height: 52px;
          text-align: center;
          border:1px solid rgba(63,156,232,1);
          border-radius:20px;
          font-size:30px;
          font-family:MicrosoftYaHei;
          font-weight:400;
          color:rgba(63,156,232,1);
        }
      }
    }
    .bottomPopup{
      height:787px;
      box-sizing: border-box;
      padding:95px 40px 77px 39px;
      background:rgba(255,255,255,1);
      border-radius:12px 12px 0px 0px;
      position:relative;
      /*关闭按钮*/
      .offIcon{
        position:absolute;
        top:21px;
        right:25px;
        width:19px;
        height:19px;
        transform: rotate(45deg);
      }
      /*下拉框*/
      .selectTop{
        height:88px;
        line-height: 88px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        margin-bottom:62px;
        .selectOption{
          box-sizing: border-box;
          padding-left:20px;
          float:right;
          width:500px;
          position:relative;
          height:88px;
          background:rgba(250,250,252,1);
          border:1px solid rgba(217,217,217,1);
          border-radius:4px;
          font-size:28px;
          font-family:PingFang-SC-Regular;
          font-weight:400;
          color:rgba(77,77,77,1);
          .rightBottom{
            position:absolute;
            transform: rotate(90deg);
            height:20px;
            width:20px;
            top:32px;
            right: 22px;
          }
        }
      }
      .buttonOut{
        height:86px;
        margin-top:79px;
        .leftButton{
          height:86px;
          line-height: 86px;
          text-align: center;
          width:254px;
          background:rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(255,255,255,1);
          margin:0px auto;
        }
        .rightButton{
          width:254px;
          height:86px;
          line-height: 86px;
          text-align: center;
          border:2px solid rgba(57,173,239,1);
          border-radius:4px;
          font-size:32px;
          font-family:PingFang-SC-Medium;
          font-weight:500;
          color:rgba(57,173,239,1);
          margin:0px auto;
        }
      }
      .subItem{
        height:30px;
        font-size:32px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:33px;
        margin-bottom:63px;
      }
    }
  }
</style>



