<template>
  <!--登陆-->
  <div class="Login">
    <!--头部导航-->
    <headerBack title="注册" typeStatus="tittle" rightTitle="登录"  @clickMe="login"></headerBack>
    <!--顶部Logo-->
    <loginIcon></loginIcon>
    <div class="inputCard">
      <!--手机号码-->
      <div class="phone">
        <div class="phoneIcon">
        </div>
        <div class="input">
          <input type="text" class="inputFull"  data-vv-as="手机号"  v-model.trim="mobile" v-validate="'required|phone'"  name="mobile"  placeholder="手机号码" >
        </div>
        <span class="code"  @click="toNumber">+{{prphone}}&nbsp;<i>></i></span>
      </div>
      <div class="errorShow">
        <span v-show="errors.has('mobile')"  >{{ errors.first('mobile') }}</span>
      </div>
      <!--图形验证码-->
      <div class="phone">
        <div class="codeIcon">
        </div>
        <div class="middleInput">
          <input type="text"  class="inputMiddle" v-model.trim="codeNumber"  v-validate="'required'"    name="code"  placeholder="图形验证码">
        </div>
        <div class="rightImg">
          <div class="leftLine">
          </div>
          <div class="imgShow">
            <div class="imgOut"  @click="randomCode">
              <img v-lazy="codeImg"  width="100%" height="100%" alt="">
            </div>
          </div>
        </div>
      </div>
      <div class="errorShow">
        <span v-show="errors.has('code')" >{{ errors.first('code') }}</span>
      </div>
      <!--短信验证码-->
      <div class="phone">
        <div class="cellIcon">
        </div>
        <div class="middleInput">
          <input type="text"  v-model="shortNumber" v-validate="'required'"  name="phoneCode"  class="inputMiddle" placeholder="短信验证码" >
        </div>
        <div class="rightImg">

          <div class="imgShow" @click="getCode">
            {{codeObj.codeText}}
          </div>
          <div class="leftLine">
          </div>
        </div>
      </div>
      <div class="errorShow">
        <span v-show="errors.has('phoneCode')" class="help is-danger" >{{ errors.first('phoneCode') }}</span>
      </div>
      <!--登陆密码-->
      <div class="phone">
        <div class="passIcon">
        </div>
        <div class="input">
          <input class="inputFull"  data-vv-as="密码"  v-validate="'required|min:6'" maxlength="16" name="pass" v-model="pwd" type="text" placeholder="设置登陆密码" >
        </div>
      </div>
      <div class="errorShow">
        <span v-show="errors.has('pass')" class="help is-danger" >{{ errors.first('pass') }}</span>
      </div>
      <!--登陆按钮-->
      <div style="padding-top:13px;" @click="register">
        <div class="loginButton">
          注册
        </div>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="right" style="width:100%;height:100%;">
      <number :popupVisible="popupVisible"  @closeModal="closeModal"></number>
    </mt-popup>
  </div>
</template>
<script>
  import headerBack from '@/components/HeaderBack';     //头部返回导航
  import loginIcon from '@/components/LoginIcon';     //头部返回导航
  import number from '@/components/Number';     //头部返回导航
  import Server from '@/service/request';
  import Path from '@/service/Path';
  import {Popup,Toast} from 'mint-ui';
  export default{
    name:'Login',
    components:{
      headerBack,
      loginIcon,
      number,
      'mt-popup':Popup
    },
    data(){
      return{
        publickey:'',     //公钥
        popupVisible:false,    //选择区号
        mobile:'',      //手机号
        codeNumber:'',  //图形验证码
        shortNumber:'',   //短信验证码
        prphone:86,      //默认区号
        pwd:'',         //密码
        codeImg:'',     //图形验证码图片
        sessionid:'',   //图形sessionID
        codeObj:{
          codeText:'获取验证码' , //发送验证码
          timer:null,
          count:''
        },
      }
    },
    created(){
      // 获取图形验证码
      this.randomCode();
      // 获取公钥
      this.getKey();
    },
    methods:{
      // 获取短信验证码
      getCode(){
        if(this.mobile.length!=11){
          //请输入正确手机号
          Toast({
            message:'请输入正确手机号',
            iconClass: 'icon icon-error',
            duration: '1800'
          });
          return false;
        }
        // 定义读秒
        const count = 60;
        if(!this.codeObj.timer){
          Server.post(Path.sendMobileMsg,{
            'mobile':this.mobile,
            'prphone':this.prphone,
            'code':this.codeNumber,
            'sessionid':this.sessionid,
            'type':2
          },(res)=>{
            console.log(res);
            let { code, data } = res;
            if(code===0){
            }else{
              Toast({
                message:res.info,
                iconClass: 'icon icon-error',
                duration: '1800'
              });
              if(this.codeObj.timer){
                clearInterval(this.codeObj.timer);
                this.codeObj.timer = null;
                this.codeObj.codeText = '获取验证码';
              }
            }
          });
          this.codeObj.count = count;
          this.codeObj.codeText = this.codeObj.count+'s';
          this.codeObj.timer = setInterval(() => {
            if (this.codeObj.count > 0 && this.codeObj.count <= count) {
              this.codeObj.count--;
              this.codeObj.codeText = this.codeObj.count+'s';
            } else {
              clearInterval(this.codeObj.timer);
              this.codeObj.timer = null;
              // 重发验证码
              this.codeObj.codeText = '重新发送';
            }
          }, 1000)
        }
      },
      // 关闭选择区号
      closeModal(info){
        this.popupVisible = false;
        // 给区号赋值
        this.prphone = info.code;
      },
      // 获取图形验证码
      randomCode(){
        this.codeImg = '';
        Server.post(Path.randomCode, {
        }, (res) => {
          if (res.code === 0) {
            // 记录图片
            this.codeImg = res.data.codeimg;
            // 记录验证码id
            this.sessionid = res.data.sessionid;
          }
        });

      },

      // 选择区号
      toNumber(){
        this.popupVisible = true;
      },
      login(){
        this.$router.push('Login');
      },
      // 注册
      register(){
        this.$validator.validate().then(result => {
          if (!result) {
            return;
          }else{
            var key = this.randomString(10);
            let jsons = {
              'mobile':this.mobile,     //手机号
              'code':this.shortNumber,      //短信验证码
              'password':this.pwd ,     //密码
              'prphone':this.prphone,   //区间号
              'key':key,
            };
            var postData = this.wrap_encrypt(JSON.stringify(jsons));
            Server.post(Path.register,{
              data:postData
            },(res)=>{
              console.log(res);
              let { code, data } = res;
              if(code===0){
                // this.$Message.success(res.info);
                window.localStorage.setItem('appstudentinfo', JSON.stringify(data));
                window.localStorage.setItem('appstudentkey', key);
                this.$router.push({path:'/DataEditing',query:{from:'register'}});
              }else{
                // this.$Message.error(res.info);
                Toast({
                  message: res.info,
                  iconClass: 'icon icon-error',
                  duration: '1800'
                });
              }
            })
          }
        });

      },
      // 生成随机字符串
      randomString(len, charSet) {
        charSet = charSet || "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
        var randomString = "";
        for (var i = 0; i < len; i++) {
          var randomPoz = Math.floor(Math.random() * charSet.length);
          randomString += charSet.substring(randomPoz, randomPoz + 1);
        }
        return randomString;
      },
      //公钥加密
      wrap_encrypt(content) {
        var encryptobj = new JSEncrypt();
        encryptobj.setPublicKey(this.publickey);
        var result = "";
        var step = 117;
        var strlen = content.length;
        var count = Math.ceil(strlen / step);
        for (var i = 0; i < count; i++) {
          let chunk = content.substring(step * i, step * (1 + i));
          result += encryptobj.encrypt(chunk);
        }
        return result;
      },
      //获取公钥
      getKey(){
        Server.post(Path.getPublicKey,{
        },res=>{
          let {code,data}=res;
          if(code===0){
            this.publickey = res.key;
          }else{
            Toast({
              message: res.info,
              iconClass: 'icon icon-error',
              duration: '1800'
            });
          }
        })
      },
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";

  .Login{
    position:absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:rgba(255,255,255,1);
    .inputCard{
      padding:0px 65px 0px 66px;
      .errorShow{
        height:50px;
        line-height:50px;
        padding-left:65px;
        color:red;
      }
      .phone{
        height:58px;
        box-sizing: border-box;
        padding-left:10px;
        position:relative;
        border-bottom:1px solid rgba(217,217,217,1);
        .code{
          position: absolute;
          right: 7%;
          top: 50%;
          transform: translateY(-50%);
          color:#C4CCC9;
          font-size: 30px;
          i{
            font-family: '宋体';
            font-weight: bold;
          }
        }
        .phoneIcon{
          height:32px;
          width:32px;
          margin-top:13px;
          float:left;
          line-height:58px;
          margin-right:28px;
          @include bg-image("../assets/icon_phone");
        }
        .passIcon{
          height:32px;
          width:32px;
          margin-top:13px;
          float:left;
          line-height:58px;
          margin-right:28px;
          @include bg-image("../assets/icon_pass");
        }
        .codeIcon{
          height:32px;
          width:32px;
          margin-top:13px;
          float:left;
          line-height:58px;
          margin-right:28px;
          @include bg-image("../assets/icon_code");
        }
        .cellIcon{
          height:32px;
          width:32px;
          margin-top:13px;
          float:left;
          line-height:58px;
          margin-right:28px;
          @include bg-image("../assets/icon_cellphone");
        }
        /*fullItem*/
        .input{
          height:56px;
          width:540px;
          float:left;
          box-sizing: border-box;
          line-height: 56px;
          .inputFull{
            width:340px;
            height:56px;
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            /*color:rgba(179,179,179,1);*/
          }
        }
        /*middleItem*/
        .middleInput{
          height:56px;
          width:340px;
          float:left;
          box-sizing: border-box;
          line-height: 56px;
          .inputMiddle{
            width:340px;
            height:56px;
            font-size:32px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            /*color:rgba(179,179,179,1);*/
          }
        }
        /*右侧图形*/
        .rightImg{
          width:190px;
          height:58px;
          box-sizing: border-box;
          float:right;
          .leftLine{
            width:0px;
            height:42px;
            margin-top:8px;
            float:right;
            margin-right:17px;
            background:rgba(245,245,245,1);
            border-left:1px solid rgba(217,217,217,1);
          }
          .imgShow{
            height:58px;
            box-sizing: border-box;
            line-height: 58px;
            float:right;
            display:flex;
            align-items: center;
            font-size:30px;
            font-family:PingFang-SC-Regular;
            font-weight:400;
            color:rgba(16,165,228,1);
            /*border:1px solid red;*/
            .imgOut{
              width:140px;
              height:40px;
              line-height:40px;
            }
          }
        }
      }
      /*登陆按钮*/
      .loginButton{
        width:581px;
        height:92px;
        line-height:92px;
        text-align: center;
        margin:0px auto;
        background:rgba(16,165,228,1);
        opacity:0.6;
        border-radius:46px;
        font-size:32px;
        font-family:PingFang-SC-Medium;
        font-weight:500;
        color:rgba(255,255,255,1);
      }
      /*注册详情*/
      .register{
        text-align: center;
        font-size:30px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(16,165,228,1);
      }
    }
  }
</style>



