<template>
  <div>
    <div class="MyNewsItem" >
      <div class="imgOut">
        <div class="imgShow"></div>
      </div>
      <div class="infoContent">
        <div class="title">
          {{data.title}}
        </div>
        <div class="infoInfo">
          {{data.content}}
        </div>
        <div class="dateTime">
          {{data.addtime}}
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name:"MyNewsItem",
    props:{
      data:{
        type:Object,
        default(){
          return {};
        }
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .MyNewsItem{
    padding:11px  23px 10px 20px;
    border-bottom:1px solid rgba(217,217,217,1);
    overflow: hidden;
    position:relative;
    background:rgba(255,255,255,1);
    .imgShow{
      height:80px;
      width:80px;
      @include bg-image("../../assets/icon_news1");
      position:absolute;
      top:calc(50% - 40px);
      bottom:0;
      right:0;
      left:20px;
    }
    .infoContent{
      width: calc(100% - 130px);
      margin-left:118px;
      float:left;
      .title{
        height:39px;
        font-size:20px;
        font-family:MicrosoftYaHei-Bold;
        font-weight:bold;
        color:rgba(51,51,51,1);
      }
      .infoInfo{
        width: calc(100% - 100px);
        font-size:18px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(51,51,51,1);
        line-height:40px;
        margin-bottom:20px;
      }
      .dateTime{
        text-align: right;
        font-size:14px;
        font-family:MicrosoftYaHei;
        font-weight:400;
        color:rgba(77,77,77,1);
      }
    }
  }
</style>



