<template>
  <div id="Country">
    <div id="headerBack">
      <div class="Headbar-back" @click="closeModal">
      </div>
      <div class="Headbar-title">地区选择</div>
      <!--<span class="confirmArea" @click="handleArea()" v-if="confirmFlag">确定</span>-->
    </div>
    <div class="city-content">
      <verticalScroll :data="popupVisible">
        <ul class="subject-list-1">
          <li
          class="list-item border-1px"
          v-for="(el, i) in cityObj.cityList"
          :key="i"
          @click="childArea(el.id, el.is_child,i)"
          :class="{'confirmed':checkedItem===i}">
            {{el.name}}
          </li>
        </ul>
      </verticalScroll>
    </div>
  </div>
</template>

<script>
import verticalScroll from '@/components/VerticalScroll';   //better-scroll
import Server from "@/service/request";
import Path from "@/service/Path";

export default {
  name: "Country",
  components: {
    verticalScroll
  },
  props:{
    popupVisible:{
      type:Boolean,
      default(){
        return false;
      }
    }
  },
  data() {
    return {
      cityObj: { cityList: [] },
      confirmFlag: false,
      checkedItem: "",
      country: ""
    };
  },
  created() {
    this.organid = JSON.parse(this.$loca.getItem("appstudentinfo")).organid;
    this.getCityList();
  },
  methods: {
    // 关闭国家选择
    closeModal(){
      this.$emit('closeModal');
    },
    getCityList() {
      let parentid = this.$route.query.parentid || 0;
      Server.post(
        Path.getCityList,
        {
          parentid: parentid
        },
        res => {
          if (res.code === 0) {
            this.cityObj.cityList = res.data;
          }
        }
      );
    },
    childArea(parentid, is_child, i) {
      if (is_child != 0) {
        this.$emit('selectCountry',parentid);
      } else {
        this.confirmFlag = true;
        this.checkedItem = i;
        this.country = parentid;
      }
    }
  }
};
</script>

<style scoped lang="scss">
@import "~@/assets/common/variable";
@import "~@/assets/common/mixin";
#headerBack {
  z-index: 3;
  position: relative;
  height: 92px;
  line-height: 92px;
  font-size: $font-size-36;
  text-align: center;
  background-color: $color-white;
  color: $color-black;
  @extend .boxShadow;
  .Headbar-back {
    position: absolute;
    left: 17px;
    top: 20px;
    display: inline-block;
    width: 48px;
    height: 48px;
    @include bg-image('../assets/nav_icon_back');
    background-repeat: no-repeat;
    background-size: cover;
  }
}
#Country {
  @extend .fullPosition;
  .confirmArea {
    font-size: 30px;
    color: #878787;
    position: absolute;
    right: 31px;
  }
  .city-content {
    position: absolute;
    top: 0px;
    left: 0;
    right: 0;
    bottom: 0;
    .subject-list-1 {
      padding: 0 30px 0 30px;
      background-color: $color-white;
      .list-item {
        position: relative;
        height: 90px;
        line-height: 90px;
        font-size: $font-size-30;
        @include border-1px($border-gary);
        &:before {
          position: absolute;
          top: 32px;
          right: 0;
          content: "";
          display: block;
          width: 28px;
          height: 30px;
          @include bg-image("../assets/icon_more2");
          background-repeat: no-repeat;
          background-size: contain;
          background-position: center center;
          overflow: hidden;
        }
      }
      .confirmed {
        color: #5ad2cd !important;
      }
    }
  }
}
</style>



