<template>
  <div class="InfoCard">
    <div class="imgOut">
      <div class="imgShow">
        <img v-lazy="data.imageurl || defaultTeacher" width="100%" height="100%" alt="">
      </div>
      <div class="collection" @click="teacherCollect" v-if="!data.is_collect">
        收藏
      </div>
      <div class="collection"  v-if="data.is_collect">
        已收藏
      </div>
    </div>
    <div class="teacherName">
      <div class="name">
        {{data.teachername}}
      </div>
      <div    :class="{'sexMan':data.sex==1,'sexWoman':data.sex==2}">
      </div>
      <!--<div class="fontShow" v-if="!data.sex">-->
        <!--保密-->
      <!--</div>-->
    </div>
    <div class="teacherInfo">
      {{data.profile}}
    </div>
  </div>
</template>
<script>
  import defaultTeacher from '@/assets/images/defaultTeacher.png';      //图片加载页面失败显示
  export default{
    name:'TeacherInfo',
    data(){
      return{
        defaultTeacher
      }
    },
    props:{
      data:{
        type:Object,
        default(){
          return {};
        }
      }
    },
    methods:{
      teacherCollect(){
        this.$emit('teacherCollect');
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "~@/assets/common/mixin";
  .InfoCard{
    box-sizing: border-box;
    height:445px;
    @include bg-image("~@/assets/images/bg_teacherInfo");
    padding:60px 40px 0px 40px;
    .imgOut{
      height:140px;
      .imgShow{
        width:140px;
        height:140px;
        border-radius:50%;
        border:5px solid white;
        overflow: hidden;
        float:left;
      }
      .collection{
        float:right;
        width:110px;
        height:46px;
        line-height:46px;
        text-align: center;
        background:rgba(57,173,239,1);
        border-radius:23px;
        font-size:26px;
        font-family:PingFang-SC-Regular;
        font-weight:400;
        color:rgba(255,255,255,1);
      }
    }
    .teacherName{
      clear:both;
      height:100px;
      line-height:100px;
      .fontShow{
        height:100px;
        line-height:100px;
        float:left;
        font-size:36px;
        font-family:PingFang-SC-Regular;
        font-weight:normal;
        color:rgba(255,255,255,1);
      }
      .name{
        height:100px;
        line-height:100px;
        float:left;
        font-size:36px;
        font-family:PingFang-SC-Regular;
        font-weight:normal;
        color:rgba(255,255,255,1);
        margin-right:24px;
      }
      .sexMan{
        height:30px;
        width:30px;
        margin-top:35px;
        float:left;
        @include bg-image("~@/containers/Main/assets/icon_man");
      }
      .sexWoman{
        height:30px;
        width:30px;
        margin-top:35px;
        float:left;
        @include bg-image("~@/containers/Main/assets/icon_woman");
      }

    }
    .teacherInfo{
      height:96px;
      overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 3;
      -webkit-box-orient: vertical;
      font-size:24px;
      font-family:PingFang-SC-Regular;
      font-weight:normal;
      color:rgba(161,167,187,1);
      line-height:32px;
    }

  }
</style>



